export default function getDiscountAmountPerUnit({
  special_price,
  special_price_to,
  special_price_from,
  full_price,
  discount_percent,
}) {
  const now = new Date();
  const specialToDateUnix = +new Date(special_price_to);
  const specialFromDateUnix = +new Date(special_price_from);

  const specialPrice =
    !!special_price &&
    specialFromDateUnix < now &&
    now < specialToDateUnix &&
    +special_price;

  const discountFloat = (+discount_percent || 0) / 100;

  const discountAmountPerUnit = specialPrice
    ? specialPrice * discountFloat
    : +full_price * discountFloat;

  return discountAmountPerUnit;
}
