import axios from 'axios';
import cartItemsByStore from '@/mixins/cartItemsByStore';

export default {
  name: 'clearStoreFromCart',
  mixins: [cartItemsByStore],
  methods: {
    async clearStoreFromCart(storeID) {
      const { items, shipment } = this.cartItemsByStore
        .find(({ id }) => id === storeID);
      await axios.put(
        '/api/rest/cart',
        {
          data: [
            shipment || {},
            ...items,
          ].map(
            ({ item_id }) => ({
              quote_item_id: item_id,
              setqty: 0,
            }),
          ),
        },
      );
      this.$store.dispatch('loadCart');
    },
  },
}