import algoliasearch from 'algoliasearch';

const searchClient = algoliasearch('5D97PB00YB', '6e6550362dda457bf773b121e6e6acf0');
const searchIndex = searchClient.initIndex('products');

export async function getPickupLocations() {
  const search = await searchIndex.search('Nettó', {
    hitsPerPage: 20,
    page: 0,
    facetFilters: [
      [
        'attribute_set_name:Store'
      ],
    ],
  });

  return search.hits.map(
    ({
      name,
      short_description: description,
      objectID,
      _geoloc: {
          lat: latitude,
          lng: longitude,
      },
    }) => ({
      name,
      description,
      latitude,
      longitude,
      id: +objectID,
      icon: 'https://images.aha.is/image/upload/v1569504556/icons/netto-logo.png'
    }),
  )
}