<template>
  <BaseModal
    class="change-draft-delivery-address-modal"
    v-if="isModalVisible"
    isVisible
    centeredTitle
    noFooter
    noHeaderTitle
    @hide="handleCloseAddressModal"
    :isUncloseable="isUncloseable"
  >
    <template #header>{{ $t("pickYourLocation") }}</template>
    <DeliveryAddressPicker
      :confirmAddress="confirmAddress"
      :resizeKey="isModalVisible"
    />
  </BaseModal>
</template>

<i18n>
{
  "is": {
    "addressNotSet": "Veldu heimilisfang sem á að senda á eða staðsetningu sem þú vilt sækja frá",
    "addressNotUpdated": "Heimilisfangi ekki breytt: {currAddress}"
  },
  "en": {
    "addressNotSet": "Please select address or pickup location to start shopping",
    "addressNotUpdated": "Address not updated current address is: {currAddress}"
  }
}
</i18n>
<script>
import BaseModal from "@/components/BaseModal";
import DeliveryAddressPicker from "@/components/DeliveryAddressPicker";
import { mapActions, mapGetters } from "vuex";
import { MODAL_KEY_CHANGE_DRAFT_DELIVERY_ADDRESS } from "@/store/modalModule";
import {
  LOADING_KEY_ADDRESS_SET_SUCCESS,
  LOADING_KEY_ADDRESS_SET_WARNING,
} from "@/store/loadingModule";

export default {
  name: "ChangeDraftDeliveryAddressModal",
  props: {
    hideDraftAddressModal: Function,
    confirmDeliveryData: Function,
    isUncloseable: undefined,
  },
  components: {
    BaseModal,
    DeliveryAddressPicker,
  },
  computed: {
    ...mapGetters([
      "getModalVisibilityState",
      "deliveryDraftData",
      "isMissingAddressData",
      "currDeliveryText",
      "getAddressTextUserAddress",
    ]),
    isModalVisible() {
      return this.getModalVisibilityState(
        MODAL_KEY_CHANGE_DRAFT_DELIVERY_ADDRESS
      );
    },
  },
  methods: {
    ...mapActions([
      "toggleLoading",
      "toggleModal",
      "setDeliveryDraftData",
      "notification",
    ]),
    confirmAddress(address) {
      const {
        // notification,
        setDeliveryDraftData,
        confirmDeliveryData,
        deliveryDraftData,
        toggleLoading,
        // getAddressTextUserAddress,
      } = this;
      setDeliveryDraftData({
        ...deliveryDraftData,
        userAddress: address,
      });
      confirmDeliveryData();
      // notification({
      //   type: "success",
      //   message: this.$t("addressChanged", {
      //     currAddress: getAddressTextUserAddress(address),
      //   }),
      // });
      toggleLoading({
        loadingKey: LOADING_KEY_ADDRESS_SET_SUCCESS,
        isLoading: true,
      });
      setTimeout(
        () =>
          toggleLoading({
            loadingKey: LOADING_KEY_ADDRESS_SET_SUCCESS,
            isLoading: false,
          }),
        1000
      );
    },
    handleCloseAddressModal() {
      const {
        hideDraftAddressModal,
        notification,
        isMissingAddressData,
        currDeliveryText,
        toggleLoading,
      } = this;

      hideDraftAddressModal();
      toggleLoading({
        loadingKey: LOADING_KEY_ADDRESS_SET_WARNING,
        isLoading: true,
      });
      setTimeout(
        () =>
          toggleLoading({
            loadingKey: LOADING_KEY_ADDRESS_SET_WARNING,
            isLoading: false,
          }),
        2000
      );

      if (isMissingAddressData) {
        return notification({
          type: "warning",
          message: this.$t("addressNotSet"),
        });
      }

      return notification({
        type: "warning",
        message: this.$t("addressNotUpdated", {
          currAddress: currDeliveryText,
        }),
      });
    },
  },
};
</script>

