  >>> .modal-body {
    padding: 0;
  }
  >>> .modal__close-btn-wrapper {
    top: 15px;
    z-index: 6;
  }

  @media screen and (max-width: 767px) {
    >>> .modal__close-btn-wrapper {
      top: 0;
      right: 0;
    }

    >>> .modal-body,
    >>> .delivery-modal-address,
    >>> .loading-wrapper,
    >>> .delivery-modal-address__location-map-container,
    >>> #modal-location-map {
      display: flex;
      flex-direction: column;
      flex-grow: 1;
    }
  }
  @media screen and (min-width: 768px) {
    >>> .modal-header {
      padding: 0;
    }
  }
}
</style>
