import i18n from "@/i18n";
import { getStoreShipmentsById } from "@/services/ws";
import {
  MODAL_KEY_SELECT_DELIVERY_OPTION,
  MODAL_KEY_CHANGE_DRAFT_PICKUP_POINT,
} from "@/store/modalModule";
import { ATTRIBUTE_SET, FULFILLMENT_METHOD } from "@/util/constants";
import get from "@/util/get";
import getShipmentTextByLanguage from "@/util/getShipmentTextByLanguage";
import Vue from "vue";
import getMethodTextByLanguage from "@/util/getMethodTextByLanguage";
import filterAvailableShipments from "@/util/filterAvailableShipments";
import redirectToSellerList from "@/util/redirectToSellerList";
import { SELLER_TYPES } from "@/util/constants";
import { convertDateToStr } from "@/util/openingHours";
import getStartOfDay from "@/util/getStartOfDay";

export const deliveryModule = {
  state: {
    userAddress: undefined,
    selectedNettoAddress: undefined,
    deliveryDraftData: undefined,
    storeShipments: {},
    selectedDeliveryOptionByStore: {},
    fulfillmentMethod: FULFILLMENT_METHOD.DELIVERY,
  },
  mutations: {
    setFulfillmentMethod(state, fulfillmentMethod) {
      state.fulfillmentMethod = fulfillmentMethod;
    },
    setUserAddress(state, newAddress) {
      Vue.set(state, "userAddress", newAddress);
    },
    setSelectedNettoAddress(state, selectedNettoAddress) {
      state.selectedNettoAddress = {
        ...(selectedNettoAddress || {}),
      };
    },
    setDeliveryDraftData(state, deliveryDraftData) {
      state.deliveryDraftData = {
        ...state.deliveryDraftData,
        ...deliveryDraftData,
      };
    },
    setStoreShipmentsById(state, { storeId, shipments }) {
      Vue.set(state.storeShipments, storeId, shipments);
    },
    setSelectedShipmentOptionByStore(
      state,
      { storeId, selectedOption, clearAllStoreDeliveryOption }
    ) {
      Vue.set(state.selectedDeliveryOptionByStore, storeId, {
        ...(clearAllStoreDeliveryOption
          ? undefined
          : get(state, `selectedDeliveryOptionByStore.${storeId}`, {})),
        selectedShipment: selectedOption,
      });
    },
    setSelectedPreOrderHourByStore(state, { storeId, selectedOption }) {
      state.selectedDeliveryOptionByStore = {
        ...state.selectedDeliveryOptionByStore,
        [storeId]: {
          ...state.selectedDeliveryOptionByStore[storeId],
          selectedPreOrderHour: selectedOption,
        },
      };
    },
    setSelectedPreOrderDayByStore(state, { storeId, selectedOption }) {
      state.selectedDeliveryOptionByStore = {
        ...state.selectedDeliveryOptionByStore,
        [storeId]: {
          ...state.selectedDeliveryOptionByStore[storeId],
          selectedPreOrderDay: +selectedOption,
          selectedPreOrderHour: undefined,
        },
      };
    },
  },
  actions: {
    setUserAddress({ commit }, newAddress) {
      commit("setUserAddress", newAddress);
    },
    setFulfillmentMethod({ commit }, fulfillmentMethod) {
      commit("setFulfillmentMethod", fulfillmentMethod);
    },
    setSelectedNettoAddress({ commit }, selectedNettoAddress) {
      commit("setSelectedNettoAddress", selectedNettoAddress);
    },
    setDeliveryDraftData({ commit }, deliveryDraftData) {
      commit("setDeliveryDraftData", deliveryDraftData);
    },
    checkMissingDeliveryData({ dispatch, getters }, { sellerType }) {
      const { isMissingAddressData, currentNettoStoreIdByMethod } = getters;

      if (sellerType === SELLER_TYPES.GROCERY && currentNettoStoreIdByMethod) {
        // if user select a store from pickup list, we don't need asking address for now
        return;
      }
      if (isMissingAddressData) {
        dispatch("toggleModal", {
          modalKey: MODAL_KEY_SELECT_DELIVERY_OPTION,
          isVisible: true,
          sellerType,
        });
      }

      return isMissingAddressData;
    },
    async fetchStoreShipmentsById({ commit, getters }, storeId) {
      let shipments = getters.getStoreShipmentsById(storeId);
      if (!shipments) {
        shipments = await getStoreShipmentsById(storeId);
      }
      commit("setStoreShipmentsById", { storeId, shipments });
      return shipments;
    },
    setSelectedShipmentOptionByStore({ commit }, { storeId, selectedOption }) {
      commit("setSelectedShipmentOptionByStore", { storeId, selectedOption });
    },
    setSelectedPreOrderHourByStore({ commit }, { storeId, selectedOption }) {
      commit("setSelectedPreOrderHourByStore", { storeId, selectedOption });
    },
    setSelectedPreOrderDayByStore({ commit }, { storeId, selectedOption }) {
      commit("setSelectedPreOrderDayByStore", { storeId, selectedOption });
    },
    async syncSelectedShipmentWithCart(
      { commit, getters, dispatch },
      { storeId }
    ) {
      const { product_id: shipmentId } =
        getters.getShipmentDataFromCart(storeId) || {};

      let storeShipments = getters.getStoreShipmentsById(storeId) || [];
      if (!storeShipments.length) {
        await dispatch("fetchStoreShipmentsById", storeId);
        storeShipments = getters.getStoreShipmentsById(storeId) || [];
      }

      const selectedShipment = storeShipments.find(
        shipment => shipment.entity_id === shipmentId
      );
      commit("setSelectedShipmentOptionByStore", {
        storeId,
        selectedOption: selectedShipment,
      });
    },
    async handleClickFulfillmentMethod(
      { getters, dispatch },
      { method = FULFILLMENT_METHOD.DELIVERY, sellerType }
    ) {
      if (sellerType !== SELLER_TYPES.GROCERY) {
        return dispatch("setFulfillmentMethod", method);
      }

      if (method === FULFILLMENT_METHOD.DELIVERY) {
        const { getNettoIdByPostcode, userAddress } = getters;
        await dispatch("fetchStorePostcodeIdMap");
        await redirectToSellerList({
          sellerType: SELLER_TYPES.GROCERY,
          groceryStoreId: getNettoIdByPostcode(userAddress.postcode),
        });
        return dispatch("setFulfillmentMethod", method);
      }

      dispatch("toggleModal", {
        modalKey: MODAL_KEY_CHANGE_DRAFT_PICKUP_POINT,
      });
    },
  },
  getters: {
    fulfillmentMethod: state => {
      return state.fulfillmentMethod;
    },
    selectedNettoAddress: state => {
      return state.selectedNettoAddress;
    },

    userAddress: state => {
      return get(state, "userAddress", {});
    },
    userPostcode: (state, getters) => {
      const { userAddress } = getters;
      return get(userAddress, "postcode");
    },
    deliveryDraftData: state => {
      return state.deliveryDraftData;
    },
    currDeliveryText: (state, getters) => {
      if (!state.userAddress) {
        const { pathname } = window.location;
        return i18n.t(
          pathname.includes("checkout")
            ? "chooseAddress"
            : "chooseDeliveryMethod"
        );
      }

      return getters.getAddressTextUserAddress(getters.userAddress);
    },
    getAddressTextUserAddress: () => userAddress => {
      const { street, postcode, nr } = userAddress || {};
      if ([street, postcode, nr].every(value => !value)) {
        return "";
      }
      return `${street}${nr ? ` ${nr}` : ""}, ${postcode}`;
    },
    userAddressString: (state, getters) => {
      const { getAddressTextUserAddress, userAddress } = getters;
      return getAddressTextUserAddress(userAddress);
    },
    isMissingAddressData: (state, getters) => {
      return !get(getters, "userAddress.street");
    },
    getStoreShipmentsById: state => storeId => {
      const shipmentData = get(state, `storeShipments.${storeId}`);
      if (!shipmentData) return undefined;
      return Object.values(get(shipmentData, "0"));
    },
    getAvailableStoreShipmentsById: (state, getters) => storeId => {
      const { userPostcode, getStoreShipmentsById } = getters;
      const storeShipments = getStoreShipmentsById(storeId);

      return filterAvailableShipments({
        shipments: storeShipments,
        userPostcode,
      });
    },
    getStorePlasticBagOptions: state => storeId => {
      const shipmentData = get(state, `storeShipments.${storeId}`);
      const plasticBagOptions = get(shipmentData, "1");
      if (!plasticBagOptions) return undefined;

      return Object.values(plasticBagOptions);
    },
    getStoreIdByDraftDeliveryData: (state, getters) => deliveryDraftData => {
      const { fulfillmentMethod } = state;
      return fulfillmentMethod === FULFILLMENT_METHOD.PICKUP
        ? deliveryDraftData.id
        : getters.getNettoIdByPostcode(
            get(deliveryDraftData, "userAddress.postcode")
          );
    },
    currentNettoStoreIdByMethod: (state, getters) => {
      const { fulfillmentMethod, selectedNettoAddress, userAddress } = state;
      return fulfillmentMethod === FULFILLMENT_METHOD.PICKUP
        ? get(selectedNettoAddress, "id")
        : getters.getNettoIdByPostcode(get(userAddress, "postcode"));
    },
    getSelectedShipmentByStoreId: state => storeId => {
      return get(
        state,
        `selectedDeliveryOptionByStore.${storeId}.selectedShipment`
      );
    },
    getSelectedPreOrderHourByStoreId: state => storeId => {
      return get(
        state,
        `selectedDeliveryOptionByStore.${storeId}.selectedPreOrderHour`
      );
    },
    getSelectedPreOrderDayByStoreId: state => storeId => {
      return get(
        state,
        `selectedDeliveryOptionByStore.${storeId}.selectedPreOrderDay`,
        0
      );
    },
    getShipmentDataFromCart: (state, getters, rootState) => storeId => {
      const { cartItems } = rootState;
      const shipment = cartItems.find(
        ({
          attribute_set_id, // shipment object
          parent_product_id, // storeId
        }) =>
          [
            attribute_set_id === ATTRIBUTE_SET.SHIPMENT,
            parent_product_id === +storeId,
          ].every(valid => valid)
      );
      return shipment;
    },
    getPreorderTimeFromCart: (state, getters, rootState) => storeId => {
      if (!storeId) return;
      const { quote } = getters;

      const storePreorderTimeStr = get(
        JSON.parse(get(quote, "preorder_time", {})),
        `${storeId}`
      );
      if (!storePreorderTimeStr) return;
      if (storePreorderTimeStr === "NULL") {
        const result = {
          preorderDate: new Date(),
          selectedHour: "NULL",
          selectedDay: 0,
        };
        return result;
      }

      const preorderDate = new Date(storePreorderTimeStr);
      const result = {
        preorderDate,
        selectedHour: convertDateToStr(preorderDate),
        selectedDay:
          Math.abs(+getStartOfDay(new Date()) - getStartOfDay(preorderDate)) /
          86400000,
      };

      return result;
    },
    getShipmentTextFromCart: (state, getters, rootState) => ({
      storeId,
      shipmentId,
    }) => {
      const cartShipment = getters.getShipmentDataFromCart(storeId);
      const shipment = (getters.getStoreShipmentsById(storeId) || []).find(
        shipment => shipment.entity_id === shipmentId
      );
      if (!cartShipment) return null;

      const { deal_address: method } = cartShipment;

      const methodText = getMethodTextByLanguage(method);
      const shipmentText = getShipmentTextByLanguage(shipment);
      return `${methodText}${shipmentText ? ` | ${shipmentText}` : ""}`;
    },
    searchAnchorPoint: (state, getters) => {
      const { userAddress } = getters;
      // fallback to aha office address
      if (!get(userAddress, "lat")) return null;

      return {
        lat: userAddress.lat,
        lng: userAddress.lon,
      };
    },
  },
};
