<template>
  <div class="location-require-modal__container" ref="modal">
    <BaseModal
      name="location-require"
      :flex="true"
      :isVisible="isModalVisible"
      :isUncloseable="payload.isUncloseable"
      :centeredTitle="true"
      @hide="payload.showAddressModalWithGeoPerm(false)"
    >
      <div class="location-require-modal__message-wrapper">
        {{ $t("requiredMessage") }}
      </div>
      <template #footer>
        <base-button
          type="button"
          class="secondary"
          @click.native="payload.showAddressModalWithGeoPerm(false)"
          >{{ $t("no") }}</base-button
        >
        <base-button
          type="button"
          class="primary"
          @click.native="payload.showAddressModalWithGeoPerm(true)"
          >{{ $t("yes") }}</base-button
        >
      </template>
    </BaseModal>
  </div>
</template>

<i18n>
{
  "is": {
    "requiredMessage": "Do you want to detect your location automatically?",
    "yes": "Yes",
    "no": "No"
  },
  "en": {
    "requiredMessage": "Do you want to detect your location automatically?",
    "yes": "Yes",
    "no": "No"
  }
}
</i18n>

<script>
import { mapGetters } from "vuex";
import { MODAL_KEY_LOCATION_REQUIRED } from "@/store/modalModule";
import BaseModal from "@/components/BaseModal";
import BaseButton from "@/components/BaseButton";
export default {
  name: "LocationRequiredModal",
  components: { BaseModal, BaseButton },
  computed: {
    ...mapGetters(["getModalState", "getModalVisibilityState"]),
    isModalVisible() {
      return this.getModalVisibilityState(MODAL_KEY_LOCATION_REQUIRED);
    },
    payload() {
      return this.getModalState(MODAL_KEY_LOCATION_REQUIRED);
    },
  },
};
</script>

