import get from "@/util/get";
import Vue from "vue";

// each modal must have a unique key
export const MODAL_KEY_PRODUCT = "MODAL_KEY_PRODUCT";
export const MODAL_KEY_DELIVERY_MODAL_V3 = "MODAL_KEY_DELIVERY_MODAL_V3";
export const MODAL_KEY_CHANGE_DRAFT_DELIVERY_ADDRESS =
  "MODAL_KEY_CHANGE_DRAFT_DELIVERY_ADDRESS";
export const MODAL_KEY_CHANGE_DRAFT_PICKUP_POINT =
  "MODAL_KEY_CHANGE_DRAFT_PICKUP_POINT";
export const MODAL_KEY_CONFIRM_CLEAR_CART = "MODAL_KEY_CONFIRM_CLEAR_CART";
export const MODAL_KEY_OUTSIDE_DELIVERY_AREA =
  "MODAL_KEY_OUTSIDE_DELIVERY_AREA";
export const MODAL_KEY_MOBILE_CATEGORIES = "MODAL_KEY_MOBILE_CATEGORIES";
export const MODAL_KEY_VIEW_COUPON_DETAIL = "MODAL_KEY_VIEW_COUPON_DETAIL";
export const MODAL_KEY_ADD_TO_CART_PREVIEW = "MODAL_KEY_ADD_TO_CART_PREVIEW";
export const MODAL_KEY_LOCATION_REQUIRED = "MODAL_KEY_LOCATION_REQUIRED";
export const MODAL_KEY_PRODUCT_OPTION = "MODAL_KEY_PRODUCT_OPTION";
export const MODAL_KEY_CATEGORIES_FILTER = "MODAL_KEY_CATEGORIES_FILTER";
export const MODAL_KEY_MORE_OPTIONS_POP_UP = "MODAL_KEY_MORE_OPTIONS_POP_UP";
export const MODAL_KEY_SELECT_DELIVERY_OPTION =
  "MODAL_KEY_SELECT_DELIVERY_OPTION";
export const MODAL_KEY_SELECT_PICKUP_AREA = "MODAL_KEY_SELECT_PICKUP_AREA";
export const MODAL_KEY_3DS_VERIFICATION = "MODAL_KEY_3DS_VERIFICATION";

export const modalModule = {
  state: {
    modalStackByScreen: {},
    modalState: {},
    modalStack: [],
    noResetModal: false,
  },
  mutations: {
    toggleVisibleState(state, { modalKey, isVisible, ...payload }) {
      state.modalState = {
        ...state.modalState,
        [modalKey]: {
          ...state[modalKey],
          isVisible,
          ...payload,
        },
      };
    },
    pushModalStack(state, payload) {
      state.modalStack = [...state.modalStack, payload];
    },
    popModalStack(state) {
      state.modalStack = state.modalStack.slice(0, -1);
    },
    clearModalStack(state) {
      state.modalStack = [];
    },
    resetModalState(state) {
      state.modalState = {};
      state.modalStack = [];
      state.modalStackByScreen = {};
    },
    pushModalToScreenStack(state, { currPathName, modalKey }) {
      const oldUrlStack = get(state, `modalStackByScreen.${currPathName}`, []);
      Vue.set(state.modalStackByScreen, currPathName, [
        ...oldUrlStack,
        modalKey,
      ]);
    },
    popModalOutOfScreenStack(state, { currPathName }) {
      if (!get(state, `modalStackByScreen.${currPathName}`, []).length) {
        return 0;
      }
      state.modalStackByScreen[currPathName] = state.modalStackByScreen[
        currPathName
      ].slice(0, -1);
    },
    toggleNoResetModal(state, value) {
      state.noResetModal = value;
    },
  },
  actions: {
    toggleModal(
      { state, commit },
      { modalKey, isVisible, registerWithHistory = true, ...payload }
    ) {
      const oldVisibleState = get(state, `modalState.${modalKey}.isVisible`);
      // if isVisible is passed, set isVisible directly by this value
      // else just toggle curr visible state
      const newVisibleState =
        isVisible !== undefined ? isVisible : !oldVisibleState;

      commit("toggleVisibleState", {
        modalKey,
        isVisible: newVisibleState,
        ...payload,
      });
      // if (newVisibleState) {
      //   registerWithHistory && history.pushState(null, document.title, location.href);
      //   commit("pushModalToScreenStack", {
      //     currPathName: window.location.pathname,
      //     modalKey,
      //   });
      // } else {
      //   registerWithHistory && router.go(-1)
      //   commit("popModalOutOfScreenStack", {
      //     currPathName: window.location.pathname,
      //     modalKey,
      //   });
      // }
    },
    hideModal({ dispatch }, modalKey) {
      dispatch("toggleModal", { modalKey, isVisible: false });
    },
    pushModalStack({ commit }, payload) {
      commit("pushModalStack", payload);
    },
    popModalStack({ commit }) {
      commit("popModalStack");
    },
    clearModalStack({ commit }) {
      commit("clearModalStack");
    },
    toggleNoResetModal({ commit }, value) {
      commit("toggleNoResetModal", value);
    },
    resetModalState({ getters, commit }) {
      if (getters.noResetModal) {
        commit("toggleNoResetModal", false);
        return;
      }
      commit("resetModalState");
    },
  },
  getters: {
    getModalVisibilityState: state => modalKey => {
      return get(state, `modalState.${modalKey}.isVisible`);
    },
    getModalState: state => modalKey => {
      return get(state, `modalState.${modalKey}`, {});
    },
    modalStack: state => {
      return get(state, `modalStack`, []);
    },
    getModalStackByScreen: state => currPathName => {
      return get(state, `modalStackByScreen.${currPathName}`, []);
    },
    noResetModal: state => get(state, "noResetModal"),
  },
};
