<template>
  <img @error="handleError" @click="onClick" />
</template>

<script>
import defaultImg from "@/assets/images/default_image.png";

export default {
  name: "ImageViewer",
  props: {},
  methods: {
    onClick() {
      this.$emit("click");
    },
    handleError(e) {
      e.target.style.objectFit = "contain";
      e.target.src = defaultImg;
      return;
    },
  },
};
</script>
