<template>
  <div
    :class="[
      'delivery-curr-address-container',
      isBlinkSuccess && 'delivery-curr-address-container--blink-success',
      isBlinkWarning && 'delivery-curr-address-container--blink-warning',
    ]"
  >
    <DeliveryCurrAddressRepresentation
      :text="customText || currDeliveryText"
      :onClick="toggleSelectDeliveryOptionModal"
      :selectedMethod="selectedMethod"
      :selectedMethodText="selectedMethodText"
    />
  </div>
</template>

<script>
import DeliveryCurrAddressRepresentation from "@/components/DeliveryCurrAddressRepresentation";
import { mapGetters, mapActions } from "vuex";
import { MODAL_KEY_SELECT_DELIVERY_OPTION } from "@/store/modalModule";
import {
  LOADING_KEY_ADDRESS_SET_SUCCESS,
  LOADING_KEY_ADDRESS_SET_WARNING,
} from "@/store/loadingModule";
import { FULFILLMENT_METHOD } from "@/util/constants";

export default {
  name: "DeliveryCurrAddressContainer",
  components: {
    DeliveryCurrAddressRepresentation,
  },
  props: {
    id: Number,
    sellerType: String,
    noSuccessRedirect: Boolean,
    customText: undefined,
    setAddressHook: Function,
    supportPickupShipment: {
      type: Boolean,
      default: true,
    },
    availablePostCodes: Array,
    storeName: {
      type: String,
      default: "",
    },
    viewName: String,
  },
  computed: {
    ...mapGetters(["currDeliveryText", "getLoadingState", "fulfillmentMethod"]),
    isBlinkSuccess() {
      return this.getLoadingState(LOADING_KEY_ADDRESS_SET_SUCCESS);
    },
    isBlinkWarning() {
      return this.getLoadingState(LOADING_KEY_ADDRESS_SET_WARNING);
    },
    routeName() {
      const { name: routeName } = this.$route;
      return routeName;
    },
    selectedMethod() {
      if (!this.supportPickupShipment) return FULFILLMENT_METHOD.DELIVERY;
      return this.fulfillmentMethod;
    },
    selectedMethodText() {
      const {
        fulfillmentMethod,
        supportPickupShipment,
        storeName,
        viewName,
      } = this;
      if (
        fulfillmentMethod === FULFILLMENT_METHOD.DELIVERY ||
        !supportPickupShipment
      ) {
        return this.$t("deliveryTo");
      }

      switch (viewName) {
        case "ViewRestaurants":
        case "ViewStores":
        case "ViewProduct":
          return this.$t("pickupNear");
        default: {
          return `${this.$t("pickupAt")} ${storeName && `"${storeName}"`}`;
        }
      }
    },
    showPickupTextByStore() {
      const renderText = viewName =>
        this.viewName === viewName
          ? `${this.$t("pickupAt")} <b>"${this.storeName}"</b>`
          : this.$t("pickupMethod");
      switch (this.sellerType) {
        case "STORE":
          return renderText("ViewProducts");
        case "RESTAURANT":
          return renderText("ViewRestaurant");
        default: {
          return this.$t("pickupMethod");
        }
      }
    },
  },
  methods: {
    ...mapActions(["toggleModal"]),
    toggleSelectDeliveryOptionModal() {
      const {
        id,
        sellerType,
        noSuccessRedirect,
        setAddressHook,
        viewName,
      } = this;
      return this.toggleModal({
        modalKey: MODAL_KEY_SELECT_DELIVERY_OPTION,
        id,
        sellerType,
        noSuccessRedirect,
        setAddressHook,
        supportPickupShipment: this.supportPickupShipment,
        availablePostCodes: this.availablePostCodes,
        viewName,
        showPickupTextByStore: this.showPickupTextByStore,
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.delivery-curr-address-container--blink-success {
  animation: successBlink 1s linear 1;
}
.delivery-curr-address-container--blink-warning {
  animation: warningBlink 1s linear 2;
}

@keyframes successBlink {
  50% {
    -webkit-box-shadow: 0px 0px 8px 2px rgba(42, 187, 155, 0.98);
    -moz-box-shadow: 0px 0px 8px 2px rgba(42, 187, 155, 0.98);
    box-shadow: 0px 0px 8px 2px rgba(42, 187, 155, 0.98);
  }
}

@keyframes warningBlink {
  50% {
    -webkit-box-shadow: 0px 0px 8px 2px rgba(245, 221, 0, 1);
    -moz-box-shadow: 0px 0px 8px 2px rgba(245, 221, 0, 1);
    box-shadow: 0px 0px 8px 2px rgba(245, 221, 0, 1);
  }
}

@media screen and (max-width: 767px) {
  .delivery-curr-address-container {
    width: 100%;
  }
}
</style>
