import specialPrice from "./specialPrice";
import get from "../util/get";
import { postMessageToApp } from "@/util/reactNative";

// One-stop shop for cart functionality on a given product item
export default {
  name: "CartItemMixin",
  mixins: [specialPrice],
  props: {
    item_id: Number,
    entity_id: Number,
    name: String,
    image: String,
    short_description: String,
    parent_product_id: Number,
    price: Number,
    price_incl_tax: Number,
    qty: Number,
    row_total_incl_tax: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      quantity: this.cartItem ? this.cartItem.cart_qty : 0,
      error: false,
      adding: false,
      addToCartDebounce: null,
      updateQtyDebounce: null,
    };
  },
  computed: {
    quoteItemId() {
      const { item_id, cartItem } = this;
      return item_id || (cartItem && cartItem.item_id);
    },
    cartItem() {
      const matchItemCondition = ({
        parent_product_id,
        product_id,
        item_id,
      }) => {
        if (this.item_id) {
          return item_id == this.item_id;
        }
        return (
          parent_product_id == this.parent_product_id &&
          product_id == (this.entity_id || this.product_id)
        );
      };

      let cartItem = this.$store.state.cartItems.find(matchItemCondition);

      this.quantity = cartItem ? cartItem.cart_qty : 0;
      return cartItem || [];
    },
    cartItemQuantity() {
      return this.cartItem ? this.cartItem.qty : undefined;
    },
    item_price() {
      const { price_incl_tax, price } = this;
      return price_incl_tax || price || 0;
    },
    total() {
      // TODO: Deal with options
      return this.item_price * (this.quantity || this.qty || 0);
    },
  },
  methods: {
    addToCartClient(quantity = 1) {
      const {
        stores,
        entity_id,
        product_id,
        name,
        unit_text,
        image,
        special_price,
        price,
      } = this;
      const parent_product_id = +get(stores, "0.store_id");
      const parent_product_name = get(stores, "0.store_name");
      let data = {
        product_id: entity_id || product_id,
        name,
        item_options: [],
        product_unit_text: unit_text,
        product_img: image,
        attribute_set_id: "",
        cart_qty: quantity,
        parent_product_id,
        parent_product_name,
        full_price: "" + (special_price || price),
      };
      this.quantity = quantity;
      this.$store.commit({
        type: "addToCartClient",
        data,
      });
      clearTimeout(this.addToCartDebounce);
      this.addToCartDebounce = setTimeout(() => this.addToCart(), 2000);
    },
    addToCart() {
      return new Promise(resolve => {
        if (this.adding) return;
        this.adding = true;
        if (!this.quantity || this.quantity == 0) {
          this.quantity = 1;
        }
        let data;
        if (this.product) {
          const { name, description, image, price, entity_id } = this.product;
          data = {
            product: entity_id,
            product_name: name,
            product_image: image,
            product_short_description: description,
            product_price: price,
          };
        } else {
          const {
            entity_id,
            product_id,
            name,
            cloudinaryImage,
            image,
            short_description,
            special_price,
            price,
          } = this;
          data = {
            product: entity_id || product_id,
            product_name: name,
            product_image: cloudinaryImage || image,
            product_short_description: short_description,
            product_price: special_price || price,
          };
        }
        // TODO: This should be consistent
        data.super_product =
          (this.stores && this.stores[0].store_id) ||
          this.parent_product_id ||
          (this.parent && this.parent.entity_id);

        if (this.selected) {
          // Get the original request from store
          data = this.$store.getters.productOptions.request;
          data.options = this.selected;
          data.qty = this.quantity;
        }
        data.qty = this.quantity;
        let self = this;
        this.$store
          .dispatch("addToCart", data)
          .then(res => {
            if (res.data.data.quote_item_id) {
              this.$emit("afterAddToCart", res.data);
              if (self.$options.name == "TheProductOptionsModal") {
                this.close();
              }

              if (
                !(data.product_image || "").includes("https://") &&
                data.product_image !== "no_selection"
              ) {
                data.product_image =
                  "https://images.aha.is/image/upload/e_trim:10/h_400,c_pad,q_auto,f_auto/products" +
                  data.product_image;
              }

              const { qty, product_name } = data;

              this.$store.dispatch("notification", {
                type: "success",
                message: this.$t("addedToCart", {
                  info: `"${qty} x ${product_name}"`,
                }),
                product: {
                  ...data,
                  unit_text: this.unit_text,
                },
              });
            }

            switch (res.data.status) {
              case "error": {
                // only show notification if usingCustomNotification is truthy (existed)
                !this.usingCustomNotification &&
                  this.$store.dispatch("notification", {
                    type: "error",
                    message: res.data.message,
                  });
                this.error = res.data.message;
                break;
              }
              case "options": {
                this.error = res.data.message;
                break;
              }
            }
          })
          .finally(() => {
            this.adding = false;
            resolve();
          });
      });
    },
    /**
     * Update local properties only - does not affect cart
     */
    updateQty(quantity) {
      this.quantity = quantity;
    },
    /**
     * Increases local properties only - does not affect cart
     * TODO: Implement options
     */
    increaseQty() {
      this.quantity++;
    },
    /**
     * Decresaes quantity property only - does not affect cart
     * TODO: Implement options
     */
    decreaseQty() {
      if (this.quantity > 0) {
        this.quantity--;
      }
    },
    /**
     * Increases quantity and totals and updates cart as well
     */
    increaseCartQty() {
      this.updateCartQuantity(this.quantity + 1);
    },
    /**
     * Decreases quantity and totals and updates cart as well
     */
    decreaseCartQty() {
      this.updateCartQuantity(this.quantity - 1);
    },
    updateQtyInCartClient() {
      const { entity_id, product_id, quantity } = this;
      this.$store.commit({
        type: "setClientQtyByProductId",
        data: {
          product_id: entity_id || product_id,
          setqty: quantity,
        },
      });
    },
    updateQtyInCartServer() {
      const { quoteItemId, quantity } = this;
      quoteItemId &&
        this.$store.dispatch("setQuoteItemQty", {
          quote_item_id: quoteItemId,
          setqty: quantity,
        });
    },
    updateCartQuantity(quantity) {
      if (quantity < 0) return;
      postMessageToApp.triggerHapticImpactLight();
      if (this.quantity === 0) {
        this.addToCartClient(quantity);
      }
      this.quantity = quantity;

      if (!this.quoteItemId) {
        this.updateQtyInCartClient();
        clearTimeout(this.addToCartDebounce);
        this.addToCartDebounce = setTimeout(() => this.addToCart(), 2000);
        return;
      }

      this.$store.commit("setClientQty", {
        quote_item_id: this.quoteItemId,
        setqty: quantity,
      });
      clearTimeout(this.updateQtyDebounce);
      this.updateQtyDebounce = setTimeout(
        () => this.updateQtyInCartServer(),
        1000
      );
    },
  },
  watch: {
    cartItemQuantity(qty) {
      this.quantity = qty || this.quantity;
    },
  },
  mounted() {
    const { cartItem, quantity } = this;
    this.quantity = Number(get(cartItem, "cart_qty", quantity));
  },
};
