<template>
  <BaseModal
    class="select-pickup-area-modal modal--no-padding modal--header-bar-as-mobile "
    :isVisible="payload.isVisible && !!payload.verificationUrl"
    centeredTitle
    noFooter
    @hide="hide3DSVerificationModal"
    size="lg"
  >
    <template #header>{{ $t("verifyYourCard") }}</template>
    <LoadingWrapper :loadingKeys="[LOADING_KEY_3DS_MODAL]">
      <iframe
        class="td-iframe"
        :src="payload.verificationUrl"
        frameborder="0"
      ></iframe>
    </LoadingWrapper>
  </BaseModal>
</template>

<i18n>
{
  "en": {
    "verifyYourCard": "Verify your card"
  },
  "is": {
    "verifyYourCard": "Verify your card"
  }
}
</i18n>
<script>
import BaseModal from "@/components/BaseModal";
import LoadingWrapper from "@/components/LoadingWrapper";
import { mapGetters, mapActions } from "vuex";
import { MODAL_KEY_3DS_VERIFICATION } from "@/store/modalModule";
import { LOADING_KEY_3DS_MODAL } from "@/store/loadingModule";

export default {
  props: {
    verificationUrl: String,
  },
  components: {
    BaseModal,
    LoadingWrapper,
  },
  data() {
    return {
      LOADING_KEY_3DS_MODAL,
    };
  },
  computed: {
    ...mapGetters(["getModalState", "userAddressString"]),
    payload() {
      return this.getModalState(MODAL_KEY_3DS_VERIFICATION);
    },
  },
  methods: {
    ...mapActions(["toggleModal"]),
    hide3DSVerificationModal() {
      this.toggleModal({
        modalKey: MODAL_KEY_3DS_VERIFICATION,
        isVisible: false,
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.td-iframe {
  min-width: 100%;
  min-height: 60vh;
}
</style>
