import getDiscountAmountPerUnit from "@/util/getDiscountAmountPerUnit";

export default {
  name: "specialPrice",
  props: {
    special_price: Number | String,
    discount_percent: String,
    special_price_to: String,
    special_price_from: String,
    cart_qty: Number,
    row_total_incl_tax: Number,
    full_price: String,
  },
  computed: {
    discountAmountPerUnit() {
      const {
        special_price,
        special_price_to,
        special_price_from,
        full_price,
        discount_percent,
      } = this;
      return getDiscountAmountPerUnit({
        special_price,
        special_price_to,
        special_price_from,
        full_price,
        discount_percent,
      });
    },
    specialPrice() {
      const {
        special_price,
        special_price_to,
        special_price_from,
        discountAmountPerUnit,
      } = this;

      const now = new Date();
      const specialToDateUnix = +new Date(special_price_to);
      const specialFromDateUnix = +new Date(special_price_from);
      const hasSpecialPrice =
        discountAmountPerUnit ||
        (!!special_price &&
          specialFromDateUnix < now &&
          now < specialToDateUnix);

      if (!hasSpecialPrice) return;
      return +special_price
        ? +special_price - discountAmountPerUnit
        : +this.full_price - discountAmountPerUnit;
    },
    specialPriceTotal() {
      return this.specialPrice * this.cart_qty || undefined;
    },
    fullPriceTotal() {
      return +this.full_price * this.cart_qty || undefined;
    },
  },
};
