  <transition name="navbar-slide">
    <nav class="slidebar-nav" role="navigation" v-if="showCart">
      <OutsideClickWrapper :onClickOutside="closeCart">
        <div class="cart-sidebar box-shadow">
          <ModalTopNavSimulator
            class="cart__header--mobile"
            :onHide="closeCart"
          >
            {{ $t("myCart") }}
          </ModalTopNavSimulator>
          <div class="row cart__header">
            <div class="col-xs-9">
              <h5 class="ellipsis">{{ $t("myCart") }}</h5>
            </div>
            <div class="col-xs-3">
              <span class="close" @click="closeCart()"></span>
            </div>
          </div>
          <div v-if="hasItemInCart" class="checkout-content">
            <div v-if="activeStore">
              <div class="row active-store-box">
                <div class="active-store-items">
                  <cart-item
                    class="cart-item"
                    v-for="item in activeStore.items"
                    v-bind="item"
                    :key="item.item_id"
                  />
                </div>
                <div class="active_store_name_box col-xs-9">
                  <div>
                    {{ activeStore.name || $t("deals") }} -
                    {{ $tc("nItems", countItems(activeStore.items)) }}
                  </div>
                  <div v-if="activeStore.shipment" class="active-store-subtext">
                    {{ $t("deliveryText") }}
                    {{ activeStore.shipment.price_incl_tax | formatISK }}
                  </div>
                </div>
                <div class="active_store_priceTotal_box col-xs-3">
                  {{ priceIncludingShipment(activeStore) | formatISK }}
                </div>
              </div>
            </div>
            <div v-for="store in otherStores" :key="store.name">
              <div class="row other_store">
                <div
                  class="pointer other_store_box"
                  @click="toggleShowItems(store)"
                >
                  <div class="other_store_name_box col-xs-8">
                    <div class="other_store_name cart__store-name-wrapper">
                      <i
                        :class="`cart__arrow icon--arrow-${getArrow(store)}`"
                      />
                      <div>
                        <div class="cart__store-name">
                          {{ store.name || "Tilboð" }} -
                          {{ $tc("nItems", countItems(store.items)) }}
                        </div>
                        <div
                          class="other_store_router_box"
                          v-show="storeUrl(store)"
                        >
                          <router-link
                            class="other_store_router_box_link"
                            :to="`/${storeUrl(store)}`"
                            @click.native="e => e.stopPropagation()"
                            >{{ $t("goToSellerPage") }}</router-link
                          >
                        </div>
                      </div>
                    </div>
                    <div v-if="store.shipment" class="other_store_subtext">
                      {{ $t("deliveryText") }}
