      "myCart": "My Cart",
      "meterText": " more, and get Free Delivery.",
      "freeDelivery": "Free delivery available!",
      "nItems": "{count} item | {count} items",
      "deliveryText": "Including Delivery Fee: ",
      "minimumOrder": "{name}: Minimum order {amount}",
      "goToSellerPage": "Go to seller page",
      "deals": "Deals"
    }
  }
</i18n>
<script>
import CartItem from "./CartItem.vue";
import BaseButton from "./BaseButton";
import GrandTotal from "./GrandTotal";
import OutsideClickWrapper from "@/components/OutsideClickWrapper";
import ModalTopNavSimulator from "@/components/ModalTopNavSimulator";
import removeShipmentItems from "@/util/removeShipmentItems";
import get from "@/util/get";

import cartItemsByStore from "@/mixins/cartItemsByStore";
import clearStoreFromCart from "@/mixins/clearStoreFromCart";
import quote from "@/mixins/quote";
import { mapGetters } from "vuex";

export default {
  name: "TheCart",
  mixins: [cartItemsByStore, clearStoreFromCart, quote],
  components: {
    CartItem,
    BaseButton,
    GrandTotal,
    OutsideClickWrapper,
    ModalTopNavSimulator,
  },
  data() {
    return {
      showItemsInStores: [],
      checkoutRequirementText: "",
    };
  },
  methods: {
    removeShipmentItems,
    countItems(items) {
      return removeShipmentItems(items).reduce(
        (sum, { cart_qty }) => sum + cart_qty,
        0
      );
    },
    closeCart() {
      this.$store.dispatch("setShowCart", false);
    },
    getArrow(store) {
      const { isShowingItems } = this;
      return isShowingItems(store) ? "down" : "right";
    },
    isShowingItems(store) {
      return this.showItemsInStores.includes(store.id);
    },
    stopShowingItems(store) {
      this.showItemsInStores = this.showItemsInStores.filter(
        id => id !== store.id
      );
    },
    startShowingItems(store) {
      this.showItemsInStores.push(store.id);
    },
    toggleShowItems(store) {
      this.isShowingItems(store)
        ? this.stopShowingItems(store)
        : this.startShowingItems(store);
    },
    priceIncludingShipment(store) {
      return store.priceTotal;
    },
    storeUrl(store) {
      if (!store) return undefined;
      const storeUrl = get(store, "items.0.parent_product_url");
      return storeUrl;
    },
  },
  computed: {
    ...mapGetters(["currentStoreId"]),
    hasItemInCart() {
      return this.cartItemsByStore && this.cartItemsByStore.length;
    },
    isMinimumSatisfied() {
      const isMinimumSatisfied = this.cartItemsByStore.every(
        ({ priceTotal, minimumOrderAmount }) =>
          !minimumOrderAmount || priceTotal >= minimumOrderAmount
      );
      return isMinimumSatisfied;
    },
    showCart() {
      return this.$store.state.showCart;
    },
    activeStoreSubtotal() {
      return (
        this.activeStore &&
        this.activeStore.items.reduce(
          (sum, { price_incl_tax: price, cart_qty }) => sum + price * cart_qty,
          0
        )
      );
    },
    activeStoreID() {
      // return this.$route.params.id || 0;
      return this.currentStoreId;
    },
    activeStore() {
      return this.cartItemsByStore.find(({ id }) => id === this.activeStoreID);
    },
    otherStores() {
      return this.cartItemsByStore.filter(
        ({ id }) => id !== this.activeStoreID
      );
    },
  },
  watch: {
    currentStoreId: function(newId, oldId) {
      this.stopShowingItems({ id: oldId });
      if (newId !== "undefined") {
        this.startShowingItems({ id: newId });
      }
    },
    isMinimumSatisfied(isMinimumSatisfied) {
      if (!isMinimumSatisfied) {
        const { minimumOrderAmount, name } =
          this.cartItemsByStore.find(
            ({ priceTotal, minimumOrderAmount }) =>
              priceTotal < minimumOrderAmount
          ) || {};
        this.checkoutRequirementText = this.$t("minimumOrder", {
          name,
          amount: this.$options.filters.formatISK(minimumOrderAmount),
        });
      }
    },
  },
};
</script>
