import axios from "axios";
import get from '@/util/get'

export async function getClientIP() {
  // this function is using inside writeClientLog
  // so we must use normal axios instance instead of API instance to prevent error loop
  // (if there is any err, API instance will catch the error and trigger writeClientLog again)
  const response = await axios.get("https://www.aha.is/getipaddress.php");

  return get(response, 'data.ip')
}
