<template>
  <div
    :class="[
      'noselect quantity-control',
      disabled && 'quantity-control--disabled',
    ]"
  >
    <button
      class="quantity-control__decrement"
      :disabled="disabled"
      @click="decreaseQty"
    >
      -
    </button>
    <input
      class="quantity-control__input"
      type="number"
      pattern="[0-9]*"
      :disabled="disabled"
      :value="defaultQty(quantity)"
      :size="Math.max(2, ('' + quantity).length)"
      @keydown.enter="handleEnter"
      @blur="handleInputBlur"
    />
    <button
      class="quantity-control__increment"
      :disabled="disabled"
      @click="increaseQty"
    >
      +
    </button>
  </div>
</template>

<script>
export default {
  name: "QuantityControl",
  props: {
    updateQuantity: Function,
    quantity: Number,
    disabled: Boolean,
    min: {
      type: Number,
      default: 0,
    },
    isInModal: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      isZero: false,
    };
  },
  methods: {
    defaultQty(quantity) {
      return this.isInModal ? (quantity === 0 ? 1 : quantity) : quantity;
    },
    forcedDigitOnly(value) {
      return value.replace(/[^0-9]+/g, "");
    },
    handleInputBlur(e) {
      const { min, updateQuantity } = this;
      if (e.target.value * 1 !== this.quantity) {
        let newQuantity = this.forcedDigitOnly(e.target.value);

        // we not accept leaving value empty, it will be counted as min
        newQuantity = newQuantity === "" ? +min : +newQuantity;
        // quantity must equal or greater than min
        newQuantity = Math.max(min, newQuantity);

        !this.isZero && this.forceUpdateQuantity(newQuantity);
      }
    },
    decreaseQty() {
      if (
        !this.isInModal ||
        (this.isInModal && this.quantity - 1 > 0 && !this.isZero)
      ) {
        this.forceUpdateQuantity(this.quantity - 1);
      }
    },
    increaseQty() {
      !this.isZero && this.forceUpdateQuantity(this.quantity + 1);
    },
    handleEnter(e) {
      e.target.blur();
    },
    forceUpdateQuantity(newQuantity) {
      if (newQuantity === 0) {
        this.isZero = true;
      }
      return this.updateQuantity(newQuantity);
    },
  },
  destroyed() {
    this.isZero = false;
  },
};
</script>

