import getStartOfDay from "@/util/getStartOfDay";
import { getDateWeekDayStr } from "@/util/openingHours";

function convertDDMMYYYToDate(dd_mm_yyyy) {
  const [dd, mm, yyyy] = dd_mm_yyyy.split("/");
  return new Date(`${mm}/${dd}/${yyyy}`);
}

export default function getOverwroteOpeningHours({
  openingHours,
  openingHoursOverwriteStr,
}) {
  const startOfToday = getStartOfDay(new Date());
  const openingHoursOverwrite = openingHoursOverwriteStr
    .split(";")
    .filter(Boolean)
    .map(day => day.split(","))
    .map(([dateStr, openTime, closeTime]) => [
      convertDDMMYYYToDate(dateStr),
      openTime,
      closeTime,
    ])
    .filter(
      // only overwrite next 7 days
      ([overwriteDate]) =>
        +startOfToday <= +overwriteDate &&
        Math.abs(+startOfToday - overwriteDate) / 86400000 <= 7
    )
    .map(([overwriteDate, openTime, closeTime]) => [
      getDateWeekDayStr(overwriteDate),
      openTime,
      closeTime,
    ]);

  const weekdayToOverwrite = [
    ...new Set(
      openingHoursOverwrite.reduce(
        (result, [weekday]) => [...result, weekday],
        []
      )
    ),
  ];

  const overwroteOpeningHours = [
    ...openingHours.filter(
      ([weekDay]) => !weekdayToOverwrite.includes(weekDay)
    ),
    ...openingHoursOverwrite,
  ];

  return overwroteOpeningHours;
}
