<template>
    <BaseModal
      :isVisible="isClearCartModalVisible"
      :isUncloseable="false"
      :centeredTitle="true"
      @hide="hideClearCartModal"
    >
      <template #header>{{ $t("changeStore") }}</template>
      {{ $t("changingStoreWarning") }}
      <template #footer>
        <div class="clear-cart-footer">
          <base-button class="secondary" @click.native="hideClearCartModal">
            {{ $t("cancel") }}
          </base-button>
          <base-button class="secondary" @click.native="hideAndChangeStore">
            {{ $t("changeAnyway") }}
          </base-button>
        </div>
      </template>
    </BaseModal>
</template>

<i18n>
{
  "is": {
    "changeStore": "Skipta um verslun",
    "cancel": "Hætta við",
    "changeAnyway": "Skipta samt",
    "changingStoreWarning": "Þú ert með vörur í körfu sem tilheyra núverandi verslun. Ef þú skiptir um verslun verða þessar vörur fjarlægðar úr körfunni þinni. Viltu halda áfram?"
  },
  "en": {
    "changeStore": "Change store",
    "cancel": "Cancel",
    "changeAnyway": "Change anyway",
    "changingStoreWarning": "You have items from the current store in your cart. If you change stores, these items will be removed from your cart. Would you like to proceed?"
  }
}
</i18n>

<script>

import BaseModal from "@/components/BaseModal";
import BaseButton from "@/components/BaseButton";
import { mapGetters, mapActions } from 'vuex';
import { MODAL_KEY_CONFIRM_CLEAR_CART } from "@/store/modalModule";
import cartItemsByStore from "@/mixins/cartItemsByStore";
import clearStoreFromCart from "@/mixins/clearStoreFromCart";

export default {
  name: "ClearCartModal",
  components: {
      BaseModal,
      BaseButton,
  },
  mixins: [cartItemsByStore, clearStoreFromCart],
  computed: {
    ...mapGetters(["getModalVisibilityState", "getModalState"]),
    isClearCartModalVisible() {
      return this.getModalVisibilityState(MODAL_KEY_CONFIRM_CLEAR_CART);
    },
    payload() {
      return this.getModalState(MODAL_KEY_CONFIRM_CLEAR_CART);
    },
  },
  methods: {
    ...mapActions(["hideModal"]),
    hideClearCartModal() {
      const { hideModal } = this;
      hideModal(MODAL_KEY_CONFIRM_CLEAR_CART);
    },
    hideAndChangeStore() {
      const { payload } = this;
      payload.callback && payload.callback();
    }
  }
}
</script>

<style lang="scss" scoped>
  .clear-cart-footer {
    display: flex;
    justify-content: flex-end;
  }
</style>