import API from "@/services/API";
import get from "@/util/get";
import { pagination } from "@/util/constants";

export async function updateAccountInfo({
  firstname,
  lastname,
  email,
  nationid,
  shipping_address_id,
  change_password,
  current_password,
  password,
}) {
  const data = {
    firstname,
    lastname,
    email,
    amcustomerattr: {
      nationid,
    },
    shipping_address_id,
    change_password,
    current_password,
    password,
  };

  if (!nationid) delete data.amcustomerattr;

  const result = await API.put("/api/rest/customer", {
    account: data,
  });
  return get(result, "data", {});
}

export async function loginAccount({ username, password }) {
  const result = await API.put("/api/rest/customer", {
    login: {
      username,
      password,
    },
  });
  return get(result, "data", {});
}

export async function register(body) {
  const result = await API.put("/api/rest/customer", { ...body });
  return get(result, "data", {});
}

export async function forgotPassword(email) {
  const result = await API.put("/api/rest/customer", {
    forgetpw: {
      email,
    },
  });
  return get(result, "data", {});
}
export async function enterForgotPassword(body) {
  const result = await API.put("/api/rest/customer", {
    forgetpw: { ...body },
  });
  return get(result, "data", {});
}

export async function getCustomerCreditList({
  skip = skip || pagination.skip,
  limit = limit || pagination.limit,
}) {
  const url = `/api/rest/customer/ccredit/${limit}/${skip}`;
  const result = await API.get(url);
  return get(result, "data", {});
}

export async function refillRechargeCode(rechargeCode) {
  const result = await API.put("/api/rest/customer", {
    ccredit: {
      recharge_code: rechargeCode,
    },
  });
  return get(result, "data", {});
}

export async function updateAddressinfo(address) {
  const {
    address_id,
    firstname,
    lastname,
    telephone,
    postcode,
    street,
    city,
    region,
    default_billing,
    default_shipping,
  } = address;
  const result = await API.put("/api/rest/customer", {
    address: {
      address_id: address_id || null,
      firstname,
      lastname,
      telephone,
      postcode,
      street: { "0": street },
      city: city || null,
      region: region || "",
      default_billing: default_billing ? "1" : "0",
      default_shipping: default_shipping ? "1" : "0",
    },
  });
  return result;
}
