<template>
  <BaseModal
    class="outside-delivery-area-modal"
    ref="deliveryModePicker"
    :flex="true"
    :isVisible="isOutsideWarningModalVisible"
    :centeredTitle="true"
    @hide="hideOutSideDeliveryAreaModal"
  >
    <div class="outside-delivery-area-modal__icon-wrapper">
      <i class="icon--warning outside-delivery-area-modal__icon" />
    </div>
    <h4 class="outside-delivery-area-modal__message">{{ $t("outsideDeliveryAreaWarning") }}</h4>

  </BaseModal>
</template>

<i18n>
{
  "is": {
    "changeAddress": "Breyta heimilisfangi",
    "goBackSellerList": "Aftur í yfirlit",
    "close": "Loka",
    "outsideDeliveryAreaWarning": "Valda netfangið er utan afhendingar svæðisins. \nVeldu annað heimilisfang!"
  },
  "en": {
    "changeAddress": "Change address",
    "goBackSellerList": "Go back to seller list",
    "close": "Close",
    "outsideDeliveryAreaWarning": "The selected address is outside of delivery area.\nPlease select another address!"
  }
}
</i18n>
<script>
import BaseModal from "@/components/BaseModal";
import { mapActions, mapGetters } from "vuex";
import {
  MODAL_KEY_OUTSIDE_DELIVERY_AREA,
} from "@/store/modalModule";

export default {
  name: "OutsideDeliveryAreaModal",
  components: {
    BaseModal,
  },
  computed: {
    ...mapGetters(["getModalVisibilityState", "getModalState"]),
    isOutsideWarningModalVisible() {
      return this.getModalVisibilityState(MODAL_KEY_OUTSIDE_DELIVERY_AREA);
    },
    payload() {
      return this.getModalState(MODAL_KEY_OUTSIDE_DELIVERY_AREA);
    },
  },
  methods: {
    ...mapActions(["hideModal"]),
    hideOutSideDeliveryAreaModal() {
      this.hideModal(MODAL_KEY_OUTSIDE_DELIVERY_AREA);
    },
  },
};
</script>

<style lang="scss" scoped>
.outside-delivery-area-modal {
  background-color: rgba(0, 0, 0, 0.5);
}

.outside-delivery-area-modal__message {
  white-space: pre-wrap;
  text-align: center;
}

.outside-delivery-area-modal__icon-wrapper {
  display: flex;
  justify-content: center;
}

.outside-delivery-area-modal__icon {
  font-size: 50px;
  color: orange;
}
</style>
