import store from "@/store/store";
import changeLanguage from "@/util/changeLanguage";
import { MODAL_KEY_3DS_VERIFICATION } from "@/store/modalModule";
import { LOADING_KEY_3DS_MODAL } from "@/store/loadingModule";
import get from "@/util/get";

function postMessageToReactNative({ type, ...payload }) {
  if (!window.ReactNativeWebView) return;

  window.ReactNativeWebView.postMessage(JSON.stringify({ type, ...payload }));
}

export const postMessageToApp = {
  loaded: () => postMessageToReactNative({ type: "loaded" }),
  logout: () => postMessageToReactNative({ type: "logout" }),
  toggleLogin: () => postMessageToReactNative({ type: "toggleLogin" }),
  changeLanguage: ({ language }) =>
    postMessageToReactNative({ type: "changeLanguage", language }),
  triggerHapticImpactLight: () =>
    postMessageToReactNative({
      type: "triggerHapticFeedback",
      method: "impactLight",
      androidVibrationPattern: 10,
    }),
};

export function bindGlobalCommandShortcut() {
  const { dispatch } = store;
  window.commandShortcut = {
    login: loginData => dispatch("login", loginData),
    loginFB: loginData => dispatch("loginFB", loginData),
    logout: () => dispatch("logout"),
    getSession: () => dispatch("getSession"),
    alert: message => window.alert(message),
    changeLanguage,
    push3DSVerifyStatus: payload => {
      console.log("push3DSVerifyStatus", payload);
      const { status, message } = payload || {};
      if (status === "success") {
        store.dispatch("toggleModal", {
          modalKey: MODAL_KEY_3DS_VERIFICATION,
          isVisible: false,
        });
      }
    },
    toggle3DSModal({ isVisible, verificationUrl }) {
      store.dispatch("toggleModal", {
        modalKey: MODAL_KEY_3DS_VERIFICATION,
        isVisible,
        verificationUrl,
      });
    },
    toggle3DSLoading(isLoading) {
      store.dispatch("toggleLoading", {
        loadingKey: LOADING_KEY_3DS_MODAL,
        isLoading,
      });
    },
    async retryAddingLastPaymentMethod() {
      if (!this.reTriggerSavePaymentMethod) {
        return console.log(
          "retryAddingLastPaymentMethod -> could not found last payment method, is it still existed in checkout screen?"
        );
      }
      try {
        const result = await this.reTriggerSavePaymentMethod();
        console.log("retryAddingLastPaymentMethod -> result", result);
        if (result.status === "success") {
          const verificationUrl = get(result, "data.centinel_data.enabled")
            ? get(result, "data.centinel_data.iframe_url")
            : "";

          verificationUrl &&
            this.toggle3DSModal({ isVisible: true, verificationUrl });
        }
      } catch (error) {
        console.error("retryAddingLastPaymentMethod -> error", error);
      }
    },
    reTriggerSavePaymentMethod: undefined,
  };
}

export function isAppWebView() {
  return window.isAhaApp;
}
