<template>
  <div class="mobile-categories">
    <div class="mobile-categories__header-container">
      <i
        class="mobile-categories__back icon--arrow-left"
        @click="closeCatModal"
      />
      <div class="mobile-categories__title">{{ payload.name }}</div>
    </div>
    <div class="mobile-categories__content-container">
      <div
        class="mobile-categories__item"
        v-for="category in catChildren"
        :key="category.category_id"
        @click="openChildCat(category)"
      >
        <div>{{ category.name }}</div>
        <i class="mobile-categories__item-icon icon--arrow-right" />
      </div>

      <router-link
        class="mobile-categories__item mobile-categories__link"
        active-class="mobile-categories__link--active"
        :to="`/${payload.url_path}`"
        @click.native="clearModalStack"
      >
        <div v-if="payload.level === 0">
          {{ $t("viewAll") }}
        </div>
        <div v-else>
          {{ `${$t("viewAllInChildren")} ${payload.name}` }}
        </div>
        <i class="mobile-categories__item-icon icon--arrow-right" />
      </router-link>
    </div>
  </div>
</template>

<i18n>
{
  "en": {
    "viewAll": "Skoða allt",
    "viewAllInChildren": "Skoða allt í "
  },
  "is": {
    "viewAll": "View All",
    "viewAllInChildren": "All in"
  }
}
</i18n>

<script>
import { mapGetters, mapActions } from "vuex";
import get from "@/util/get";

export default {
  name: "MobileCategories",
  props: {
    payload: Object,
  },
  computed: {
    ...mapGetters(["getModalState"]),
    catChildren() {
      const { payload } = this;
      return get(payload, "children", []);
    },
  },
  methods: {
    ...mapActions(["popModalStack", "pushModalStack", "clearModalStack"]),
    closeCatModal() {
      const { popModalStack } = this;
      popModalStack();
    },
    openChildCat(category) {
      const { pushModalStack, clearModalStack } = this;
      if (category.level < 3) {
        pushModalStack({
          componentName: "MobileCategories",
          stackKey: `MobileCategories__${category.category_id}`,
          ...category,
        });
      } else {
        clearModalStack();
        this.$router.push(`/${category.url_path}`);
      }
    },
  },
};
</script>

