<template>
  <div class="loading-wrapper">
    <div
      v-if="loading"
      class="loading-wrapper__background"
      :style="`background: ${background}`"
    />
    <LoadingIndicator
      class="loading-wrapper__indicator"
      v-if="loading"
      :color="color"
      :size="size"
    />
    <slot />
  </div>
</template>

<script>
import LoadingIndicator from "@/components/LoadingIndicator";
import { mapGetters } from "vuex";

export default {
  name: "LoadingWrapper",
  props: {
    color: String,
    size: String,
    background: {
      type: String,
      default: "#ffffffb0",
    },
    loadingKeys: Array,
  },
  components: {
    LoadingIndicator,
  },
  computed: {
    ...mapGetters(["getLoadingState"]),
    loading() {
      const { getLoadingState, loadingKeys } = this;

      return (
        loadingKeys.length &&
        loadingKeys.every(loadingKey => getLoadingState(loadingKey))
      );
    },
  },
};
</script>

