<template>
  <div class="store-list">
    <div
      v-for="point in points"
      :key="point.name"
      :class="['hover-bg', selectedNettoId === point.id && 'selected']"
      @click="selectPickupPoint(point)"
    >
      <img src="../assets/images/netto_circle.png" :alt="point.name | removeNetto" />
      <span class="store-name">{{ point.name | removeNetto }}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "PickupListSelect",
  props: {
    selectPickupPoint: Function,
    selectedNettoId: Number,
  },
  computed: {
    points() {
      return this.$store.state.pickupLocations;
    },
  },
  filters: {
    removeNetto: str => str.replace(/^Nettó - /, ""),
  },
};
</script>

