<template>
  <transition name="fade">
    <div
      v-if="isVisible"
      :class="['modal modal--mobile', `${name}-modal`, customClass]"
      :id="name"
      tabindex="-1"
      role="dialog"
      :aria-labelledby="label"
      @click="handleClickOutside"
    >
      <div
        :class="['modal-dialog', `modal-${size}`]"
        role="document"
        @click="handleClickInside"
      >
        <div
          :class="[
            'modal-content',
            'modal-content--flex',
            isShake && 'modal-content--shake',
          ]"
        >
          <div class="modal-header">
            <i
              class="header-navigation__icon-back icon--arrow-left"
              @click="hide"
            />
            <h4
              :class="[
                'modal-title',
                centeredTitle && 'modal-title--centered',
                !$slots.header || (noHeaderTitle && 'modal-title--hidden'),
              ]"
              :id="label"
            >
              <slot name="header" />
            </h4>

            <div v-if="!isUncloseable" class="modal__close-btn-wrapper">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                :aria-label="$t('close')"
                @click="hide"
              >
                <i class="icon icon--close-icon" />
              </button>
            </div>
          </div>
          <div class="modal-body">
            <slot></slot>
          </div>
          <div class="modal-footer" v-if="!noFooter">
            <slot name="footer">
              <base-button type="button" class="tertiary" @click.native="hide">
                {{ $t("close") }}
              </base-button>
            </slot>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<i18n>
{
  "is": {
    "close": "Loka"
  },
  "en": {
    "close": "Close"
  }
}
</i18n>

<script>
import BaseButton from "@/components/BaseButton";
import { toggleBodyScroll } from "@/util/toggleBodyScroll";
export default {
  name: "BaseModal",
  components: {
    BaseButton,
  },
  props: {
    name: String,
    isVisible: Boolean,
    isUncloseable: Boolean,
    disableBodyScroll: {
      type: Boolean,
      default: true,
    },
    centeredTitle: Boolean,
    flex: Boolean,
    noFooter: Boolean,
    noHeaderTitle: Boolean,
    clickOutsideToClose: {
      type: Boolean,
      default: true,
    },
    customClass: {
      type: String,
      default: "",
    },
    size: {
      type: String,
      default: "md",
    },
    isShake: Boolean,
  },
  computed: {
    label() {
      return this.name + "-label";
    },
  },
  methods: {
    hide() {
      this.$emit("hide");
    },
    handleClickOutside(e) {
      if (!this.clickOutsideToClose) return;
      e.stopPropagation();
      !this.isUncloseable && this.$emit("hide");
    },
    handleClickInside(e) {
      e.stopPropagation();
    },
  },
  mounted() {
    this.disableBodyScroll && toggleBodyScroll(!this.isVisible);
  },
  beforeDestroy() {
    this.disableBodyScroll && toggleBodyScroll(true);
  },
  watch: {
    isVisible(isVisible) {
      toggleBodyScroll(!isVisible);
    },
  },
};
</script>

