<template>
  <div v-if="!isDelivery">
    <div class="row">
      <div class="cartItemLeft col-xs-2">
        <div class="item-image col-xs-12">
          <ImageViewer
            :src="productCartImage"
            class="img img-responsive"
            loading="lazy"
            :alt="name"
          />
        </div>
      </div>
      <div class="col-xs-10 cart-item__right">
        <div class="price-and-name">
          <div class="item-name">{{ name }}</div>
          <div class="price">
            <span :class="[specialPrice ? 'price-strike' : 'price-final']">{{
              fullPriceTotal | formatISK
            }}</span>
            <span v-if="specialPrice" class="price-final">{{
              specialPriceTotal | formatISK
            }}</span>
          </div>
        </div>
        <div class="item-option">
          <p v-for="option in item_options" :key="option.option_id">
            {{ option.label }}:
            {{ option.print_value }}
          </p>
        </div>
        <div class="unittext-and-qty-button">
          <div class="item-unit_text">{{ product_unit_text || "" }}</div>
          <QuantityControl
            class="quantity-control--cart"
            :disabled="cartItem.hide_custom_option_qty"
            :updateQuantity="updateCartQuantity"
            :quantity="cartItem.cart_qty"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CartItemMixin from "../mixins/CartItemMixin.js";

import QuantityControl from "@/components/QuantityControl";
import ImageViewer from "@/components/ImageViewer";

export default {
  name: "CartItem",
  components: {
    QuantityControl,
    ImageViewer,
  },
  mixins: [CartItemMixin],
  props: {
    name: String,
    item_options: Array,
    product_unit_text: [String, Boolean],
    product_img: String,
    attribute_set_id: String,
    product_id: Number,
    full_price: String,
  },
  computed: {
    isDelivery() {
      // TODO: Use util/constants instead of hardcoded 37
      return this.attribute_set_id === "37";
    },
    productCartImage() {
      if (this.product_img === "no_selection" || !this.product_img) return "";
      return (
        "https://images.aha.is/image/upload/f_auto,h_60/products/" +
        this.product_img
      );
    },
  },
};
</script>

