<template>
  <Fragment>
    <div class="saved-address-list" v-if="addressList.length">
      <div class="saved-address-list__save-address-label">
        {{ $t(headingTitle) }}
      </div>
      <div class="saved-address-list__save-addresses">
        <div
          class="saved-address-list__option hover-bg"
          v-for="oldAddress in addressList"
          :key="oldAddress.id"
          @click="handleSelectAddress(oldAddress)"
        >
          <div>
            {{ getFullAddress(oldAddress) }}
            <span
              v-if="!!oldAddress.isInvalid"
              class="saved-address-list__option-error"
              >{{ $t("invalidForThisStore") }}</span
            >
          </div>
          <i class="saved-address-list__arrow-right icon--arrow-right" />
        </div>
      </div>
    </div>
    <div class="saved-address-list" v-if="requireLogin">
      <p class="saved-address-list__require-login-msg">
        {{ $t("requireLoginMsg") }}
      </p>
      <base-button
        class="primary saved-address-list__log-btn"
        :title="$t('login')"
        @click.native="onToggleLogin"
      >
        {{ $t("login") }}
      </base-button>
    </div>
  </Fragment>
</template>
<i18n>
  {
    "is": {
      "invalidForThisStore": "Við sendum ekki frá þessum seljanda á þetta heimilisfang.",
      "requireLoginMsg": "Skráðu þig inn til að sjá vistuð heimilisföng",
      "login": "Skrá inn"
    },
    "en": {
      "invalidForThisStore": "We do not delivery from this seller to this address.",
      "requireLoginMsg": "Login to see your previously used addresses",
      "login": "Login"
    }
  }
</i18n>
<script>
import { forwardGeoCode } from "@/services/ws";
import execWithLoading from "@/util/execWithLoading";
import BaseButton from "@/components/BaseButton";
import Fragment from "@/components/Fragment";
import { mapActions } from "vuex";

export default {
  name: "SavedAddressList",
  props: {
    customText: String,
    addressList: Array,
    onSelectOldAddress: Function,
    forwardGeocodeLoadingKey: String,
    isRequireLogin: {
      type: Boolean,
      default: false,
    },
    beforeOpenLoginModal: {
      type: Function,
      default: () => {},
    },
  },
  components: {
    BaseButton,
    Fragment,
  },
  computed: {
    headingTitle() {
      return this.customText || "deliveryTo";
    },
    requireLogin() {
      if (this.isRequireLogin && !this.$store.state.customer.is_logged_in) {
        return true;
      }
      return false;
    },
  },
  methods: {
    ...mapActions(["toggleLogin"]),
    getFullAddress({ street, postcode, city }) {
      return `${street}, ${postcode} ${city}`;
    },
    async handleSelectAddress(oldAddess) {
      const { onSelectOldAddress, forwardGeocodeLoadingKey } = this;
      const { street, postcode, firstname, lastname } = oldAddess;

      let addressData = oldAddess;
      try {
        const forwardingResult = await execWithLoading(
          forwardGeocodeLoadingKey,
          () => forwardGeoCode({ address: street, postcode })
        );

        if (forwardingResult.street) {
          addressData = {
            ...forwardingResult,
            street,
            firstname,
            lastname,
          };
        }
      } catch (error) {
        console.error("handleSelectAddress -> error", error);
      }
      onSelectOldAddress(addressData);
    },
    onToggleLogin() {
      this.beforeOpenLoginModal();
      this.toggleLogin();
    },
  },
};
</script>

