.saved-address-list__arrow-right {
  margin-left: auto;
  padding: 0 10px;
  font-size: 10px;
}

.saved-address-list__save-address-label {
  padding: 4px 15px;
  background-color: #f6f6f6;
  border-bottom: 1px solid #dadada;
}
.saved-address-list__save-addresses {
  overflow: auto;
}

.saved-address-list__option {
  display: flex;
  align-items: center;
  padding: 15px;
  border-bottom: 1px solid #dadada;
}

.saved-address-list__option-error {
  display: block;
  font-size: 12px;
  color: #8d8d8f;
  font-style: italic;
}

.saved-address-list__require-login-msg {
  padding: 15px;
  text-align: center;
  margin-bottom: 0;
}

.saved-address-list__log-btn {
  margin: 0 auto 15px auto;
  display: block;
}

@media screen and (min-width: 768px) {
  .saved-address-list__save-addresses {
    max-height: 500px;
  }
}
</style>
