<template>
  <div 
    :class="['outside-click-wrapper', customClass]"
    ref="wrapperRef"
  >
    <slot/>
  </div>
</template>

<script>
  export default {
    name:"OutsideClickWrapper",
    props: {
      customClass: {
        type: String,
        default: ''
      },
      onClickOutside: {
        type: [Boolean, Function],
        required: true,
      },
      treatedAsChildren: Array
    },
    mounted(){
      document.addEventListener('mousedown', this.handleClickOutside)
    },
    beforeDestroy(){
      document.removeEventListener('mousedown', this.handleClickOutside)
    },
    methods: {
      handleClickOutside(e){
        const { onClickOutside, treatedAsChildren = [] } = this

        if (
          treatedAsChildren.length &&
          treatedAsChildren.some(
            ref => ref && (e.target.isEqualNode(ref) || ref.contains(e.target))
          )
        ) {
          return
        }

        if (this.$refs.wrapperRef && this.$refs.wrapperRef.contains(e.target)) {
          return
        }

        onClickOutside && onClickOutside()
      }
    }
  }
</script>

<style lang="scss" scoped>
  .outside-click-wrapper {
    height: 100%;
  }
</style>