/* Implementation of lodash.get function, not working with square brackets [] */
export default function get(object, keys, defaultVal) {
  keys = Array.isArray(keys) ? keys : keys.split(".");
  object = object && object[keys[0]];
  if (object && keys.length > 1) {
    return get(object, keys.slice(1), defaultVal);
  }

  return !object && defaultVal !== undefined ? defaultVal : object;
}
