<template>
  <div class="view-coupon-detail-modal-container" ref="modal">
    <BaseModal
      class
      :flex="true"
      :isVisible="isModalVisible"
      :isUncloseable="payload.isUncloseable"
      :centeredTitle="true"
      @hide="hideCurrentModal"
    >
      <div class="view-coupon-detail-modal__image-wrapper">
        <img
          :src="payload.product_image"
          :alt="payload.product_desc"
          class="view-coupon-detail-modal__image"
        />
      </div>
      <h1 class="view-coupon-detail-modal__name">{{ payload.product_name }}</h1>
      <div class="view-coupon-detail-modal__desc">
        {{ payload.product_desc }}
      </div>
      <div class="row fine-print-wrap">
        <div class="col-sm-9">
          <div class="cpn-content-right">
            <h4 class="view-coupon-detail-modal__title-heading">
              {{ $t("finePrint") }}
            </h4>
            <div v-html="payload.fine_print"></div>
          </div>
        </div>
        <div class="col-sm-3">
          <div class="cpn-qr-code" v-html="payload.qrcode" />
        </div>
      </div>
      <div class="row">
        <div class="col-sm-6 col-xs-6">
          <h4 class="view-coupon-detail-modal__title-heading">Duration:</h4>
          <span
            >{{ payload.valid_from | formatDate }} -
            {{ payload.valid_to | formatDate }}</span
          >
          <h4 class="view-coupon-detail-modal__title-heading">
            {{ $t("price") }}
          </h4>
          <span>{{ (payload.product_price * 1) | formatISK }}</span>
        </div>
        <div class="col-sm-6 col-xs-6">
          <h4 class="view-coupon-detail-modal__title-heading">Seller:</h4>
          <address
            v-if="payload.seller"
            class="view-coupon-detail-modal__address"
          >
            {{ payload.seller.address }} <br v-if="payload.seller.phone" />
            {{ payload.seller.phone }} <br v-if="payload.seller.website" />
            {{ payload.seller.website }}
          </address>
        </div>
      </div>
      <template #footer>
        <span class="footer-item print">
          <router-link
            :to="{
              path: $route.path + '/printcoupon',
              query: { coupon: payload.recordId },
            }"
            target="_blank"
          >
            {{ $t("print") }}
          </router-link>
        </span>
        <input
          type="text"
          class="footer-item coupon-code"
          ref="couponCode"
          readonly
          :value="payload.coupon_code"
        />
        <span class="footer-item copy" @click="copy()" ref="copy">{{
          $t("copy")
        }}</span>
      </template>
    </BaseModal>
  </div>
</template>

<i18n>
{
  "is": {
    "finePrint": "Smáa Letrið",
    "price": "Verð",
    "copy": "Afrita",
    "print": "Prenta"
  },
  "en": {
    "finePrint": "Fine Print:",
    "price": "Price:",
    "copy": "COPY",
    "print": "PRINT"
  }
}
</i18n>

<script>
import i18n from "@/i18n";
import { mapActions, mapGetters } from "vuex";
import { MODAL_KEY_VIEW_COUPON_DETAIL } from "@/store/modalModule";
import BaseModal from "@/components/BaseModal";

export default {
  name: "ViewCouponDetailModal",
  components: {
    BaseModal,
  },
  props: {},
  data() {
    return {
      currRouterName: undefined,
    };
  },
  computed: {
    ...mapGetters(["getModalState", "getModalVisibilityState"]),
    isModalVisible() {
      return this.getModalVisibilityState(MODAL_KEY_VIEW_COUPON_DETAIL);
    },
    payload() {
      return this.getModalState(MODAL_KEY_VIEW_COUPON_DETAIL);
    },
  },
  methods: {
    ...mapActions(["toggleModal", "hideModal"]),
    hideCurrentModal() {
      return this.toggleModal({
        modalKey: MODAL_KEY_VIEW_COUPON_DETAIL,
        isVisible: false,
        isUncloseable: false,
      });
    },
    copy() {
      const { couponCode, copy } = this.$refs;
      couponCode.select();
      couponCode.setSelectionRange(0, 99999); /*For mobile devices*/
      document.execCommand("copy");

      copy.innerText = i18n.t("copied");
    },
  },
  watch: {},
};
</script>

