import get from "@/util/get";

export const ERROR_KEY_CHECKOUT_SCREEN =
"ERROR_KEY_CHECKOUT_SCREEN";

export const errorsModule = {
  state: {
    errors: {}
  },
  mutations: {
    setError(state, { errorKey, ...payload }) {
      state.errors = {
        ...state.errors,
        [errorKey]: payload
      }
    },
  },
  actions: {
    setError({ commit },  { errorKey, ...payload }) {
      commit("setError",  { errorKey, ...payload });
    },
    clearError({ commit }, errorKey) {
      commit("setError",  { errorKey });
    }
  },
  getters: {
    getCheckoutErrorByStore: (state) => (storeId) => {
      return get(state, `errors.ERROR_KEY_CHECKOUT_SCREEN.${storeId}`);
    }
  },
};