<template>
  <div class="ais-infinite-loading" v-if="state">
    <slot name="items" :items="state.hits"> </slot>
    <div class="sentinel" v-observe-visibility="visibilityChanged" />
  </div>
</template>

<script>
import { createWidgetMixin } from "vue-instantsearch";
import { connectInfiniteHits } from "instantsearch.js/es/connectors";

export default {
  name: "AisInfiniteLoading",
  mixins: [createWidgetMixin({ connector: connectInfiniteHits })],
  methods: {
    visibilityChanged(isVisible) {
      if (isVisible && !this.state.isLastPage) {
        this.state.showMore();
      }
    },
  },
};
</script>

<style scoped>
.sentinel {
  float: left;
}
</style>
