<template>
  <!-- This controls login and signup with email and facebook, can be toggled through Vuex store -->
  <div>
    <!-- Login -->
    <base-modal
      name="login"
      :isVisible="isModalVisible"
      @hide="hideLogin"
      :isShake="isModalShake"
    >
      <template #header>{{ $t("loginHeader") }}</template>
      <div v-if="!$store.state.customer.is_logged_in" class="account-login">
        <div class="registered-users">
          <form @submit.prevent="login()">
            <div class="input-box-email">
              <input
                autofocus
                type="text"
                v-model="username"
                name="login[username]"
                value
                id="email"
                class="input-text required-entry validate-email"
                :placeholder="$t('email')"
                autocomplete="username"
                :title="$t('email')"
              />
            </div>
            <div class="input-box-password">
              <input
                type="password"
                v-model="password"
                name="login[password]"
                class="input-text required-entry validate-password"
                id="pass"
                :placeholder="$t('password')"
                autocomplete="current-password"
                :title="$t('password')"
              />
            </div>
            <div class="forgot-password-box">
              <a
                href="#"
                class="pull-right text-underline"
                @click.prevent="showForgotPassword"
                >{{ $t("forgotPassword") }}</a
              >
            </div>
            <div class="buttons-set">
              <base-button
                type="submit"
                name="send"
                class="primary btn-block"
                :class="{ disabled: isPending }"
                :title="$t('login')"
                id="login-btn"
              >
                <span v-if="!isPending">{{ $t("login") }}</span>
                <span v-else>
                  <i class="fa fa-lg fa-spinner fa-spin" />
                </span>
              </base-button>
            </div>
          </form>
          <div class="facebkDiv">
            <base-button
              rel="facebook-connect"
              type="button"
              title="Facebook innskráning"
              class="facebook button facebook_btn btn-block"
              @click.native="continueWithFacebook"
            >
              <span class="f-icon">
                <i class="fa fa-facebook"></i>
              </span>
              <span>{{ $t("facebookLogin") }}</span>
            </base-button>
          </div>
        </div>
        <div class="new-users">
          <div class="buttons-set">
            <span class="not-a-user">{{ $t("notUser") }}</span>
            <span class="new-users-register" @click="showCreateAccount">{{
              $t("register")
            }}</span>
          </div>
        </div>
      </div>
    </base-modal>
    <!-- End Login -->
    <!-- Register -->
    <base-modal
      :isVisible="isNewAccountModalVisible"
      @hide="hideNewAccountModal"
    >
      <template #header>{{ $t("registerHeader") }}</template>
      <input
        type="text"
        v-for="field in registerFields"
        v-model="newUser[field]"
        :key="field"
        :placeholder="$t(field)"
      />
      <div v-for="i in [0, 1]" :key="i">
        <input
          :type="showPassword[i] ? 'text' : 'password'"
          v-model="newPassword[i]"
          :placeholder="`${$t('password')}${i ? ` (${$t('again')})` : ''}`"
        />
        <span class="input-rule">
          <span v-if="newPassword[i].length && !isPasswordVerified(i)">{{
            $t(newPasswordRules[i])
          }}</span>
          <i v-else-if="isPasswordVerified(i)" class="fa fa-check" />
        </span>
      </div>
      <base-button
        class="primary btn-block"
        :disabled="!isNewUserVerified"
        @click.native="createAccount"
      >
        <span v-if="!isPending">{{ $t("register") }}</span>
        <i v-else class="fa fa-lg fa-spinner fa-spin" />
      </base-button>
    </base-modal>
    <!-- End Register -->
    <!-- Forgot password -->
    <base-modal :isVisible="isForgotPasswordVisible" @hide="hideForgotPassword">
      <template #header>{{ $t("forgotPasswordHeader") }}</template>
      <div class="view-forgot-password__wrapper">
        <p class="view-forgot-password__text">
          {{ $t("forgotPasswordSubHeader") }}
        </p>
        <form @submit.prevent="onSubmitForgotPassword()">
          <div class="fieldset">
            <ul class="form-list">
              <li>
                <label for="email_address" class="required">{{
                  $t("email")
                }}</label>
                <div class="input-box">
                  <input
                    type="text"
                    :class="['app-input', isInValidEmail && 'app-input--focus']"
                    value
                    autocomplete="off"
                    v-model="username"
                    ref="email"
                  />
                </div>
                <span class="error error-danger" v-show="isInValidEmail">{{
                  $t("errorMsg")
                }}</span>
              </li>
            </ul>
          </div>
          <div class="view-forgot-password__buttons">
            <base-button
              type="submit"
              class="primary btn-block"
              :class="{ disabled: isPending }"
            >
              <span v-if="!isPending">{{ $t("submitforgotPassword") }}</span>
              <span v-else>
                <i class="fa fa-lg fa-spinner fa-spin" />
              </span>
            </base-button>
            <div class="view-forgot-password__button-login text-center">
              <span @click="showLogin">{{ $t("login") }}</span>
            </div>
          </div>
        </form>
      </div>
    </base-modal>
    <!-- End forgot password -->
  </div>
</template>

