<script>
/* global FB */

import { mapState, mapActions } from "vuex";
import BaseModal from "./BaseModal";
import BaseButton from "./BaseButton";

export default {
  name: "TheLoginModal",
  components: {
    BaseModal,
    BaseButton,
  },
  data() {
    return {
      isNewAccountModalVisible: false,
      isForgotPasswordVisible: false,
      isInValidEmail: false,
      username: "",
      password: "",
      newPassword: {
        0: "",
        1: "",
      },
      showPassword: {
        0: false,
        1: false,
      },
      newPasswordRules: {
        0: "atLeast6",
        1: "samePasswords",
      },
      isPending: false,
      registerFields: ["firstname", "lastname", "email"],
      newUser: {
        firstname: "",
        lastname: "",
        email: "",
      },
      isModalShake: false,
    };
  },
  computed: {
    ...mapState(["notification"]),
    isModalVisible() {
      return this.$store.state.showLogin;
    },
    newPasswordVerified() {
      const { newPassword } = this;
      const first = newPassword[0];
      return first.length >= 6 && newPassword[1] === first ? first : undefined;
    },
    isNewUserVerified() {
      const {
        newUser: { firstname, lastname, email },
        newPasswordVerified,
      } = this;
      return (
        !!firstname.length &&
        !!lastname.length &&
        !!email.match(/\w+@\w+\.\w+/g) &&
        !!newPasswordVerified
      );
    },
  },
  methods: {
    ...mapActions(["forgotPassword"]),
    hideNewAccountModal() {
      this.isNewAccountModalVisible = false;
      this.destroyCurrentFormState();
    },
    hideForgotPassword() {
      this.isForgotPasswordVisible = false;
      this.isInValidEmail = false;
      this.destroyCurrentFormState();
    },
    continueWithFacebook() {
      FB.login(
        ({ authResponse }) => {
          if (authResponse) {
            const { accessToken } = authResponse;
            FB.api("/me?fields=first_name,last_name,email", response => {
              this.$store.dispatch("loginFB", {
                ...response,
                accessToken,
              });
            });
          }
        },
        { scope: "public_profile,email" }
      );
    },
    async login() {
      const { username, password } = this;
      this.isPending = true;
      await this.$store.dispatch("login", { username, password });
      this.$store.state.customer.is_logged_in && this.hideLogin();
      this.isPending = false;
      this.username = "";
      this.password = "";
      return false;
    },
    hideLogin() {
      if (this.isModalVisible) {
        this.$store.dispatch("toggleLogin");
        this.destroyCurrentFormState();
      }
    },
    async createAccount() {
      const { newPasswordVerified: password, newUser } = this;
      this.isPending = true;
      const response = await this.$store.dispatch("createCustomer", {
        register: {
          password,
          ...newUser,
        },
      });
      if (response && response.status === "success") {
        this.isNewAccountModalVisible = false;
        this.destroyCurrentFormState();
      }
      this.isPending = false;
    },
    async onSubmitForgotPassword() {
      this.isPending = true;
      let regex = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      let value = regex.test(this.username);
      if (!value) {
        this.isInValidEmail = true;
        this.isPending = false;
        return;
      }
      this.isInValidEmail = false;
      this.isPending = false;
      const response = await this.forgotPassword(this.username);
      if (response && response.status === "success") {
        this.hideForgotPassword();
      }
    },
    showLogin() {
      this.isForgotPasswordVisible = false;
      this.destroyCurrentFormState();
      this.$store.dispatch("toggleLogin");
    },
    showCreateAccount() {
      this.hideLogin();
      this.isNewAccountModalVisible = true;
    },
    showForgotPassword() {
      this.hideLogin();
      this.isForgotPasswordVisible = true;
    },
    isPasswordVerified(i) {
      switch (i) {
        case 0:
          return this.newPassword[0].length >= 6;
        case 1:
          return !!this.newPasswordVerified;
      }
    },
    destroyCurrentFormState() {
      this.username = "";
      this.password = "";
      this.newUser.firstname = "";
      this.newUser.lastname = "";
      this.newUser.email = "";
      this.newPassword = {
        0: "",
        1: "",
      };
    },
  },
  watch: {
    notification() {
      const { notification } = this;
      if (
        notification &&
        notification.action === "login" &&
        notification.type === "error"
      ) {
        this.isModalShake = true;
        setTimeout(() => (this.isModalShake = false), 500);
      }
    },
  },
};
</script>

