<template>
  <div>
    <MobileHamburgerMenu
      :isActive="currentActiveTab === 'menu'"
      :closeMenu="closeMenu"
      :treatedAsChildren="[$refs.menuBtnRef]"
    />
    <div class="mobile-nav visible-xs" @click="clearModalStack">
      <div class="container">
        <div class="mobile-nav__main-icon mobile-nav__logo">
          <router-link to="/">
            <i class="mobile-nav__aha-logo icon--aha-logo" />
          </router-link>
        </div>
        <div class="mobile-nav__main-icon" @click="focusSearch">
          <i class="icon icon--search" />
        </div>
        <div class="mobile-nav__main-icon" @click="toggleCart">
          <div :class="[itemsInCart]">
            <i
              :class="['icon icon--cart', itemsInCart && 'fa-stack', 'fa-2x']"
              :data-count="itemsInCart"
            />
          </div>
        </div>
        <div
          :class="[
            'mobile-nav__main-icon',
            currentActiveTab === 'menu' && 'active',
          ]"
          @click="toggleActiveTab('menu')"
          ref="menuBtnRef"
        >
          <i class="icon icon--menu" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions, mapGetters } from "vuex";

import MobileHamburgerMenu from "@/components/MobileHamburgerMenu";

import getFlagImage from "@/util/getFlagImage";
import get from "@/util/get";
import changeLanguage from "@/util/changeLanguage";
import removeShipmentItems from "@/util/removeShipmentItems";

export default {
  name: "TheMobileNavigationBottom",
  components: {
    MobileHamburgerMenu,
  },
  data() {
    return {
      currentActiveTab: null,
    };
  },
  computed: {
    ...mapState(["cartItems"]),
    ...mapGetters(["isSearchBarVisible"]),
    itemsInCart() {
      return removeShipmentItems(this.cartItems).reduce(
        (sum, { cart_qty }) => sum + cart_qty,
        0
      );
    },
  },
  methods: {
    ...mapActions(["clearModalStack", "toggleSearchBar"]),
    toggleActiveTab(tabName) {
      if (this.currentActiveTab === tabName) {
        return (this.currentActiveTab = null);
      }
      this.currentActiveTab = tabName;
    },
    closeMenu() {
      this.toggleActiveTab(null);
    },
    toggleCart() {
      this.$store.dispatch("toggleCart");
    },
    toggleLanguage() {
      changeLanguage(this.$i18n.locale === "en" ? "is" : "en");
      this.closeMenu();
    },
    getFlagImage() {
      return getFlagImage();
    },
    focusInput() {
      const searchBox = document.querySelector(
        ".header__search-form--mobile .header__search-form__input"
      );
      searchBox && searchBox.focus();
    },
    focusSearch() {
      const isHomePage = get(this.$route, "name") === "ViewHome";
      if (
        (isHomePage === true &&
          this.isSearchBarVisible === true &&
          isHomePage === this.isSearchBarVisible) ||
        this.isSearchBarVisible
      ) {
        this.focusInput();
        return;
      }
      if (!isHomePage) {
        this.toggleSearchBar(true);
      }
    },
  },
};
</script>

