<template>
  <div id="app">
    <the-layout v-if="switchRouter" />
    <div v-else>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import TheLayout from "./views/TheLayout";
import { mapActions } from "vuex";
import { postMessageToApp } from "@/util/reactNative";
import { handleBackPress } from "@/util/handleBackPress";

export default {
  name: "app",
  components: {
    TheLayout: TheLayout,
  },
  computed: {
    switchRouter() {
      switch (this.$route.path) {
        case "/customer/account/coupons/printcoupon":
        case "/customer/account/orders/printInvoice":
        case "/customer/account/orders/printOrders":
          return false;
        default:
          return true;
      }
    },
  },
  methods: {
    ...mapActions([
      "fetchStorePostcodeIdMap",
      "checkDataVersion",
      "getSession",
      "loadCart",
      "loadCategoriesList",
      "getStoreIdURLMap",
    ]),
    handleClick(state) {
      document.getElementsByTagName("body")[0].style.overflowY =
        state === "open" ? "hidden" : "visible";
    },
  },
  created() {
    this.fetchStorePostcodeIdMap();
    this.getStoreIdURLMap();
    this.loadCategoriesList();
  },
  mounted() {
    const { checkDataVersion, getSession, loadCart } = this;
    postMessageToApp.loaded();
    // Load user and cart session data
    checkDataVersion();
    getSession();
    loadCart();
    handleBackPress();
    // After logged in status changes, reload session data
    this.$store.watch(
      state => state.customer.is_logged_in,
      () => {
        getSession();
        loadCart();
      }
    );

    if (screen.width <= 480) {
      setTimeout(() => {
        const chatlio = document.getElementsByClassName(
          "chatlio-title-bar-content"
        )[0];
        chatlio.addEventListener("click", () => this.handleClick("open"));

        const clickExit = document.getElementsByClassName(
          "chatlio-chat-header__minimize-button"
        )[0];
        clickExit.addEventListener("click", () => this.handleClick("exit"));
      }, 5500);
    }
  },
};

// Convert number to ISK currency string
Vue.filter("formatISK", function(x) {
  if (typeof x !== "number") {
    return x;
  }
  return (
    Math.ceil(x)
      .toString()
      .replace(/\B(?=(\d{3})+(?!\d))/g, ".") + "\u00a0kr."
  );
});

// Convert ISO format to local date
Vue.filter("formatDateTime", function(x) {
  return new Date(x).toLocaleString();
});

Vue.filter("formatDate", function(x) {
  return new Date(x).toLocaleDateString();
});

// Convert number to percentage string
Vue.filter("formatPercentage", function(x) {
  if (typeof x !== "number") {
    return x;
  }
  return parseInt(x * 100) + "%";
});
</script>

