<template>
  <div class="hamburger-department">
    <div class="hamburger-department__header-section">
      <h4 class="hamburger-department__header-title">
        {{ $t("Departments") }}
      </h4>

      <div
        class="hamburger-department__language cursor-pointer"
        @click="toggleLanguage()"
      >
        <span class="hamburger-department__language-text">{{
          $i18n.locale
        }}</span>
        <img
          class="hamburger-department__locale-flag"
          alt="flag"
          :src="getFlagImage()"
        />
      </div>
    </div>

    <div class="hamburger-department__department-list row">
      <DepartmentItem
        class="hamburger-department__department-list-col col-xs-3"
        v-for="departmentItem in getDepartmentItemList()"
        :key="departmentItem.label"
        v-bind="departmentItem"
      />
    </div>
  </div>
</template>
<i18n>
{
  "is": {
    "Departments": "Deildir",
    "feedback": "Endurgjöf",
    "categories": "Flokkar",
    "Daily Deals": "Tilboð dagsins",
    "grocery": "Matvara",
    "food": "Veitingar",
    "stores": "Verslanir",
    "droneDelivery": "Himnasendingar",
    "myAccount": "Mínar síður",
    "cart": "Karfa",
    "menu": "Valmynd",
    "reset": "Endurstilla",
    "chatWithUs": "Spjallaðu við okkur"
  },
  "en": {
    "Departments": "Departments",
    "feedback": "Feedback",
    "categories": "Categories",
    "Daily Deals": "Daily Deals",
    "grocery": "Groceries",
    "food": "Food",
    "stores": "Stores",
    "droneDelivery": "Drone Deliveries",
    "myAccount": "My Account",
    "cart": "Cart",
    "menu": "Menu",
    "reset": "Reset",
    "chatWithUs": "Chat with us"
  }
}
</i18n>
<script>
import getFlagImage from "@/util/getFlagImage";
import changeLanguage from "@/util/changeLanguage";
import DepartmentItem from "@/components/DepartmentItem";
import { mapState, mapActions } from "vuex";
export default {
  name: "MobileHamburgerDepartmentSection",
  components: {
    DepartmentItem,
  },
  props: {
    closeMenu: Function,
  },
  data() {
    return {
      feedbackUrl: "",
      getFlagImage,
    };
  },
  computed: {
    ...mapState(["customer"]),
  },
  methods: {
    ...mapActions(["toggleLogin", "resetData"]),
    toggleLanguage() {
      changeLanguage(this.$i18n.locale === "en" ? "is" : "en");
    },
    openMyAccount() {
      this.closeMenu();
      if (this.customer.is_logged_in) {
        this.$router.push("/customer/account");
      } else {
        this.toggleLogin();
      }
    },
    toggleChatlio() {
      const _chatlio = window._chatlio;
      if (!_chatlio) return;
      _chatlio.isExpanded()
        ? _chatlio.hide()
        : _chatlio.show({ expanded: true });
      this.closeMenu();
    },
    onClickFeedback() {
      window.open(this.feedbackUrl);
    },
    resetUserData() {
      var r = confirm("Are you sure to reset session data?");
      if (r === true) this.resetData();
    },
    getDepartmentItemList() {
      return [
        {
          backgroundColor: "#FDECF0",
          iconClass: "icon--tag-line",
          label: this.$t("Daily Deals"),
          link: "/tilbod-dagsins",
        },
        {
          backgroundColor: "#FFF9E4",
          iconClass: "icon--food",
          label: this.$t("food"),
          link: "/veitingar",
        },
        {
          backgroundColor: "#E9F8F5",
          iconClass: "icon--shopping-basket",
          label: this.$t("grocery"),
          link: "/grocery",
        },
        {
          backgroundColor: "#edeffb",
          iconClass: "icon--stores-icon",
          label: this.$t("stores"),
          link: "/verslun",
        },
        // {
        //   backgroundColor: "#fcedf3",
        //   iconClass: "icon--drone-2",
        //   label: this.$t("droneDelivery"),
        //   disabled: true,
        // },
        {
          backgroundColor: "#EDF6FB",
          iconClass: "icon--user-line",
          label: this.$t("myAccount"),
          onClick: this.openMyAccount,
        },
        {
          backgroundColor: "#FDECF0",
          iconClass: "icon--faq",
          label: this.$t("chatWithUs"),
          onClick: this.toggleChatlio,
        },
        {
          backgroundColor: "#FFF9E4",
          iconClass: "icon--edit",
          label: this.$t("feedback"),
          onClick: this.onClickFeedback,
        },
        {
          backgroundColor: "#E9F8F5",
          iconClass: "icon--sync",
          label: this.$t("reset"),
          onClick: this.resetUserData,
        },
      ].map(props => {
        if (props.link) {
          return {
            ...props,
            onClick: () => {
              this.$router.push(props.link);
              this.closeMenu();
            },
          };
        }
        return props;
      });
    },
  },
  mounted() {
    this.feedbackUrl = encodeURI(
      `https://allrahagur.typeform.com/to/nWp2oe?url=${
        window.location.href
      }&user=${JSON.stringify(this.customer)}`
    );
  },
  watch: {
    customer(customer) {
      this.feedbackUrl = encodeURI(
        `https://allrahagur.typeform.com/to/nWp2oe?url=${
          window.location.href
        }&user=${JSON.stringify(customer)}`
      );
    },
  },
};
</script>

