import store from "@/store/store";
import { SELLER_TYPES } from "@/util/constants";

const requireLogin = (to, from, next) => {
  if (store.state.customer.is_logged_in) {
    next();
  } else {
    store.dispatch("toggleLogin", to.path);
    next({ path: "/" });
  }
};

const requireDataToPrint = (to, from, next) => {
  if (
    store.state.printData &&
    Object.values(store.state.printData).length !== 0
  ) {
    next();
  } else {
    next({ path: "/" });
  }
};

// TODO: Review Memory leaks https://vuejs.org/v2/cookbook/avoiding-memory-leaks.html
