export function afterEach(to) {
  const { name } = to;
  const screenRequiredDeliveryData = [
    "ViewRestaurants",
    "ViewRestaurant",
    "ViewGroceries",
    "ViewGrocery",
    "ViewStores",
  ];
  const sellerTypeMap = {
    ViewRestaurants: SELLER_TYPES.RESTAURANT,
    ViewRestaurant: SELLER_TYPES.RESTAURANT,
    ViewGroceries: SELLER_TYPES.GROCERY,
    ViewGrocery: SELLER_TYPES.GROCERY,
    ViewStores: SELLER_TYPES.STORE,
  };

  if (screenRequiredDeliveryData.includes(name)) {
    setTimeout(() => {
      store.dispatch("checkMissingDeliveryData", {
        sellerType: sellerTypeMap[name],
      });
    });
  }

  store.dispatch("toggleSearchBar", false);
}
export default routes;
