import cartItems from './cartItems';
import pipe from '@/util/pipe';
import getDiscountAmountPerUnit from '@/util/getDiscountAmountPerUnit'
// Return all cart items sorted by their respective stores
// [
//   { name: 'Store 1', items: [...], ... },
//   { name: 'Store 2', items: [...], ... },
// ]
export default {
  name: 'cartItemsByStore',
  mixins: [cartItems],
  methods: {
    arrayOfUniques(arr) {
      const set = new Set(arr.map(JSON.stringify));
      return [...set].map(JSON.parse);
    },
  },
  computed: {
    cartItemsByStore() {
      const { cartItems, arrayOfUniques } = this;
      const storeNames = pipe(
        (items) => items.map(
          ({
            parent_product_name: name,
            parent_product_id: id,
          }) => ({ name, id }),
        ),
        arrayOfUniques,
      )(cartItems);
      const result = storeNames.reduce(
        (arr, { name, id }) => {
          const allItemsInStore = cartItems.filter(({ parent_product_id }) => parent_product_id === id);
          // TODO refactor hardcoded 37 to use ATTRIBUTE_ID_SHIPMENT from util/constants
          let items = allItemsInStore.filter(({ attribute_set_id: id }) => +id !== 37);
          const shipment = allItemsInStore.find(({ attribute_set_id: id }) => +id === 37);
          let priceTotal = items.reduce((sum, cartItem) => {
            const { base_price_incl_tax, cart_qty } = cartItem
            return sum + (+base_price_incl_tax - getDiscountAmountPerUnit(cartItem)) * cart_qty
          }, 0);

          if (isNaN(priceTotal)) {
            priceTotal = items.reduce((sum, item) => {
              const itemPrice = item.row_total_incl_tax || +item.full_price;
              return sum + (itemPrice - getDiscountAmountPerUnit(item)) * item.cart_qty;
            }, 0);
            
          }
          const minimumOrderAmount = shipment && +shipment.shipment_min_order_amt;
          return [
            ...arr,
            {
              name,
              items,
              priceTotal,
              id,
              shipment,
              minimumOrderAmount,
            },
          ];
        },
        [],
      );
      return result
    },
  },
}