import pipe from "./pipe";
import getOverwroteOpeningHours from "@/util/getOverwroteOpeningHours";

// Various convenience functions for working with opening hours

// https://stackoverflow.com/questions/563406/add-days-to-javascript-date
Date.prototype.addDays = function(days) {
  var date = new Date(this.valueOf());
  date.setDate(date.getDate() + days);
  return date;
};

// Weekday index 0 - 6
const weekdays = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];

const filterByDay = whichDay => openingHours =>
  (openingHours || []).filter(([day]) => day === whichDay);

function getDateWeekDayStr(date) {
  return weekdays[new Date(date).getDay()];
}
const now = () => new Date();
const getTodayWeekdayString = () => getDateWeekDayStr(now());

const nDaysAhead = n => {
  const i = weekdays.indexOf(getTodayWeekdayString());
  return weekdays[(i + n) % 7];
};

const getDecimalHours = pipe(
  hhmm => hhmm.split(":"),
  ([hh, mm]) => +hh + +mm / 60
);

const decimalHoursNow = () => now().getHours() + now().getMinutes() / 60;

const getTimestamp = (hhmm, daysAhead = 0) => {
  const date = convertTimeStrToDate(hhmm).addDays(daysAhead);
  return Math.floor(date.getTime() / 1000);
};

const convertDateToStr = date => {
  const minute = date.getMinutes();
  const hour = date.getHours();
  const twoDigitMinutes = minute > 9 ? minute : `0${minute}`;
  const twoDigitHour = hour > 9 ? hour : `0${hour}`;
  return `${twoDigitHour}:${twoDigitMinutes}`;
};

const convertTimeStrToDate = hhmm => {
  const [hours, minutes] = hhmm.split(":").map(str => +str);
  const date = now();
  date.setHours(hours);
  date.setMinutes(minutes);
  date.setSeconds(0);

  return date;
};

function extractOpenHourData({
  restaurant_opening_time,
  restaurant_open_date,
}) {
  const openingHours = restaurant_opening_time
    .split(";")
    // ["Fri,11:30,23:00", "Mon,11:30,22:00", ...]
    .filter(Boolean)
    .map(day => day.split(","));
  // [["Fri", "11:30", "23:00"], ["Mon", "11:30", "22:00"], ...]

  return getOverwroteOpeningHours({
    openingHours,
    openingHoursOverwriteStr: restaurant_open_date || "",
  });
}

export {
  decimalHoursNow,
  nDaysAhead,
  filterByDay,
  getDecimalHours,
  getTimestamp,
  getTodayWeekdayString,
  // extractTodayOpenHour,
  extractOpenHourData,
  convertTimeStrToDate,
  convertDateToStr,
  getDateWeekDayStr,
};
