<template>
  <!-- This modal controls the selection of delivery address and pickup location using MapboxGL -->
  <div class="delivery-modal-v3-container">
    <div
      :class="[
        'delivery-modal__child-modals',
        isShowAddressModal && 'delivery-modal__child-modals--isOpen',
      ]"
    >
      <ChangeDraftDeliveryAddressModal
        :hideDraftAddressModal="hideDraftAddressModal"
        :confirmDeliveryData="confirmDeliveryData"
      />
      <slot />
    </div>
  </div>
</template>

<i18n>
{
  "is": {
    "confirm": "Staðfesta",
    "enterOtherLocation": "Breyta staðsetningu",
    "enterYourLocation": "Sláðu inn staðsetningu þína",
    "cancel": "Hætta við",
    "changeAnyway": "Skipta samt",
    "changingStoreWarning": "Þú ert með vörur í körfu sem tilheyra núverandi verslun. Ef þú skiptir um verslun verða þessar vörur fjarlægðar úr körfunni þinni. Viltu halda áfram?",
    "addressChanged": "Heimilisfangi breytt í \"{currAddress}\", smelltu á staðsetningarmerkið ef þú vilt breyta aftur"
  },
  "en": {
    "confirm": "Confirm",
    "enterOtherLocation": "Enter other location",
    "enterYourLocation": "Enter your location",
    "cancel": "Cancel",
    "changeAnyway": "Change anyway",
    "changingStoreWarning": "You have items from the current store in your cart. If you change stores, these items will be removed from your cart. Would you like to proceed?",
    "addressChanged": "Address set as \"{currAddress}\", click location icon if you need to change it"
  }
}
</i18n>

<script>
import ChangeDraftDeliveryAddressModal from "@/components/ChangeDraftDeliveryAddressModal";
import { mapActions, mapGetters } from "vuex";
import {
  MODAL_KEY_DELIVERY_MODAL_V3,
  MODAL_KEY_CHANGE_DRAFT_DELIVERY_ADDRESS,
  MODAL_KEY_CONFIRM_CLEAR_CART,
  MODAL_KEY_LOCATION_REQUIRED,
} from "@/store/modalModule";
import {
  METHOD_PICKUP,
  SELLER_TYPES,
  FULFILLMENT_METHOD,
} from "@/util/constants";
import redirectToSellerList from "@/util/redirectToSellerList";
import isChangingNettoStoreByPostcode from "@/util/isChangingNettoStoreByPostcode";
import get from "@/util/get";
import cartItemsByStore from "@/mixins/cartItemsByStore";
import clearStoreFromCart from "@/mixins/clearStoreFromCart";

export default {
  name: "DeliveryModalV3",
  components: {
    ChangeDraftDeliveryAddressModal,
  },
  mixins: [cartItemsByStore, clearStoreFromCart],
  data() {
    return {
      currRouterName: undefined,
      isShowAddressModal: false,
    };
  },
  computed: {
    ...mapGetters([
      "getModalState",
      "getModalVisibilityState",
      "selectedNettoAddress",
      "deliveryDraftData",
      "getStoreIdByDraftDeliveryData",
      "getSelectedShipmentByStoreId",
      "userAddress",
      "fulfillmentMethod",
      "getNettoIdByPostcode",
      "getAddressTextUserAddress",
    ]),
    id() {
      // if passing id this id won't be changed when address is being changed
      // example: we should not change id and refetch shipment when we change location in specific store
      return (
        this.payload.id ||
        this.getStoreIdByDraftDeliveryData(this.deliveryDraftData)
      );
    },
    initId() {
      const { userAddress, getNettoIdByPostcode } = this;
      const userPostcode = get(userAddress, "postcode");
      return this.payload.id || getNettoIdByPostcode(userPostcode);
    },
    userAddressText() {
      return get(this, "deliveryDraftData.userAddress.street", "");
    },
    isModalClearCartVisible() {
      return this.getModalVisibilityState(MODAL_KEY_CONFIRM_CLEAR_CART);
    },
    isModalVisible() {
      return this.getModalVisibilityState(MODAL_KEY_DELIVERY_MODAL_V3);
    },
    payload() {
      return this.getModalState(MODAL_KEY_DELIVERY_MODAL_V3);
    },
    modalTitle() {
      const {
        payload: { currentMode },
      } = this;
      return this.$t(
        currentMode ? `choose${currentMode}` : "enterYourLocation"
      );
    },
    disableSubmit() {
      const { deliveryDraftData } = this;
      return !get(deliveryDraftData, "userAddress.street");
    },
    hasItemsInCart() {
      return this.cartItemsByStore.some(({ id }) => id === +this.initId);
    },
    selectedShipment() {
      const { getSelectedShipmentByStoreId, id } = this;
      return getSelectedShipmentByStoreId(id);
    },
  },
  methods: {
    ...mapActions([
      "toggleModal",
      "setDeliveryDraftData",
      "getPickupLocations",
      "addShipmentToCart",
      "setUserAddress",
      "hideModal",
      "notification",
      "fetchStorePostcodeIdMap",
    ]),
    setUserAddressWithHook(userAddress) {
      const { payload, setUserAddress } = this;
      payload.setAddressHook && payload.setAddressHook();
      setUserAddress(userAddress);
    },
    hideDeliveryModal() {
      this.toggleModal({
        modalKey: MODAL_KEY_DELIVERY_MODAL_V3,
        isVisible: false,
        isUncloseable: false,
      });
      this.toggleModal({
        modalKey: MODAL_KEY_CHANGE_DRAFT_DELIVERY_ADDRESS,
        isVisible: false,
        registerWithHistory: false,
      });
      this.isShowAddressModal = false;
    },
    toggleDraftAddressModal(allowGeolocation) {
      this.isShowAddressModal = true;
      this.toggleModal({
        modalKey: MODAL_KEY_CHANGE_DRAFT_DELIVERY_ADDRESS,
        allowGeolocation,
        registerWithHistory: false,
      });
    },
    hideLocationRequiredModal() {
      return this.toggleModal({
        modalKey: MODAL_KEY_LOCATION_REQUIRED,
        isVisible: false,
        isUncloseable: false,
      });
    },
    async toggleLocationRequiredModal() {
      if (!get(navigator, "permissions.query")) {
        // browser does not support check permission
        return this.toggleDraftAddressModal(true);
      }

      const permisson = await navigator.permissions.query({
        name: "geolocation",
      });

      if (!["denied", "granted"].includes(permisson.state)) {
        return this.toggleModal({
          modalKey: MODAL_KEY_LOCATION_REQUIRED,
          showAddressModalWithGeoPerm: allowGeolocation => {
            this.toggleDraftAddressModal(allowGeolocation);
            this.hideLocationRequiredModal();
          },
        });
      }

      this.toggleDraftAddressModal();
    },
    showClearCartModal() {
      const { fulfillmentMethod } = this;
      if (fulfillmentMethod === FULFILLMENT_METHOD.DELIVERY) {
        return this.toggleModal({
          modalKey: MODAL_KEY_CONFIRM_CLEAR_CART,
          isVisible: true,
          callback: this.hideAndChangeStore,
        });
      }
    },
    hideClearCartModal() {
      return this.hideModal(MODAL_KEY_CONFIRM_CLEAR_CART);
    },
    async successRedirect() {
      const { sellerType, noSuccessRedirect } = this.payload;
      const { currRouterName, fulfillmentMethod } = this;

      if (noSuccessRedirect) return;

      if (
        ["ViewGroceries", "ViewGrocery", "ViewProducts"].includes(
          currRouterName
        ) &&
        fulfillmentMethod === FULFILLMENT_METHOD.PICKUP
      ) {
        return;
      }
      await redirectToSellerList({ sellerType, groceryStoreId: this.id });
    },
    hideAndChangeStore() {
      const {
        deliveryDraftData,
        successRedirect,
        initId,
        setUserAddressWithHook,
        hasItemsInCart,
        clearStoreFromCart,
        hideClearCartModal,
        hideDraftAddressModal,
        getAddressTextUserAddress,
        notification,
        id,
      } = this;
      const { preventClearCart, sellerType } = this.payload;

      !preventClearCart && hasItemsInCart && clearStoreFromCart(initId);
      hideClearCartModal && hideClearCartModal();
      setUserAddressWithHook(deliveryDraftData.userAddress);
      successRedirect();
      hideDraftAddressModal();

      if (sellerType === SELLER_TYPES.GROCERY && !id) {
        return;
      }
      notification({
        type: "success",
        message: this.$t("addressChanged", {
          currAddress: getAddressTextUserAddress(deliveryDraftData.userAddress),
        }),
        preventRemoveWhenRouteChange: true,
      });
    },

    async confirmDeliveryData() {
      const {
        // selectedShipment,
        id,
        initId,
        deliveryDraftData,
        isChangingNettoStore,
        hasItemsInCart,
        showClearCartModal,
        hideAndChangeStore,
        // addShipmentToCart,
        hideDeliveryModal,
        payload,
        isChangingStore,
        setUserAddressWithHook,
      } = this;

      const { userAddress } = deliveryDraftData;
      if (
        payload.sellerType === SELLER_TYPES.GROCERY &&
        (await isChangingNettoStore())
      ) {
        return hasItemsInCart && id !== initId
          ? showClearCartModal()
          : hideAndChangeStore();
      }

      if (isChangingStore()) {
        return hideAndChangeStore();
      }

      hideDeliveryModal();
      setUserAddressWithHook(userAddress);
    },

    async isChangingNettoStore() {
      const { initId, deliveryDraftData, fulfillmentMethod, id } = this;
      const userPostcode = get(deliveryDraftData, "userAddress.postcode");

      if (fulfillmentMethod === METHOD_PICKUP) {
        return id !== initId;
      } else {
        return isChangingNettoStoreByPostcode({
          newPostcode: userPostcode,
          oldStoreId: initId,
        });
      }
    },
    isChangingStore() {
      const { userAddress, deliveryDraftData } = this;
      return userAddress.postcode !== deliveryDraftData.postcode;
    },
    hideDraftAddressModal() {
      this.hideDeliveryModal();
    },
  },
  mounted() {
    this.currRouterName = this.$router.history.current.name;
    const { isModalVisible } = this;
    if (isModalVisible) {
      this.toggleLocationRequiredModal();
    }
  },
  watch: {
    async isModalVisible(newVisibleState) {
      if (newVisibleState) {
        this.setDeliveryDraftData({
          ...this.selectedNettoAddress,
          oldUserAddress: this.userAddress,
          userAddress: this.userAddress,
        });
        this.currRouterName = this.$router.history.current.name;
        this.toggleLocationRequiredModal();
      }
    },
  },
};
</script>

