import { router } from "@/main";
import store from "@/store/store";

import { SELLER_TYPES } from "@/util/constants";

const redirectToSellerList = async ({ sellerType, groceryStoreId }) => {
  switch (sellerType) {
    case SELLER_TYPES.RESTAURANT: {
      return router.push({ name: "ViewRestaurants" });
    }
    case SELLER_TYPES.STORE: {
      return router.push({ name: "ViewStores" });
    }
    case SELLER_TYPES.GROCERY: {
      if (!groceryStoreId) return;
      await store.dispatch("getStoreIdURLMap");
      const storeURL = store.getters.getNettoURLByID(groceryStoreId);
      return router.push(`/grocery/${storeURL}`);
    }
    default: {
      return;
    }
  }
};

export default redirectToSellerList;
