<template>
  <transition name="noti-transition">
    <div
      v-if="message"
      :class="[
        'notification-box-wapper',
        product && 'notification--add-to-card',
      ]"
    >
      <div :class="['notification-box', type]">
        <div class="notification-box__header">
          <i
            v-if="type === 'success'"
            class="icon icon--check-fill notification-box__icon"
          />
          <i
            v-if="type === 'warning'"
            class="icon icon--warning notification-box__icon"
          />
          <i
            v-if="type === 'error'"
            class="icon icon--info-fill notification-box__icon"
          />

          <span class="notification-box__header-text" v-html="message" />

          <span class="close-x" @click="deleteThis">
            <i class="icon--remove" />
          </span>
        </div>
        <div class="notification-box__body">
          <div v-if="product" class="notification-box__body--add-to-card">
            <ImageViewer
              class="notification-box__add-to-card-product-img"
              :src="productImage"
              :alt="product.product_name"
              loading="lazy"
            />
            <div class="notification-box__add-to-card-right">
              <div>
                <div>{{ product.product_name }}</div>
                <div class="notification-box__add-to-card-unit-text">
                  {{ product.unit_text }}
                </div>
              </div>
              <div class="notification-box__add-to-card-price">
                {{ product.product_price | formatISK }}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import ImageViewer from "@/components/ImageViewer";

export default {
  name: "Notification",
  components: {
    ImageViewer,
  },
  props: {
    notification: { type: Object, default: () => ({}) },
  },
  data() {
    return {
      timeout: undefined,
    };
  },
  methods: {
    deleteThis() {
      this.$store.dispatch("deleteNotification");
    },
  },
  computed: {
    type() {
      return this.notification.type;
    },
    message() {
      return this.notification.message;
    },
    customClass() {
      return this.notification.customClass || "";
    },
    product() {
      return this.notification.product;
    },
    productImage() {
      if (
        !this.notification.product.product_image ||
        this.notification.product.product_image === "no_selection"
      ) {
        return "";
      }
      return this.notification.product.product_image;
    },
  },
  watch: {
    $route() {
      if (this.notification.preventRemoveWhenRouteChange) {
        return;
      }
      this.deleteThis();
    },
    message() {
      clearTimeout(this.timeout);
      this.timeout = setTimeout(this.deleteThis, 4000);
    },
  },
};
</script>

