<template>
  <OutsideClickWrapper class="app-select" :onClickOutside="showDropdown && toggleDropdown">
    <div class="app-select__container" @click="toggleDropdown">
      <input type="hidden" :name="name" :value="selectedOption && selectedOption.value">

      <div class="app-select__selected-option-container">
        <i 
          v-if="defaultIconClass || (selectedOption && selectedOption.iconClass)"
          :class="defaultIconClass || (selectedOption && selectedOption.iconClass)"
        />

        <div 
          :class="['ellipsis app-select__selected-option', placeholderClass]"
        >
          <div class="app-select__selected-option-label ellipsis">
            {{selectedOption && selectedOption.label}}
          </div>
          <div class="app-select__selected-option-placeholder">
            {{placeholder}}
          </div>

        </div>
        <i class="icon icon--arrow-down app-select__dropdown-icon"/>


        <div :class="['box-shadow app-select__dropdown', showDropdown && 'app-select__dropdown--show']">
          <div 
            class="hover-bg app-select__dropdown-item"
            v-for="option in options"
            v-bind:key="option.value"
            @click="() => onSelectOption(option)"
          >
            <i
              v-if="option && option.iconClass"
              :class="[option && option.iconClass, 'app-select__option-icon']"
            />
            <div class="app-select__option-label">
              {{option && option.label}}
            </div>
          </div>
        </div>
      </div>

    </div>
  </OutsideClickWrapper>
</template>

<script>
  import OutsideClickWrapper from '@/components/OutsideClickWrapper.vue';

  export default {
    name: "Select",
    components: {
      OutsideClickWrapper,
    },
    data: function() {
      return {
        showDropdown: false,
      };
    },
    props: {
      name: {
        type: String,
        default: '',
      },
      defaultIconClass: {
        type: String,
        default: '',
      },
      placeholder: {
        type: String,
        default: 'Select...',
      },
      onSelectOption: Function,
      options: {
        type: Array,
        required: true,
      },
      selectedOption: [Object, undefined],
    },
    computed: {
      placeholderClass() {
        const selectedOption = this.selectedOption
        return selectedOption && (selectedOption.value || selectedOption.label) 
          ? ''
          : 'app-select__selected-option--show-placeholder'
      }
    },
    methods: {
      toggleDropdown() {
        this.showDropdown = !this.showDropdown
      }
    }
  }
</script>

<style lang="scss" scoped>
  .app-select {
    border: 1px solid #eee;
  }
  .app-select__container {
    height: 100%;
  }
  .app-select__selected-option-container {
    display: flex;
    align-items: center;
    position: relative;
    cursor: pointer;
    height: 100%;
  }

  .app-select__dropdown-icon {
    font-size: 10px;
    padding: 0 10px;
  }

  .app-select__selected-option {
    flex-grow: 1;
  }

  .app-select__selected-option {
    padding: 0 10px;
  }

  .app-select__selected-option-placeholder {
    color: #909092;
    display: none;
  }

  .app-select__selected-option--show-placeholder {
    display: block;
    .app-select__selected-option-label {
      display: none;
    }
  }

  .app-select__dropdown-item {
    cursor: pointer;
    padding: 10px;
    text-align: left;
  }
  .app-select__selected-option-label {
    padding: 10px 0;
    max-height: 100px;
    overflow: hidden;
    text-align: left;
  }

  .app-select__dropdown {
    position: absolute;
    bottom: 0;
    transform: translateY(100%);
    background: white;
    width: 100%;
    display: none;
    border-radius: 3px;
    z-index: 1;
  }
  
  .app-select__dropdown--show {
    display: block;
  }
</style>