import { extractOpenHourData } from "@/util/openingHours";

function notOpenInAnyDay({ restaurant_opening_time, restaurant_open_date }) {
  return extractOpenHourData({restaurant_opening_time, restaurant_open_date})
    .map(([, openTimeStr]) => openTimeStr)
    .every((openTimeStr) => openTimeStr === "closed");
}

export default function filterAvailableShipments({ shipments, userPostcode }) {
  if (!shipments) return;

  return shipments.filter((shipment) => {
    const {
      restaurant_delivery_postcodes,
      restaurant_opening_time,
      restaurant_open_date,
    } = shipment;

    if (notOpenInAnyDay({ restaurant_opening_time, restaurant_open_date })) return false;

    if (restaurant_delivery_postcodes === "*") return true;

    const shipmentAvailablePostcode = restaurant_delivery_postcodes.toString()
      .split(",")
      .map(Number);

    if (shipmentAvailablePostcode.includes(+userPostcode)) return true;
  });
}
