import { writeClientLog } from "../services/ws";

function writeErrorLog({ error }) {
  const errMsg = error
    ? error.stack || error.message
    : `Unknown error: ${JSON.stringify(error)}`;
  
  writeClientLog({error: errMsg})
}

window.onerror = function(message, source, lineno, colno, error) {
  writeErrorLog({error: error || new this.Error(message)})
  return;
};

// eslint-disable-next-line no-unused-vars
export default async function vueErrorHandler(error, vm, info) {
  // error: error trace
  // vm: component in which error occured
  // info: Vue specific error information such as lifecycle hooks, events etc.
  
  writeErrorLog({error})
  throw error
}
