import get from "@/util/get";

export const LOADING_KEY_FETCHING_ADDITIONAL_DELIVERY_OPTIONS =
  "LOADING_KEY_FETCHING_ADDITIONAL_DELIVERY_OPTIONS";
export const LOADING_KEY_CHECKOUT_FETCHING_SHIPMENT =
  "LOADING_KEY_CHECKOUT_FETCHING_SHIPMENT";
export const LOADING_KEY_FETCHING_ACCOUNT_INFO =
  "LOADING_KEY_FETCHING_ACCOUNT_INFO";
export const LOADING_KEY_FETCHING_ACCOUNT_PASSWORD =
  "LOADING_KEY_FETCHING_ACCOUNT_PASSWORD";
export const LOADING_KEY_FETCHING_ACCOUNT_ORDERS =
  "LOADING_KEY_FETCHING_ACCOUNT_ORDERS";
export const LOADING_KEY_FETCHING_COUPON = "FETCHING_COUPON";
export const LOADING_KEY_FETCHING_ACCOUNT_CREDIT =
  "LOADING_KEY_FETCHING_ACCOUNT_CREDIT";
export const LOADING_KEY_ADDRESS_SET_SUCCESS =
  "LOADING_KEY_ADDRESS_SET_SUCCESS";
export const LOADING_KEY_ADDRESS_SET_WARNING =
  "LOADING_KEY_ADDRESS_SET_WARNING";
export const LOADING_KEY_FETCH_GROCERY_PRODUCT =
  "LOADING_KEY_FETCH_GROCERY_PRODUCT";
export const LOADING_KEY_DELIVERY_MODAL_V3 = "LOADING_KEY_DELIVERY_MODAL_V3";
export const LOADING_KEY_FETCH_ITEM_GRID = "LOADING_KEY_FETCH_ITEM_GRID";
export const LOADING_KEY_FETCHING_DATA = "LOADING_KEY_FETCHING_DATA";
export const LOADING_KEY_DELIVERY_ADDRESS_FORWARD_GEOCODING =
  "LOADING_KEY_DELIVERY_ADDRESS_FORWARD_GEOCODING";
export const LOADING_KEY_PICKUP_ADDRESS_FORWARD_GEOCODING =
  "LOADING_KEY_PICKUP_ADDRESS_FORWARD_GEOCODING";
export const LOADING_KEY_ADDING_PAYMENT_METHOD =
  "LOADING_KEY_ADDING_PAYMENT_METHOD";
export const LOADING_KEY_3DS_MODAL =
  "LOADING_KEY_3DS_MODAL";

export const loadingModule = {
  state: {
    loadingKeys: {},
  },
  mutations: {
    toggleLoading(state, { loadingKey, isLoading }) {
      const oldLoadingKeyState = get(state, `loadingKeys.${loadingKey}`);
      state.loadingKeys = {
        ...state.loadingKeys,
        [loadingKey]: isLoading !== undefined ? isLoading : !oldLoadingKeyState,
      };
    },
  },
  actions: {
    toggleLoading({ commit }, { loadingKey, isLoading }) {
      commit("toggleLoading", { loadingKey, isLoading });
    },
  },
  getters: {
    getLoadingState: state => loadingKey => {
      return get(state, `loadingKeys.${loadingKey}`, false);
    },
    getCheckoutFetchingShipmentKey: state => storeId => {
      return `${LOADING_KEY_CHECKOUT_FETCHING_SHIPMENT}__${storeId}`;
    },
    getAccountOrdersKey: state => orderId => {
      const loadingKey = LOADING_KEY_FETCHING_ACCOUNT_ORDERS;
      return `${loadingKey}${orderId ? `__${orderId}` : ""}`;
    },
  },
};
