<template>
  <div class="delivery-modal-address">
    <LoadingWrapper :loadingKeys="[LOADING_KEY_DELIVERY_MODAL_V3]">
      <div class="delivery-modal-address__location-map-container">
        <div id="modal-location-map" ref="locationMap"></div>
        <div class="delivery-modal-address__err-msg">
          <p v-if="accuracy !== null">Your accuracy: {{ accuracy }}</p>
          <p v-if="!isHouseLevel">{{ $t("requireHouseLevelMsg") }}</p>
          <p v-if="isValidAccuracy !== null && !isValidAccuracy">
            {{ $t("requireAccuracy") }}
          </p>
        </div>
        <base-button
          class="box-shadow primary delivery-modal-address__confirm-address-btn"
          @click.native="handleConfirmAddress"
          :disabled="isDisableConfirm || gettingAddress"
          >{{ $t(gettingAddress ? "findingAddress" : "confirm") }}</base-button
        >
      </div>
      <div id="modal-location-geocoder"></div>
    </LoadingWrapper>
  </div>
</template>

<i18n>
{
  "is": {
    "chooseLocation": "Velja staðsetningu",
    "select": "Velja",
    "address": "Sláðu inn heimilisfangið þitt eða dragðu kortið",
    "confirm": "Staðfesta",
    "findingAddress": "Leita að heimilisfangi",
    "requireHouseLevelMsg": "Zoom in further to select address or type your address to continue",
    "requireAccuracy": "We could not accurate locate your device - please enter address"
  },
  "en": {
    "chooseLocation": "Choose location",
    "select": "Select",
    "address": "Enter your address or drag the map",
    "confirm": "Confirm",
    "findingAddress": "Finding address",
    "requireHouseLevelMsg": "Zoom in further to select address or type your address to continue",
    "requireAccuracy": "We could not accurate locate your device - please enter address"
  }
}
</i18n>

<script>
import Vue from "vue";
import axios from "axios";
import i18n from "@/i18n";
import { mapGetters, mapActions } from "vuex";
import get from "@/util/get";
let mapboxgl = require("mapbox-gl");
let MapboxGeocoder = require("@mapbox/mapbox-gl-geocoder");
let exceptions = require("@mapbox/mapbox-gl-geocoder/lib/exceptions");
let extend = require("xtend");

import { METHOD_DELIVERY, ACCURACY_NUMBER } from "@/util/constants";
import getGeolocation from "@/util/getGeolocation";
import { MODAL_KEY_CHANGE_DRAFT_DELIVERY_ADDRESS } from "@/store/modalModule";
import { LOADING_KEY_DELIVERY_MODAL_V3 } from "@/store/loadingModule";

import LoadingWrapper from "@/components/LoadingWrapper";
import DeliveryModalList from "@/components/DeliveryModalList";
import BaseButton from "@/components/BaseButton";

export default {
  name: "DeliveryAddressPicker",
  components: {
    // DeliveryModalList,
    BaseButton,
    LoadingWrapper,
  },
  props: {
    confirmAddress: Function,
    resizeKey: undefined,
  },
  data() {
    return {
      mapReady: false,
      address: this.$store.state.delivery.userAddress || undefined,
      location: null,
      gettingAddress: false,
      isDisableConfirm: true,
      map: {},
      geocoder: {},
      geocontrol: {},
      getAddressDebounceTimer: null,
      LOADING_KEY_DELIVERY_MODAL_V3,
      hasFocusInput: false,
      isHouseLevel: true,
      isValidAccuracy: null,
      accuracy: null,
    };
  },
  computed: {
    ...mapGetters(["deliveryDraftData", "userAddress", "getModalState"]),
    userStreet() {
      const { deliveryDraftData, userAddress } = this;
      return (
        get(deliveryDraftData, "userAddress.street") ||
        get(userAddress, "street")
      );
    },
    officeAddress() {
      return [-21.87307, 64.1308556];
    },
    savedAddresses() {
      return (
        this.$store.state.address.list &&
        this.$store.state.address.list.map(
          ({
            entity_id,
            street,
            postcode,
            city,
            firstname,
            lastname,
            telephone,
          }) => ({
            id: entity_id,
            properties: {
              name: `${street}, ${postcode} ${city}`,
              short_description: `${firstname} ${lastname} (${telephone.replace(
                "+354",
                ""
              )})`,
            },
            street,
            postcode,
            firstname,
            lastname,
            telephone,
            method: METHOD_DELIVERY,
          })
        )
      );
    },
    payload() {
      return this.getModalState(MODAL_KEY_CHANGE_DRAFT_DELIVERY_ADDRESS);
    },
    heading() {
      return i18n.t("pickYourLocation");
    },
    clearTextSearchQueryEle() {
      return document.getElementsByClassName("mapboxgl-ctrl-geocoder--button");
    },
  },
  methods: {
    ...mapActions(["toggleLoading", "toggleModal"]),
    centerLocation(center) {
      return {
        container: "modal-location-map",
        center,
        style: "mapbox://styles/mapbox/streets-v11",
        zoom: 16,
        attributionControl: false,
      };
    },
    handleConfirmAddress() {
      this.confirmAddress(this.address);
    },
    selectOldAddress(address) {
      const searchStr = address.properties.name;
      !this.geocoder.mapMarker && this.addMarker();
      this.geocoder._inputEl.value = searchStr;
      this.geocoder._typeahead.selected = null;
      this.geocoder._typeahead.clear();
      this.geocoder._geocode(searchStr).then(data => {
        this.isDisableConfirm = true;
        this.removeOldAddressFromDOM();
        const suggestionsUl = document.getElementsByClassName("suggestions")[0];
        if (data.body.features.length !== 0) {
          suggestionsUl.style.display = "none";
          this.map.flyTo(this.centerLocation(data.body.features[0].center));
        } else {
          suggestionsUl.style.display = "block";
        }
      });
    },
    getAddress: function(geocoder, map, self) {
      if (self.gettingAddress) {
        return;
      }
      self.isDisableConfirm = true;
      self.gettingAddress = true;
      let location = geocoder.mapMarker
        ? geocoder.mapMarker.getLngLat()
        : map.getCenter();
      if (location.lng == 0 && location.lat == 0) {
        location = map.getCenter();
      }
      let data = {
        service: "reverse_geocode",
        latitude: location.lat,
        longitude: location.lng,
      };

      let request = {
        headers: {
          "content-type": "application/json",
          mode: "no-cors",
        },
        params: data,
      };
      // this.toggleLoading({
      //   loadingKey: LOADING_KEY_DELIVERY_MODAL_V3,
      //   isLoading: true,
      // });
      axios
        .post("/ws.php", request)
        .then(res => {
          let r = res.data[0][0];
          geocoder.setInput(r.street + " " + r.nr + ", " + r.postcode);
          geocoder.mapMarker &&
            geocoder.mapMarker.setLngLat({ lng: r.lng, lat: r.lat });
          map.setCenter(r);

          self.address = {
            street: `${r.street} ${r.nr}`,
            postcode: r.postcode,
            special_name: r.serheiti,
            lat: r.lat,
            lon: r.lng,
            city: r.city,
          };

          geocoder.lastSelected = "";
          geocoder.fresh = true;
        })
        .then(() => {
          self.isDisableConfirm = false;
          self.gettingAddress = false;
          this.geocoder._loadingEl.style.display = "none";
          this.geocoder._inputEl.style.pointerEvents = "unset";
          this.geocoder._clearEl.style.display = "block";
        });
    },
    findAccuracy: async function(self) {
      const success = pos => {
        var crd = pos.coords;
        self.accuracy = crd.accuracy;
      };

      const error = err => {
        console.warn(`ERROR(${err.code}): ${err.message}`);
        self.accuracy = null;
      };

      const permisson = await navigator.permissions.query({
        name: "geolocation",
      });
      if (
        !get(navigator, "permissions.query") ||
        ["prompt", "denied"].includes(permisson.state)
      ) {
        // browser does not support check permission
        self.accuracy = null;
        return;
      }

      navigator.geolocation.getCurrentPosition(success, error, {
        enableHighAccuracy: true,
      });
    },

    async getUserLocation() {
      let currentAddress = {};
      let isAssigned = false;
      if (this.address) {
        isAssigned = true;
        // get location store in vuex
        if (this.address.lat && this.address.lon) {
          currentAddress.pos = [this.address.lon, this.address.lat];
        } else {
          currentAddress.street = this.userStreet;
        }
      } else if (navigator.geolocation && !isAssigned) {
        // get location using html5 geolocation api
        try {
          isAssigned = true;
          if (!this.payload.allowGeolocation) {
            throw new Error("Granted is not set");
          }
          const location = await getGeolocation();
          const { accuracy, longitude, latitude } = location.coords;
          if (accuracy > ACCURACY_NUMBER) {
            // throw Error('Maximum accuracy should be lesser than 100m', accuracy)
            currentAddress.pos = null;
          } else {
            currentAddress.pos = [longitude, latitude];
          }
          // }
        } catch (e) {
          isAssigned = false;
          currentAddress.pos = null;
        }
      }

      return currentAddress;
    },
    getAddressDebouncing() {
      const { getAddress, map, geocoder } = this;
      if (!this.isHouseLevel) return;
      clearTimeout(this.getAddressDebounceTimer);
      this.getAddressDebounceTimer = setTimeout(() => {

        this.isDisableConfirm = true;
        this.geocoder._inputEl.value = i18n.t("loading");
        this.geocoder._inputEl.style.pointerEvents = "none";
        this.geocoder._loadingEl.style.display = "block";
        this.geocoder._clearEl.style.display = "none";
        // if (!this.isHouseLevel) {
        //   this.geocoder._removeMarker();
        // }

        getAddress(geocoder, map, this);
      }, 600);
    },
    renderOldAddresses() {
      const el = document.getElementsByClassName(
        "mapboxgl-ctrl-geocoder mapboxgl-ctrl"
      )[0];
      const ComponentClass = Vue.extend(DeliveryModalList);
      let instance = new ComponentClass({
        propsData: {
          features: this.savedAddresses,
          selectOldAddress: this.selectOldAddress,
          heading: this.heading,
        },
      });
      instance.$mount();
      el.appendChild(instance.$el);
    },
    removeOldAddressFromDOM() {
      document.getElementsByClassName("delivery-modal-list")[0] &&
        document.getElementsByClassName("delivery-modal-list")[0].remove();
      this.hasFocusInput = false;
    },
    handleFocusInput() {
      if (
        (!this.geocoder._inputEl.value ||
          (this.geocoder._inputEl.value &&
            !this.geocoder._inputEl.value.trim())) &&
        !this.hasFocusInput
      ) {
        this.renderOldAddresses();
        this.hasFocusInput = true;
      } else {
        this.removeOldAddressFromDOM();
      }
    },
    async initialize() {
      let self = this;
      const currentAddress = await this.getUserLocation();
      const initializeAddress = currentAddress.pos || this.officeAddress;
      mapboxgl.accessToken =
        "pk.eyJ1IjoibWFyb24iLCJhIjoiY2lvZTl3dmU0MDAxdnc0ajd1amQ4aWg1ciJ9.jBLC3HDSgznzsmsRje-uxw"; //TODO: Move to config
      self.map = new mapboxgl.Map(self.centerLocation(initializeAddress));

      let geooptions = {
        accessToken: mapboxgl.accessToken,
        countries: "is",
        placeholder: this.$t("address"),
        marker: {
          color: "#03a0da",
        },
        mapboxgl,
        limit: 10,
        flyTo: {
          speed: 100, // flying speed
          curve: 0.8, // change the speed at which it zooms out
          // This can be any easing function: it takes a number between
          // 0 and 1 and returns another number between 0 and 1.
        },
        render: function(item) {
          const highlightOldAddress = item.place_name
            ? "normal"
            : "highlighted";
          return `<div class="mapboxgl-ctrl-geocoder--suggestion mapboxgl-ctrl-geocoder--suggestion--custom ${highlightOldAddress}"><i class="icon icon--location"></i><h4 class="title">
            ${item.place_name || item.properties.name}
            </h4></div>`;
        },
      };
      self.geocoder = new MapboxGeocoder(geooptions);

      self.geocoder.savedAddresses = this.savedAddresses || [];
      self.geocoder.selectOldAddress = this.selectOldAddress;
      self.isValidAccuracy = this.isValidAccuracy;
      self.geocoder.handleZoom = function() {
        const geolocateControl = document.getElementsByClassName(
          "mapboxgl-ctrl-bottom-right"
        )[0];

        if (self.map.getZoom() >= 16) {
          self.isHouseLevel = true;
          // self.map.dragPan._state === "disabled" && self.map.dragPan.enable();
          if (self.accuracy && self.accuracy <= ACCURACY_NUMBER) {
            geolocateControl.style.opacity = 1;
            geolocateControl.style.visibility = "visible";
          }
          self.map.style.stylesheet.layers.forEach(function(layer) {
            if (layer["id"] === "poi-label") {
              self.map.setLayoutProperty(layer.id, "visibility", "visible");
            }
          });
        } else {
          self.isHouseLevel = false;
          // self.map.dragPan._state !== "disabled" && self.map.dragPan.disable();
          geolocateControl.style.opacity = 0;
          geolocateControl.style.visibility = "hidden";
          self.map.style.stylesheet.layers.forEach(function(layer) {
            if (layer["id"] === "poi-label") {
              self.map.setLayoutProperty(layer.id, "visibility", "none");
            }
          });
        }
      };

      self.geocoder._geocode = function(searchInput) {
        self.isValidAccuracy = !!searchInput && searchInput.trim();
        this._loadingEl.style.display = "block";
        this._eventEmitter.emit("loading", { query: searchInput });
        this.inputString = searchInput;
        // Possible config proprerties to pass to client
        var keys = [
          "bbox",
          "limit",
          "proximity",
          "countries",
          "types",
          "language",
          "reverseMode",
          "mode",
        ];
        var self1 = this;
        // Create config object
        var config = keys.reduce(function(config, key) {
          if (self1.options[key]) {
            // countries, types, and language need to be passed in as arrays to client
            // https://github.com/mapbox/mapbox-sdk-js/blob/master/services/geocoding.js#L38-L47
            ["countries", "types", "language"].indexOf(key) > -1
              ? (config[key] = self1.options[key].split(/[\s,]+/))
              : (config[key] = self1.options[key]);

            if (
              key === "proximity" &&
              self1.options[key] &&
              self1.options[key].longitude &&
              self1.options[key].latitude
            ) {
              config[key] = [
                self1.options[key].longitude,
                self1.options[key].latitude,
              ];
            }
          }
          return config;
        }, {});

        var request;
        if (this.options.localGeocoderOnly) {
          request = Promise.resolve();
        }
        // check if searchInput resembles coordinates, and if it does,
        // make the request a reverseGeocode
        else if (
          this.options.reverseGeocode &&
          /(-?\d+\.?\d*)[, ]+(-?\d+\.?\d*)[ ]*$/.test(searchInput)
        ) {
          // parse coordinates
          var coords = searchInput
            .split(/[\s(,)?]+/)
            .map(function(c) {
              return parseFloat(c, 10);
            })
            .reverse();

          // client only accepts one type for reverseGeocode, so
          // use first config type if one, if not default to poi
          config.types ? [config.types[0]] : ["poi"];
          config = extend(config, { query: coords, limit: 1 });
          request = this.geocoderService.reverseGeocode(config).send();
        } else {
          config = extend(config, { query: searchInput });
          request = this.geocoderService.forwardGeocode(config).send();
        }

        var localGeocoderRes = [];
        if (this.options.localGeocoder) {
          localGeocoderRes = this.options.localGeocoder(searchInput);
          if (!localGeocoderRes) {
            localGeocoderRes = [];
          }
        }

        request.then(
          function(response) {
            this._loadingEl.style.display = "none";

            var res = {};

            if (!response) {
              res = {
                type: "FeatureCollection",
                features: [],
              };
            } else if (response.statusCode == "200") {
              res = response.body;
            }

            res.config = config;
            if (this.fresh) {
              this.eventManager.start(this);
              this.fresh = false;
            }
            // supplement Mapbox Geocoding API results with locally populated results
            res.features = res.features
              ? localGeocoderRes.concat(res.features)
              : localGeocoderRes;

            // apply results filter if provided
            if (this.options.filter && res.features.length) {
              res.features = res.features.filter(this.options.filter);
            }

            const matchSavedAddress = self1.savedAddresses.filter(
              address =>
                address.properties.name
                  .toLowerCase()
                  .indexOf(searchInput.toLowerCase()) !== -1
            );

            if (res.features.length) {
              this._clearEl.style.display = "block";
              this._eventEmitter.emit("results", res);
              this._typeahead.update([...matchSavedAddress, ...res.features]);
            } else {
              if (matchSavedAddress.length !== 0) {
                this._clearEl.style.display = "block";
                this._typeahead.update([...matchSavedAddress]);
                return;
              }
              this._clearEl.style.display = "none";
              this._typeahead.selected = null;
              this._renderNoResults();
              this._eventEmitter.emit("results", res);
            }
          }.bind(this)
        );

        request.catch(
          function(err) {
            this._loadingEl.style.display = "none";

            // in the event of an error in the Mapbox Geocoding API still display results from the localGeocoder
            if (localGeocoderRes.length && this.options.localGeocoder) {
              this._clearEl.style.display = "block";
              this._typeahead.update(localGeocoderRes);
            } else {
              this._clearEl.style.display = "none";
              this._typeahead.selected = null;
              this._renderError();
            }

            this._eventEmitter.emit("results", { features: localGeocoderRes });
            this._eventEmitter.emit("error", { error: err });
          }.bind(this)
        );

        return request;
      };

      self.geocoder._onChange = function() {
        var selected = this._typeahead.selected;
        if (selected && selected.lastname) {
          self.selectOldAddress(selected);
          return;
        }
        if (selected && selected.id !== this.lastSelected) {
          this._clearEl.style.display = "none";
          if (this.options.flyTo) {
            var flyOptions;
            if (
              selected.properties &&
              !exceptions[selected.properties.short_code] &&
              selected.bbox
            ) {
              var bbox = selected.bbox;
              flyOptions = extend({}, this.options.flyTo);
              this._map.fitBounds(
                [[bbox[0], bbox[1]], [bbox[2], bbox[3]]],
                flyOptions
              );
            } else if (
              selected.properties &&
              exceptions[selected.properties.short_code]
            ) {
              // Certain geocoder search results return (and therefore zoom to fit)
              // an unexpectedly large bounding box: for example, both Russia and the
              // USA span both sides of -180/180, or France includes the island of
              // Reunion in the Indian Ocean. An incomplete list of these exceptions
              // at ./exceptions.json provides "reasonable" bounding boxes as a
              // short-term solution; this may be amended as necessary.
              flyOptions = extend({}, this.options.flyTo);
              this._map.fitBounds(
                exceptions[selected.properties.short_code].bbox,
                flyOptions
              );
            } else {
              var defaultFlyOptions = {
                zoom: this.options.zoom,
              };
              flyOptions = extend({}, defaultFlyOptions, this.options.flyTo);
              //  ensure that center is not overriden by custom options
              flyOptions.center = selected.center;
              this._map.flyTo(flyOptions);
            }
          }
          if (this.options.marker && this._mapboxgl) {
            this._handleMarker(selected);
          }

          // After selecting a feature, re-focus the textarea and set
          // cursor at start.
          this._inputEl.focus();
          this._inputEl.scrollLeft = 0;
          this._inputEl.setSelectionRange(0, 0);

          this._eventEmitter.emit("result", { result: selected });
          this.eventManager.select(selected, this);
          this.lastSelected = selected.id;
        }
      };

      self.geocoder.on("result", function(result) {
        self.geocoder.mapMarker.setLngLat(result.result.center);
      });
      self.geocoder.on("error", function(err) {
        console.error(err);
      });

      self.geocontrol = new mapboxgl.GeolocateControl({
        positionOptions: {
          enableHighAccuracy: true,
        },
        trackUserLocation: false,
      });

      self.map.addControl(self.geocontrol, "bottom-right");

      self.geocontrol.on("geolocate", function(result) {
        self.map.flyTo({
          center: [result.coords.longitude, result.coords.latitude],
        });
      });

      self.map.once("idle", function() {
        this.resize();
      });
      self.map.on("dragstart", function() {
        clearTimeout(self.getAddressDebounceTimer);
      });
      self.map.on("dragend", function() {
        const { getAddressDebouncing } = self;
        if (self.isHouseLevel) {
          getAddressDebouncing();
        }
      });

      self.map.on("zoomstart", function() {
        self.isDisableConfirm = true;
        self.geocoder.handleZoom();

        clearTimeout(self.getAddressDebounceTimer);
      });

      self.map.on("zoomend", function() {
        const { getAddressDebouncing } = self;
        if (!self.isHouseLevel) {
          // self.geocoder._removeMarker();
          return;
        }

        if (!self.geocoder.mapMarker) {
          self.addMarker();
        }
        self.geocoder.mapMarker.setLngLat(self.map.getCenter()).addTo(self.map);

        getAddressDebouncing();
      });

      self.map.addControl(self.geocoder);
      if (currentAddress.pos) {
        self.addMarker();
      }

      self.map.on("drag", function() {
        if (!self.geocoder.mapMarker && self.isHouseLevel) {
          self.addMarker();
          return;
        }

        self.isHouseLevel &&
          self.geocoder.mapMarker.setLngLat(self.map.getCenter());
      });

      self.map.on("load", function() {
        self.map.resize();
        self.findAccuracy(self);

        self.map.style.stylesheet.layers.forEach(function(layer) {
          if (layer["id"] === "poi-label") {
            self.map.setLayoutProperty(layer.id, "visibility", "none");
          }
        });

        const geolocateControl = document.getElementsByClassName(
          "mapboxgl-ctrl-bottom-right"
        )[0];

        if (
          !self.accuracy ||
          (self.accuracy && self.accuracy > ACCURACY_NUMBER)
        ) {
          geolocateControl.style.opacity = 0;
          geolocateControl.style.visibility = "hidden";
          self.isValidAccuracy = false;
        } else {
          geolocateControl.style.opacity = 1;
          geolocateControl.style.visibility = "visible";
          self.isValidAccuracy = true;
        }

        if (currentAddress.pos) {
          self.getAddress(self.geocoder, self.map, self);
        }
        if (currentAddress.street) {
          self.geocoder.query(currentAddress.street);
        }

        self.toggleLoading({
          loadingKey: LOADING_KEY_DELIVERY_MODAL_V3,
          isLoading: false,
        });
      });
    },
    addMarker() {
      let marker = new mapboxgl.Marker()
        .setLngLat(this.map.getCenter())
        .addTo(this.map);
      this.geocoder.mapMarker = marker;
    },
    handleClickClearTextSearchQuery() {
      this.renderOldAddresses();
      this.hasFocusInput = true;
    },
  },
  async mounted() {
    this.toggleLoading({
      loadingKey: LOADING_KEY_DELIVERY_MODAL_V3,
      isLoading: true,
    });
    await this.initialize();
    this.geocoder._inputEl.addEventListener("input", this.handleFocusInput);
    this.clearTextSearchQueryEle[0] &&
      this.clearTextSearchQueryEle[0].addEventListener(
        "click",
        this.handleClickClearTextSearchQuery
      );
  },

  beforeDestroy() {
    this.geocoder._inputEl.removeEventListener("input", this.handleFocusInput);
    this.clearTextSearchQueryEle[0] &&
      this.clearTextSearchQueryEle[0].removeEventListener(
        "click",
        this.handleClickClearTextSearchQuery
      );
  },

  watch: {
    resizeKey(newKey, oldKey) {
      if (newKey !== oldKey) {
        this.map.resize();
      }
    },
  },
};
</script>
<style lang="scss">
.mapboxgl-ctrl-bottom-left {
  bottom: 20px;
