import store from "@/store/store";

export default function aisSearchFunction({
  helpers, // first payload of searchFunction
  searchAnchorPoint = store.getters.searchAnchorPoint,
}) {
  let payload = {};

  payload = searchAnchorPoint
    ? {
        aroundLatLng: `${searchAnchorPoint.lat}, ${searchAnchorPoint.lng}`,
        aroundRadius: "all",
        getRankingInfo: true,
      }
    : null;

  helpers.setState({
    ...helpers.state,
    ...payload,
  });

  helpers.search();
}
