<template>
  <div class="wrapper">
    <ais-instant-search
      index-name="products"
      :search-client="searchClient"
      :class-names="{
        'ais-InstantSearch': 'search-wrapper',
      }"
      :search-function="searchFunction"
    >
      <ais-configure :filters="filters" :hits-per-page.camel="24" />
      <header class="header">
        <the-header-top :storeData="storeData" />
        <the-header-bottom />
      </header>
      <div class="main-content">
        <ais-state-results>
          <div slot-scope="{ query, hits }">
            <div v-if="scrollTop(query)" />
            <AisCategoryFilterBySearching
              v-if="isSearching && query && hits.length"
              :items="hits"
              :query="query"
              @categoryId="onClickCategoryWhenSearching"
            />
            <div v-if="isSearching && !hits.length" class="container mt--40">
              {{ $t("noResultsFor") }}
              <b>{{ query }}</b
              >.
            </div>

            <div v-else-if="isSearching && query">
              <DeliveryBarWrapper
                class="delivery-bar-wrapper--mt-5 container"
                v-show="showDeliveryBar()"
              >
                <div class="delivery-bar-wrapper__left-group">
                  <DeliveryCurrAddressContainer
                    noSuccessRedirect
                    :customText="isMissingAddressData && $t('setLocationAlert')"
                    :setAddressHook="stopSearching"
                  />
                </div>
              </DeliveryBarWrapper>
              <AisInfiniteLoading>
                <template slot="items" slot-scope="{ items }">
                  <div class="container mt--40">
                    <div class="row grid-search-item-container">
                      <div
                        v-for="item in items"
                        :key="item.objectID"
                        class="col-xs-12 col-sm-6 col-md-3 ais__search-item"
                      >
                        <keep-alive>
                          <component
                            :is="componentName"
                            :parent_product_id="storeData.storeId"
                            :entity_id="+item.objectID"
                            :showQuantityControlInMobile="
                              componentName === 'GridGroceryItem'
                            "
                            v-bind="item"
                            :imgHeight="150"
                          />
                        </keep-alive>
                      </div>
                    </div>
                  </div>
                </template>
              </AisInfiniteLoading>
            </div>
            <div v-else>
              <router-view />
            </div>
          </div>
        </ais-state-results>
      </div>
      <TheCart />
      <TheMobileNavigationBottom />
      <TheFooter />
      <TheProductOptionsModal
        v-if="showOptions"
        v-bind="productOptions.request"
      />
      <TheLoginModal />
    </ais-instant-search>
    <GlobalModals />
    <Notification :notification="$store.state.notification" />
  </div>
</template>

<i18n>
  {
  "is": {
  "noResultsFor": "Engar niðurstöður fundust fyrir",
  "setLocationAlert":"Veldu staðsetningu til að sýna niðurstöður nálægt þér fyrst",
  "showMore": "Sjá fleiri vörur"

  },
  "en": {
  "noResultsFor": "No results found for",
  "setLocationAlert":"Set location to show results near you first",
    "showMore": "Show more items"
  }
  }
</i18n>

<script>
import TheHeaderTop from "@/components/TheHeaderTop";
import TheHeaderBottom from "@/components/TheHeaderBottom";
import TheFooter from "@/components/TheFooter";
import TheLoginModal from "@/components/TheLoginModal";
import algoliasearch from "algoliasearch/lite";
import TheCart from "@/components/TheCart";
import GlobalModals from "@/components/GlobalModals";
import Notification from "@/components/Notification";
import AisInfiniteLoading from "@/components/AisInfiniteLoading";
import DeliveryBarWrapper from "@/components/DeliveryBarWrapper";
import DeliveryCurrAddressContainer from "@/components/DeliveryCurrAddressContainer";
import { mapGetters, mapActions, mapState } from "vuex";
import get from "@/util/get";
import BaseButton from "@/components/BaseButton";
import aisSearchFunction from "@/util/aisSearchFunction";
const TheProductOptionsModal = () =>
  import("@/components/TheProductOptionsModal");
import TheMobileNavigationBottom from "@/components/TheMobileNavigationBottom";
const GridSearchItem = () => import("@/components/GridSearchItem");
const GridGroceryItem = () => import("@/components/GridGroceryItem");
const AisCategoryFilterBySearching = () =>
  "@/components/AisCategoryFilterBySearching";

export default {
  name: "TheLayout",
  components: {
    TheCart,
    TheMobileNavigationBottom,
    TheProductOptionsModal,
    TheLoginModal,
    TheHeaderTop,
    TheHeaderBottom,
    TheFooter,
    GridSearchItem,
    GridGroceryItem,
    GlobalModals,
    Notification,
    DeliveryBarWrapper,
    DeliveryCurrAddressContainer,
    BaseButton,
    AisInfiniteLoading,
    AisCategoryFilterBySearching,
  },
  data() {
    return {
      searchClient: algoliasearch(
        //TODO: Move to proxy
        "5D97PB00YB",
        "6e6550362dda457bf773b121e6e6acf0"
      ),
      queryClone: "",
      filterCategoryId: null,
    };
  },
  computed: {
    ...mapGetters([
      "userAddress",
      "isMissingAddressData",
      "getNettoIdByURL",
      "selectedFilterOption",
      "getNettoNameByID",
      "productOptions",
    ]),
    ...mapState(["isSearching"]),
    showOptions() {
      return typeof this.productOptions.options !== "undefined";
    },
    componentName() {
      return this.filters ? "GridGroceryItem" : "GridSearchItem";
    },
    filters() {
      const { selectedFilterOption } = this;
      const storeIdToFilter = +get(selectedFilterOption, "value");
      let query =
        this.isFiltered && storeIdToFilter
          ? `stores.store_id: ${storeIdToFilter}`
          : "";

      if (this.filterCategoryId) {
        query =
          query +
          `${query ? " AND " : ""}categories.category_id: ${
            this.filterCategoryId
          }`;
      }

      return query;
    },
    isFiltered() {
      return !!this.storeData.storeUrl;
    },
    storeData() {
      const storeUrl = this.$route.params.storeUrl || this.$route.params.path;
      const storeId = this.getNettoIdByURL(storeUrl);
      return {
        storeUrl,
        storeId,
      };
    },
  },
  methods: {
    ...mapActions(["stopSearching", "setFilterOptions", "getStoreIdURLMap"]),
    scrollTop(query) {
      if (query !== this.queryClone) {
        this.queryClone = query;
        window.scrollTo(0, 0);
      }
    },
    searchFunction(helpers) {
      return aisSearchFunction({ helpers });
    },
    showDeliveryBar() {
      const { name: routeName } = this.$route;
      switch (routeName) {
        case "ViewGrocery":
        case "ViewGroceries":
          return false;
        default: {
          return true;
        }
      }
    },
    onClickCategoryWhenSearching(categoryId) {
      this.filterCategoryId = categoryId;
    },
  },
  watch: {
    async storeData(storeData) {
      const { storeId } = storeData;
      if (storeId) {
        await this.getStoreIdURLMap();
        const filterOptions = [
          {
            label: this.getNettoNameByID(storeId),
            value: storeId,
          },
          {
            label: "*",
            value: "*",
          },
        ];
        this.setFilterOptions(filterOptions);
      }
    },
    queryClone(newQuery, oldQuery) {
      if (newQuery !== oldQuery) {
        this.filterCategoryId = null;
      }
    },
  },
};
</script>

