<template>
  <!-- :isVisible="payload.isVisible" -->
  <!-- :isVisible="true" -->
  <BaseModal
    class="select-pickup-area-modal modal--no-padding modal--header-bar-as-mobile "
    :isVisible="payload.isVisible"
    centeredTitle
    noFooter
    @hide="hideSelectPickupAreaModal"
  >
    <template #header>{{ $t("selectPickupArea") }}</template>
    <LoadingWrapper
      :loadingKeys="[LOADING_KEY_PICKUP_ADDRESS_FORWARD_GEOCODING]"
    >
      <div
        class="select-pickup-area-modal__option hover-bg"
        @click="handleClickSelectLastAddress"
        v-if="userAddressString"
      >
        <i class="icon icon--location" />
        <span class="select-pickup-area-modal__option-label">
          {{ $t("selectLastAddress") }} - {{ userAddressString }}
        </span>
        <i class="select-pickup-area-modal__arrow-right icon--arrow-right" />
      </div>
      <div
        class="select-pickup-area-modal__option hover-bg"
        @click="handleSelectNewPickupAddress"
      >
        <i class="icon icon--compass" />
        <span class="select-pickup-area-modal__option-label">
          {{ $t("selectNewAddress") }}
        </span>
        <i class="select-pickup-area-modal__arrow-right icon--arrow-right" />
      </div>
      <SavedAddressList
        :customText="$t('pickupNear')"
        :addressList="payload.addressesToRender"
        :onSelectOldAddress="oldAddress => selectOldAddress(oldAddress)"
        :forwardGeocodeLoadingKey="LOADING_KEY_PICKUP_ADDRESS_FORWARD_GEOCODING"
        :beforeOpenLoginModal="hideSelectPickupAreaModal"
        :isRequireLogin="true"
      />
    </LoadingWrapper>
  </BaseModal>
</template>
<i18n>
  {
    "is": {
      "selectNewAddress": "Velja nýtt heimilisfang",
      "selectLastAddress": "Síðasta heimilisfang",
      "selectPickupArea": "Veldu svæði til að sækja á"
    },
    "en": {
      "selectNewAddress": "Select new address",
      "selectLastAddress": "Select last address",
      "selectPickupArea": "Select pickup area"
    }
  }
</i18n>
<script>
import BaseModal from "@/components/BaseModal";
import LoadingWrapper from "@/components/LoadingWrapper";
import SavedAddressList from "@/components/SavedAddressList";
import { mapGetters, mapActions } from "vuex";
import { MODAL_KEY_SELECT_PICKUP_AREA } from "@/store/modalModule";
import { FULFILLMENT_METHOD } from "@/util/constants";
import { LOADING_KEY_PICKUP_ADDRESS_FORWARD_GEOCODING } from "@/store/loadingModule";

export default {
  name: "SelectPickupAreaModal",
  components: {
    BaseModal,
    SavedAddressList,
    LoadingWrapper,
  },
  data() {
    return {
      LOADING_KEY_PICKUP_ADDRESS_FORWARD_GEOCODING,
    };
  },
  computed: {
    ...mapGetters(["getModalState", "userAddressString"]),
    payload() {
      return this.getModalState(MODAL_KEY_SELECT_PICKUP_AREA);
    },
  },
  methods: {
    ...mapActions(["toggleModal", "handleClickFulfillmentMethod"]),
    hideSelectPickupAreaModal() {
      this.toggleModal({
        modalKey: MODAL_KEY_SELECT_PICKUP_AREA,
        isVisible: false,
      });
    },
    handleClickSelectLastAddress() {
      this.hideSelectPickupAreaModal();
      this.handleClickFulfillmentMethod({ method: FULFILLMENT_METHOD.PICKUP });
    },
    handleSelectNewPickupAddress() {
      this.payload.toggleDeliveryAddressModal({
        fulfillmentMethod: FULFILLMENT_METHOD.PICKUP,
      });
      this.hideSelectPickupAreaModal();
    },
    selectOldAddress(address) {
      this.payload.selectOldAddress(address);
      this.handleClickFulfillmentMethod({ method: FULFILLMENT_METHOD.PICKUP });
      this.hideSelectPickupAreaModal();
    },
  },
};
</script>

<style lang="scss" scoped>
.select-pickup-area-modal__option-label {
  margin-left: 10px;
}
.select-pickup-area-modal__option {
  display: flex;
  align-items: center;
  padding: 15px;
  border-bottom: 1px solid #dadada;
}
.select-pickup-area-modal__arrow-right {
  margin-left: auto;
  padding: 0 10px;
  font-size: 10px;
}
</style>
