<template>
  <BaseModal
    class="delivery-option-modal modal--no-padding modal--header-bar-as-mobile "
    :isVisible="payload.isVisible"
    centeredTitle
    noFooter
    @hide="hideSelectDeliveryOptionModal"
  >
    <template #header>{{ $t("selectDeliveryOption") }}</template>
    <LoadingWrapper
      :loadingKeys="[LOADING_KEY_DELIVERY_ADDRESS_FORWARD_GEOCODING]"
    >
      <div
        class="delivery-option-modal__option hover-bg"
        @click="toggleDeliveryAddressModal"
      >
        <i class="icon icon--shipping" />
        <span class="delivery-option-modal__option-label">{{
          $t("deliveryMethod")
        }}</span>
        <i class="delivery-option-modal__arrow-right icon--arrow-right" />
      </div>
      <div
        class="delivery-option-modal__option hover-bg"
        @click="clickPickupMethod"
        v-if="showPickupMethod"
      >
        <i class="icon icon--shopping-basket" />
        <span
          class="delivery-option-modal__option-label"
          v-html="payload.showPickupTextByStore || $t('pickupMethod')"
        ></span>
        <i class="delivery-option-modal__arrow-right icon--arrow-right" />
      </div>
      <SavedAddressList
        :addressList="addressesToRender"
        :onSelectOldAddress="oldAddress => selectOldAddress(oldAddress)"
        :forwardGeocodeLoadingKey="
          LOADING_KEY_DELIVERY_ADDRESS_FORWARD_GEOCODING
        "
        :beforeOpenLoginModal="hideSelectDeliveryOptionModal"
        :isRequireLogin="true"
      />
    </LoadingWrapper>
  </BaseModal>
</template>
<i18n>
  {
    "is": {
      "selectDeliveryOption": "Veldu afhendingarmáta"
    },
    "en": {
      "selectDeliveryOption": "Select delivery option"
    }
  }
</i18n>
<script>
import BaseModal from "@/components/BaseModal";
import { mapGetters, mapActions } from "vuex";
import {
  MODAL_KEY_SELECT_DELIVERY_OPTION,
  MODAL_KEY_DELIVERY_MODAL_V3,
  MODAL_KEY_SELECT_PICKUP_AREA,
} from "@/store/modalModule";
import { LOADING_KEY_DELIVERY_ADDRESS_FORWARD_GEOCODING } from "@/store/loadingModule";
import { FULFILLMENT_METHOD } from "@/util/constants";
import { SELLER_TYPES } from "@/util/constants";
import redirectToSellerList from "@/util/redirectToSellerList";
import SavedAddressList from "@/components/SavedAddressList";
import LoadingWrapper from "@/components/LoadingWrapper";

export default {
  name: "SelectDeliveryOptionModal",
  components: {
    BaseModal,
    SavedAddressList,
    LoadingWrapper,
  },
  data() {
    return {
      LOADING_KEY_DELIVERY_ADDRESS_FORWARD_GEOCODING,
    };
  },
  computed: {
    ...mapGetters([
      "getModalState",
      "savedAddresses",
      "getNettoIdByPostcode",
      "isInGroceriesScreen",
      "getNettoIdByURL",
    ]),
    payload() {
      return this.getModalState(MODAL_KEY_SELECT_DELIVERY_OPTION);
    },
    showPickupMethod() {
      const { sellerType, supportPickupShipment = true } = this.payload;

      if (sellerType === SELLER_TYPES.GROCERY) {
        return true;
      }
      return supportPickupShipment;
    },
    addressesToRender() {
      const { availablePostCodes } = this.payload;
      if (availablePostCodes && availablePostCodes.length) {
        if (availablePostCodes.includes("*")) {
          return this.savedAddresses;
        }

        let addressess = JSON.parse(JSON.stringify(this.savedAddresses)).sort(
          address => (availablePostCodes.includes(address.postcode) ? -1 : 0)
        );
        addressess = addressess.map(address => {
          return {
            ...address,
            isInvalid: !availablePostCodes.includes(address.postcode),
          };
        });
        return addressess;
      }
      return this.savedAddresses;
    },
  },
  methods: {
    ...mapActions([
      "toggleModal",
      "handleClickFulfillmentMethod",
      "setUserAddress",
      "getStoreIdURLMap",
      "setFulfillmentMethod",
    ]),
    hideSelectDeliveryOptionModal() {
      return this.toggleModal({
        modalKey: MODAL_KEY_SELECT_DELIVERY_OPTION,
        isVisible: false,
      });
    },
    toggleDeliveryAddressModal({
      fulfillmentMethod = FULFILLMENT_METHOD.DELIVERY,
    } = {}) {
      const {
        toggleModal,
        hideSelectDeliveryOptionModal,
        setFulfillmentMethod,
      } = this;
      const { isVisible, ...restPayload } = this.payload;
      hideSelectDeliveryOptionModal();
      return toggleModal({
        modalKey: MODAL_KEY_DELIVERY_MODAL_V3,
        ...restPayload,
        setAddressHook: () => {
          setFulfillmentMethod(fulfillmentMethod);
        },
      });
    },
    toggleSelectPickupAreaModal() {
      const {
        selectOldAddress,
        addressesToRender,
        toggleModal,
        toggleDeliveryAddressModal,
      } = this;
      return toggleModal({
        modalKey: MODAL_KEY_SELECT_PICKUP_AREA,
        toggleDeliveryAddressModal,
        addressesToRender,
        selectOldAddress,
      });
    },
    clickPickupMethod() {
      const {
        handleClickFulfillmentMethod,
        hideSelectDeliveryOptionModal,
        toggleSelectPickupAreaModal,
      } = this;
      const { sellerType, viewName } = this.payload;
      hideSelectDeliveryOptionModal();
      switch (viewName) {
        case "ViewGroceries":
        case "ViewProducts":
        case "ViewRestaurant": {
          handleClickFulfillmentMethod({
            method: FULFILLMENT_METHOD.PICKUP,
            sellerType,
          });
          return;
        }
      }
      toggleSelectPickupAreaModal();
    },
    async selectOldAddress(oldAddress) {
      this.handleClickFulfillmentMethod({
        method: FULFILLMENT_METHOD.DELIVERY,
      });
      const {
        street,
        postcode,
        city,
        lat,
        lng: lon,
        firstname,
        lastname,
      } = oldAddress;

      this.setUserAddress({
        street: street,
        city: city,
        lat,
        lon,
        postcode: +postcode,
        firstname,
        lastname,
      });

      const { sellerType } = this.payload;

      // if (this.isInGroceriesScreen) {
      if (sellerType === SELLER_TYPES.GROCERY) {
        const currStoreUrl = this.$route.params.storeUrl;
        await this.getStoreIdURLMap();
        const currStoreId = this.getNettoIdByURL(currStoreUrl);
        const newStoreId = this.getNettoIdByPostcode(postcode);

        currStoreId !== newStoreId &&
          redirectToSellerList({ sellerType, groceryStoreId: newStoreId });
      }
      this.hideSelectDeliveryOptionModal();
    },
  },
};
</script>

<style lang="scss" scoped>
.delivery-option-modal__arrow-right {
  margin-left: auto;
  padding: 0 10px;
  font-size: 10px;
}

.delivery-option-modal__option {
  display: flex;
  align-items: center;
  padding: 15px;
  border-bottom: 1px solid #dadada;
  p {
    margin-bottom: 0;
  }
}

.delivery-option-modal__option-label {
  margin-left: 10px;
}

@media screen and (min-width: 768px) {
  .delivery-option-modal__save-addresses {
    height: 255px;
  }
}
</style>
