<template>
  <!-- Top half of the nav, controls the search bar and indicates when the search is limited to the current store -->
  <div :class="['header__top', isInHomePage && 'header__top--in-home-page']">
    <div class="container">
      <div class="row">
        <div class="header__logo hidden-xs">
          <router-link to="/">
            <i class="header__logo-icon icon--aha-logo" />
          </router-link>
        </div>
        <div class="header__search-form header__search-form--desktop">
          <div class="header__search-form__content">
            <ais-search-box
              :placeholder="$t(placeholder, { routeName })"
              :class-names="{
                'ais-SearchBox-reset': 'header__search-form__reset',
                'ais-SearchBox-submit': 'header__search-form__button',
                'ais-SearchBox-input': 'header__search-form__input',
              }"
              @click.native="startSearching"
              @keydown.native="startSearching"
              v-model="query"
            >
              <div slot="submit-icon">
                <i class="header__search-icon icon--search" />
              </div>
              <div
                slot="reset-icon"
                class="header__reset-icon"
                @click="stopSearching"
              >
                <i class="icon icon--close-icon" />
              </div>
            </ais-search-box>
            <AisVoiceSearchWidget
              search-as-you-speak
              v-if="isSupportVoiceSearch"
              :class-names="{
                'ais-VoiceSearch-status':
                  'header__search-form__voice-search-status',
                'ais-VoiceSearch-button':
                  'header__search-form__voice-search-button',
              }"
            >
              <template slot="status" slot-scope="{ status, transcript }">
                <p class="header__search-form__voice-search-status-bar">
                  <!-- Searching for {{ transcript }} -->
                  {{ handleVoiceSearch(status, transcript) }}
                </p>
              </template>
            </AisVoiceSearchWidget>
          </div>
          <Select
            class="header__filter-option"
            v-if="storeData.storeId"
            :options="localizedFilterOptions"
            :selectedOption="selectedFilterOption"
            :onSelectOption="setSelectedFilterOption"
          />
        </div>
        <div class="header__search-form header__search-form--mobile">
          <div class="header-navigation" v-if="isFetching">
            <div class="header-navigation__icon-back icon--arrow-left" />
            <div class="header-navigation__title app-ph" />
            <div class="header-navigation__icon-search icon--search" />
          </div>
          <div class="header-navigation" v-else>
            <i
              class="header-navigation__icon-back icon--arrow-left"
              @click="handleBack"
            />
            <h4
              class="header-navigation__title ellipsis"
              v-if="!isSearchBarVisible"
            >
              {{ navBarTitle }}
            </h4>
            <ais-search-box
              :placeholder="$t(placeholder, { routeName })"
              :class-names="{
                'ais-SearchBox-reset': 'header__search-form__reset',
                'ais-SearchBox-submit': 'header__search-form__button',
                'ais-SearchBox-input': 'header__search-form__input',
              }"
              @click.native="startSearching"
              @keydown.native="startSearching"
              v-model="query"
              v-show="isSearchBarVisible"
            >
              <div slot="submit-icon">
                <i class="header__search-icon icon--search" />
              </div>
              <div
                slot="reset-icon"
                class="header__reset-icon"
                @click="stopSearching"
              >
                <i class="icon icon--close-icon" />
              </div>
            </ais-search-box>
            <AisVoiceSearchWidget
              v-if="isSupportVoiceSearch && isSearchBarVisible"
              search-as-you-speak
              :class-names="{
                'ais-VoiceSearch-status':
                  'header__search-form__voice-search-status',
                'ais-VoiceSearch-button':
                  'header__search-form__voice-search-button',
              }"
            >
              <template slot="status" slot-scope="{ status, transcript }">
                <p class="header__search-form__voice-search-status-bar">
                  <!-- Searching for {{ transcript }} -->
                  {{ handleVoiceSearch(status, transcript) }}
                </p>
              </template>
            </AisVoiceSearchWidget>
            <i
              class="header-navigation__icon-search icon--search"
              @click="handleToggleSearch"
            />
          </div>
        </div>
        <div class="header__right hidden-xs">
          <div class="header__login" @click="profileAction()">
            <i class="header__login-icon icon icon--user-line" />
            <small
              class="header__logged-in-text"
              v-if="$store.state.customer.is_logged_in"
              >{{ $t("myAccount") }}</small
            >
            <small v-else>{{ $t("login") }}</small>
          </div>
          <div
            class="header__cart"
            @click="toggleCart()"
            style="position: relative;"
          >
            <div
              :class="[itemsInCart && 'fa-stack', 'fa-2x']"
              :data-count="itemsInCart"
            >
              <i class="header__cart-icon icon icon--cart" />
            </div>
            <small>{{ $t("cart") }}</small>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<i18n>
  {
    "is": {
      "noResultsFor": "Engar niðurstöður fundust fyrir",
      "searchIn": "Leita í {routeName}",
      "searchPlaceholder": "Leitaðu að því sem þig vantar...",
      "myAccount": "Mínar síður",
      "cart": "Karfa",
      "login": "Skrá inn",
      "allSellers": "Allir seljendur"
    },
    "en": {
      "noResultsFor": "No results found for",
      "searchIn": "Search in {routeName}",
      "searchPlaceholder": "Search for anything...",
      "myAccount": "My Account",
      "cart": "Cart",
      "login": "Log in",
      "allSellers": "All sellers"
    }
  }
</i18n>

<script>
import { mapState, mapActions, mapGetters } from "vuex";
import removeShipmentItems from "@/util/removeShipmentItems";
import Select from "@/components/Select";
import AisVoiceSearchWidget from "@/components/AisVoiceSearchWidget";
import get from "@/util/get";
import { LOADING_KEY_FETCHING_DATA } from "@/store/loadingModule";

export default {
  name: "TheHeaderTop",
  components: {
    Select,
    AisVoiceSearchWidget,
  },
  props: {
    storeData: Object,
  },
  data() {
    return {
      query: "",
      isSupportVoiceSearch: false,
    };
  },
  computed: {
    ...mapState(["clearSearch"]),
    ...mapGetters([
      "filterOptions",
      "selectedFilterOption",
      "isSearchBarVisible",
      "getNavBarTitle",
      "getLoadingState",
      "getStoreNameByUrl",
    ]),
    navBarTitle() {
      return this.getNavBarTitle(this.$i18n.locale);
    },
    isInHomePage() {
      return get(this.$route, "name") === "ViewHome";
    },
    isFetching() {
      return this.getLoadingState(LOADING_KEY_FETCHING_DATA);
    },
    localizedFilterOptions() {
      return this.filterOptions.map(({ label, value }) => ({
        label: label === "*" ? this.$t("allSellers") : label,
        value,
      }));
    },
    routeName() {
      const storeUrl = this.$route.params.path || this.$route.params.storeUrl;
      const storeName = this.getStoreNameByUrl(storeUrl);
      return storeName;
    },
    options() {
      return [
        { label: "Aha.is", value: false },
        this.routeName && { label: this.routeName, value: true },
      ].filter(Boolean);
    },
    placeholder() {
      return this.isFiltered ? "searchIn" : "searchPlaceholder";
    },
    itemsInCart() {
      return removeShipmentItems(this.$store.state.cartItems).reduce(
        (sum, { cart_qty }) => sum + cart_qty,
        0
      );
    },
    isFiltered() {
      return !!this.routeName;
    },
  },
  methods: {
    ...mapActions([
      "setClearSearch",
      "setSelectedFilterOption",
      "toggleSearchBar",
    ]),
    searchBox() {
      return document.querySelector(
        ".header__search-form--mobile .header__search-form__input"
      );
    },
    startSearching() {
      this.$store.dispatch("startSearching");
    },
    stopSearching() {
      this.query = "";
      this.$store.dispatch("stopSearching");
    },
    toggleCart() {
      this.$store.dispatch("toggleCart");
    },
    profileAction() {
      if (this.$store.state.customer.is_logged_in) {
        this.$router.push("/customer/account");
      } else {
        this.$store.dispatch("toggleLogin");
      }
    },
    handleBack() {
      if (this.isInHomePage) {
        if (this.query) {
          this.stopSearching();
          return;
        }
        return this.$router.back();
      }
      if (this.isSearchBarVisible) {
        this.stopSearching();
        this.handleToggleSearch();
        return;
      }
      this.$router.back();
    },
    handleToggleSearch() {
      if (this.isInHomePage) {
        this.searchBox() && this.searchBox().focus();
        return;
      }
      this.toggleSearchBar(!this.isSearchBarVisible);
      if (!this.isSearchBarVisible) {
        this.stopSearching();
      }
    },
    handleVoiceSearch(status, transcript) {
      if (transcript && status !== "finished") {
        this.query = transcript;
        this.startSearching();
      }
      return "";
    },
  },
  created() {
    const SpeechRecognition =
      window.SpeechRecognition || window.webkitSpeechRecognition;
    if (!SpeechRecognition) {
      this.isSupportVoiceSearch = false;
    } else {
      this.isSupportVoiceSearch = true;
    }
  },
  updated() {
    if (this.isSearchBarVisible && !this.isInHomePage) {
      this.$nextTick(() => {
        this.searchBox() && this.searchBox().focus();
      });
    }
  },
  mounted() {
    const { isInHomePage, toggleSearchBar } = this;
    toggleSearchBar(isInHomePage);
  },
  watch: {
    isInHomePage(value) {
      this.toggleSearchBar(value);
    },
    $route(to, from) {
      to.fullPath !== from.fullPath && this.stopSearching();
    },
    clearSearch() {
      const { clearSearch, setClearSearch } = this;
      if (clearSearch) {
        this.query = "";
        setClearSearch();
      }
    },
  },
};
</script>

