import store from "@/store/store";

export default async function execWithLoading(loadingKey, executeFunc) {
  store.dispatch("toggleLoading", {
    loadingKey,
    isLoading: true,
  });
  try {
    const result = await executeFunc();
    return result;
  } catch (error) {
    throw error;
  } finally {
    store.dispatch("toggleLoading", {
      loadingKey,
      isLoading: false,
    });
  }
}
