import get from "@/util/get";
import Vue from "vue";

export const searchFilterModule = {
  state: {
    filterOptions: [],
    selectedFilterOption: undefined,
    isSearchBarVisible: false,
    navBarTitle: {
      en: '',
      is: '',
    },
  },
  mutations: {
    setFilterOptions(state, filterOptions) {
      Vue.set(state, "filterOptions", filterOptions);
    },
    setSelectedFilterOption(state, selectedFilterOption) {
      Vue.set(state, "selectedFilterOption", selectedFilterOption);
    },
    toggleSearchBar(state, isSearchBarVisible) {
      Vue.set(state, "isSearchBarVisible", isSearchBarVisible);
    },
    setNavBarTitle(state, {en, is}) {
      Vue.set(state, "navBarTitle", {en, is});
    },
  },
  actions: {
    setFilterOptions({ commit }, filterOptions) {
      commit("setFilterOptions", filterOptions);
      commit("setSelectedFilterOption", get(filterOptions, "0"));
    },
    setSelectedFilterOption({ commit }, selectedFilterOption) {
      commit("setSelectedFilterOption", selectedFilterOption);
    },
    toggleSearchBar({ commit }, isSearchBarVisible) {
      commit("toggleSearchBar", isSearchBarVisible);
    },
    setNavBarTitle({ commit }, {en, is}) {
      commit("setNavBarTitle", {en, is});
    },
  },
  getters: {
    filterOptions: state => {
      return get(state, `filterOptions`, []);
    },
    selectedFilterOption: state => {
      return get(state, `selectedFilterOption`);
    },
    isSearchBarVisible: state => {
      return get(state, `isSearchBarVisible`, false);
    },
    getNavBarTitle: state => (lang = 'is') => {
      const currLangText = get(state, `navBarTitle.${lang}`, '')
      const restLangText = get(state, `navBarTitle.${lang === 'is' ? 'en' : 'is' }`, '')
      return currLangText || restLangText;
    }
  },
};
