<template>
  <BaseModal
    :isVisible="payload.isVisible"
    :centeredTitle="true"
    :noFooter="true"
    @hide="hideChangePickupModal"
  >
    <PickupListSelect
      :selectPickupPoint="selectPickupPoint"
      :selectedNettoId="currentNettoStoreIdByMethod"
    />
  </BaseModal>
</template>

<script>
import PickupListSelect from "@/components/PickupListSelect";
import BaseModal from "@/components/BaseModal";
import { mapGetters, mapActions } from "vuex";
import { MODAL_KEY_CHANGE_DRAFT_PICKUP_POINT } from "@/store/modalModule";
import redirectToSellerList from "@/util/redirectToSellerList";
import { SELLER_TYPES } from "@/util/constants";
import { FULFILLMENT_METHOD } from "@/util/constants";
import { MODAL_KEY_CONFIRM_CLEAR_CART } from "@/store/modalModule";
import cartItemsByStore from "@/mixins/cartItemsByStore";
import clearStoreFromCart from "@/mixins/clearStoreFromCart";

export default {
  name: "ChangePickupModal",
  mixins: [cartItemsByStore, clearStoreFromCart],
  components: {
    PickupListSelect,
    BaseModal,
  },
  computed: {
    ...mapGetters(["getModalState", "currentNettoStoreIdByMethod"]),
    payload() {
      return this.getModalState(MODAL_KEY_CHANGE_DRAFT_PICKUP_POINT);
    },
  },
  methods: {
    ...mapActions([
      "hideModal",
      "setSelectedNettoAddress",
      "setFulfillmentMethod",
      "toggleModal",
    ]),
    hideChangePickupModal() {
      this.hideModal(MODAL_KEY_CHANGE_DRAFT_PICKUP_POINT);
    },
    hasItemsInCart(storeId) {
      return this.cartItemsByStore.some(({ id }) => id === +storeId);
    },
    selectPickupPoint(point) {
      const {
        toggleModal,
        confirmCallback,
        hasItemsInCart,
        clearStoreFromCart,
        currentNettoStoreIdByMethod,
      } = this;
      const newDeliveryData = {
        lat: point.latitude,
        lon: point.longitude,
        postcode: null,
        street: point.name,
        id: point.id,
      };

      if (
        currentNettoStoreIdByMethod &&
        hasItemsInCart(currentNettoStoreIdByMethod) &&
        currentNettoStoreIdByMethod !== newDeliveryData.id
      ) {
        return toggleModal({
          modalKey: MODAL_KEY_CONFIRM_CLEAR_CART,
          isVisible: true,
          callback: () => {
            clearStoreFromCart(currentNettoStoreIdByMethod);
            confirmCallback(newDeliveryData);
          },
        });
      }

      confirmCallback(newDeliveryData);
    },
    async confirmCallback(newDeliveryData) {
      const {
        setSelectedNettoAddress,
        hideChangePickupModal,
        setFulfillmentMethod,
        hideModal,
        payload,
      } = this;
      setSelectedNettoAddress(newDeliveryData);
      setFulfillmentMethod(FULFILLMENT_METHOD.PICKUP);
      hideChangePickupModal();
      hideModal(MODAL_KEY_CONFIRM_CLEAR_CART);
      if (payload.allowToRedirect === false) {
        return;
      }
      await redirectToSellerList({
        sellerType: SELLER_TYPES.GROCERY,
        groceryStoreId: newDeliveryData.id,
      });
    },
  },
};
</script>

