import Vue from "vue";
import App from "./App.vue";
import InstantSearch from "vue-instantsearch";
import VueGtm from "vue-gtm";
import qs from "qs";

import VueRouter from "vue-router";
import routes, { afterEach } from "./router/router";
import store from "./store/store.js";
import i18n from "./i18n";
import "@/assets/global.css";
import vueErrorHandler from "@/util/vueErrorHandler";
import { ROUTER_BASE } from "@/util/constants";
import { bindGlobalCommandShortcut } from "@/util/reactNative";
import titleMixin from "./mixins/titleMixin";
import VueObserveVisibility from "vue-observe-visibility";

Vue.use(VueObserveVisibility);
Vue.use(VueRouter);
Vue.use(InstantSearch);
Vue.config.productionTip = false;
Vue.config.lang = "is";
Vue.mixin(titleMixin);
Vue.config.errorHandler = vueErrorHandler;
bindGlobalCommandShortcut();
store.dispatch("fetchServerTime");

// alway use the server time when using new Date()
// eslint-disable-next-line
Date = class extends Date {
  constructor(options) {
    if (options) {
      super(options);
    } else {
      super(Date.now() + store.getters.serverTimeDiff);
    }
  }
};

export const router = new VueRouter({
  routes,
  // Avoid unwanted saving of scroll state
  scrollBehavior(to, from, savedPosition) {
    const position = {};
    if (to.hash) {
      position.selector = to.hash;
      if (to.name === "ViewAllCategories") {
        const header = document.querySelector(".header");
        const categoriesHeader = document.querySelector(
          "#all-categories .categories__header"
        );
        const spaceTop = header.clientHeight + categoriesHeader.clientHeight;
        position.offset = { x: 0, y: spaceTop + 25 };
      }
      // bypass #1number check
      if (/^#\d/.test(to.hash) || document.querySelector(to.hash)) {
        return position;
      }
    }
    return savedPosition || { x: 0, y: 0 };
  },
  mode: "history",
  base: ROUTER_BASE,
  // Custom query resolver to integrate with instantsearch routing/state
  parseQuery(query) {
    return qs.parse(query);
  },
  stringifyQuery(query) {
    var result = qs.stringify(query);

    return result ? "?" + result : "";
  },
});
router.afterEach(afterEach);
router.beforeEach((to, from, next) => {
  store.dispatch("resetModalState");
  next();
});

// Google tag manager
Vue.use(VueGtm, {
  id: "GTM-TRTN948",
  queryParams: {
    gtm_auth: "tbMXgXnT0rjh8cd9xoZjRQ",
    env_id: 5,
  },
  enabled: true,
  debug: true,
  vueRouter: router,
});

new Vue({
  router,
  i18n,
  store,
  render: h => h(App),
}).$mount("#app");
