import axios from "axios";
import get from "@/util/get";
import store from "@/store/store";
import getClientIPInstance from "@/util/getClientIPInstance";
import API from "@/services/API";

export async function writeClientLog({ error, level = "error" }) {
  // we must use normal axios instance instead of API instance to prevent error loop
  // (if there is any err, API instance will catch the error and trigger writeClientLog again)
  try {
    const gdpr_approval = null;
    const user_agent = navigator.userAgent;
    const url = window.location.href;
    const customer_id = get(store, "state.customer.id");
    const ip_address = await getClientIPInstance();

    const { data } = await axios.post(
      "/ws.php",
      new URLSearchParams({
        service: "client_log",
        level,
        url,
        error,
        gdpr_approval,
        ip_address,
        user_agent,
        customer_id,
      })
    );

    return data;
  } catch (error) {
    // if err, do not throw more err, it's will create infinite loop
    console.log("writeClientLog failed", error); // eslint-disable-line no-console
  }
}

export async function getStoresIdURLMap() {
  const response = await API.post(
    "/ws.php",
    new URLSearchParams({
      service: "get_stores",
    })
  );

  const result = Object.values(get(response, "data.0", {})).reduce(
    (acc, curr) => {
      if (!curr) return acc;

      acc[curr.id] = curr;
      return acc;
    },
    {}
  );

  return result;
}

export async function getStoreShipmentsById(storeId) {
  if (!storeId) return;

  const response = await API.post(
    "/ws.php",
    new URLSearchParams({
      service: "shipments",
      id: storeId,
    })
  );

  return get(response, "data", {});
}

export async function getCategories() {
  const response = await API.post(
    "/ws.php",
    new URLSearchParams({
      service: "menu_categories",
    })
  );

  const formattedRes = Object.values(get(response, "data.0", {}));

  const groupedByParentId = formattedRes.reduce((result, curr) => {
    const { parent_id } = curr;
    result = {
      ...result,
      [parent_id]: [...(result[parent_id] || []), curr],
    };
    return result;
  }, {});

  const rootCat = get(formattedRes, "0");
  if (!rootCat) return;

  const urlPathToCategoryMapping = formattedRes.reduce((result, curr) => {
    const { url_path } = curr;
    result = {
      ...result,
      [url_path]: curr,
    };
    return result;
  }, {});

  function getChildrenCat(baseCat) {
    const { category_id } = baseCat;
    const rawChildren = groupedByParentId[category_id] || [];

    const catWithChildData = {
      ...baseCat,
      children: rawChildren.map(childCat => getChildrenCat(childCat)),
    };

    return catWithChildData;
  }
  const catWithChildren = getChildrenCat(rootCat);

  return {
    catWithChildren,
    urlPathToCategoryMapping,
  };
}

export async function getStorePostcodeMap() {
  const response = await API.post(
    "/ws.php",
    new URLSearchParams({
      service: "store_postcode_map",
      merchant_id: 181,
    })
  );

  const storePostCodeIdMap = Object.values(get(response, "data.0", {})).reduce(
    (acc, { postcode, store_id }) => {
      acc[postcode] = store_id;
      return acc;
    },
    {}
  );
  return storePostCodeIdMap;
}

export async function getServerTime() {
  const response = await API.post(
    "/ws.php",
    new URLSearchParams({
      service: "servertime",
      nocache: 1,
    })
  );

  return new Date(get(response, "data.0.0.now"));
}

export async function reverseGeoCode({ latitude, longitude }) {
  const response = await API.post(
    "/ws.php",
    new URLSearchParams({
      service: "reverse_geocode",
      latitude,
      longitude,
    })
  );

  return get(response, "data.0.0");
}

// address should be street + house number
// ex: Brattagata 4B
export async function forwardGeoCode({ address, postcode }) {
  const response = await API.post(
    "/ws.php",
    new URLSearchParams({
      service: "geocode",
      address,
      postcode,
    })
  );
  //Incorrect Parameters
  if (typeof response.data === "string") throw response.data;

  return get(response, "data.0.0");
}
