<template>
  <div class="hamb-categories">
    <h4>{{ $t("category") }}</h4>
    <div class="hamb-categories__categories-container hide-scroll-bar">
      <CategoryItem
        :imgURL="require('@/assets/svg/grid-icon.svg')"
        :name="$t('viewAll')"
        :onClickItem="viewAllCategory"
      />
      <CategoryItem
        v-for="category in lv1CatList"
        :key="category.category_id"
        :onClickItem="() => openChildCat(category)"
        :imgURL="tempCatURLToImgMap[category.url_path]"
        :name="category.name"
      />
    </div>
  </div>
</template>
<i18n>
  {
    "is": {
      "category" : "Flokkar",
      "viewAll" : "Sjá allt"
    },
    "en": {
      "category" : "Categories",
      "viewAll": "View all"
    }
  }
</i18n>
<script>
import { mapActions, mapGetters } from "vuex";
import get from "@/util/get";
import CategoryItem from "@/components/CategoryItem";

const tempCatURLToImgMap = {
  "flokkar/matvara": require("@/assets/images/category-icons/Food.svg"),
  "flokkar/ibrottir-og-utivist": require("@/assets/images/category-icons/Sports.svg"),
  "flokkar/ur-skartgripir-og-fylgihlutir": require("@/assets/images/category-icons/Jewelry.svg"),
  "flokkar/raftaki-og-tolvur": require("@/assets/images/category-icons/iPhone.svg"),
  "flokkar/heimilio-og-gjafavara": require("@/assets/images/category-icons/Gift.svg"),
  "flokkar/fot-og-skor": require("@/assets/images/category-icons/Clothes.svg"),
  "flokkar/bakur-ritfong-og-afbreying": require("@/assets/images/category-icons/Book.svg"),
  "flokkar/heilsa-og-utlit": require("@/assets/images/category-icons/Health.svg"),
};
export default {
  name: "MobileHamburgerCategories",
  components: {
    CategoryItem,
  },
  props: {
    closeMenu: Function,
  },
  data() {
    return {
      tempCatURLToImgMap,
    };
  },
  computed: {
    ...mapGetters(["categoriesList"]),
    lv1CatList() {
      return get(this.categoriesList, "children", []);
    },
  },
  methods: {
    ...mapActions(["pushModalStack", "clearModalStack"]),
    viewAllCategory() {
      const { closeMenu } = this;
      closeMenu();
      this.$router.push("/all-categories");
    },
    openChildCat(category) {
      const { pushModalStack, clearModalStack, closeMenu } = this;
      closeMenu();
      if (category.level < 3) {
        pushModalStack({
          componentName: "MobileCategories",
          stackKey: `MobileCategories__${category.category_id}`,
          ...category,
        });
      } else {
        clearModalStack();
        this.$router.push(`/${category.url_path}`);
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.hamb-categories__categories-container {
  display: flex;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
</style>
