// store.js
/* global FB */

import Vue from "vue";
import Vuex from "vuex";
import VuexPersistence from "vuex-persist";

import i18n from "@/i18n";
import axios from "axios";

import { router } from "@/main";

import { getClientIP } from "@/services/getClientIP";
import { getPickupLocations } from "@/services/getPickupLocations";
import {
  getStoresIdURLMap,
  getCategories,
  getStorePostcodeMap,
  writeClientLog,
  getServerTime,
} from "@/services/ws";
import {
  loginAccount,
  register,
  forgotPassword,
  enterForgotPassword,
} from "@/services/customer";

import { deliveryModule } from "@/store/deliveryModule";
import { loadingModule } from "@/store/loadingModule";
import { modalModule, MODAL_KEY_PRODUCT_OPTION } from "@/store/modalModule";
import { errorsModule } from "@/store/errorsModule";
import { searchFilterModule } from "@/store/searchFilterModule";
import { DATA_VERSION } from "@/util/constants";
import { postMessageToApp, isAppWebView } from "@/util/reactNative";
import { getTimestamp } from "@/util/openingHours";

import get from "@/util/get";

const vuexLocal = new VuexPersistence({
  reducer: state => {
    const { delivery, customer, printData, __dataVersion } = state;

    return {
      delivery: {
        fulfillmentMethod: delivery.fulfillmentMethod,
        selectedNettoAddress: delivery.selectedNettoAddress,
        userAddress: delivery.userAddress,
      },
      customer,
      printData,
      __dataVersion,
    };
  },
  storage: window.localStorage,
});

Vue.use(Vuex);

const afterLogin = ({ state, dispatch }) => {
  const { showLogin, redirectPath } = state;
  dispatch("notification", {
    type: "success",
    message: i18n.t("loginSuccessful"),
  });
  // Close login modal
  showLogin && dispatch("toggleLogin");
  // If redirect path after login
  redirectPath && router.push(redirectPath);
};

const initialState = {
  __dataVersion: DATA_VERSION,
  cartItems: [],
  quote: {},
  address: {},
  cartActions: 0,
  loading: false,
  showCart: false,
  isSearching: false,
  customer: {
    email: undefined,
    firstname: undefined,
    lastname: undefined,
    nationid: undefined,
    group: undefined,
    id: undefined,
    is_logged_in: undefined,
    fb: false,
    customer_credit: undefined,
  },
  showLogin: false,
  pickupLocations: [],
  storeIdMap: null, // make sure call getStoreIdURLMap action first
  storeURLMap: null, // make sure call getStoreIdURLMap action first
  storePostcodeIdMap: null, // make sure call fetchStorePostcodeIdMap action first
  notification: {},
  clientIP: undefined,
  isInGroceriesScreen: false,
  currentStoreId: undefined,
  categoriesList: {},
  redirectPath: undefined,
  printData: {},
  counts: null,
  serverTime: new Date(),
  serverTimeDiff: 0,
  putCheckoutKey: null,
};

const store = new Vuex.Store({
  state: Vuex.Store.state || initialState,
  actions: {
    updatePutCheckoutKey({ commit }) {
      commit("updatePutCheckoutKey");
    },
    setCounts({ commit }, counting) {
      commit("setCounts", counting);
    },
    getSession({ state, commit, dispatch }) {
      axios.get("/api/rest/customer").then(({ data: { customer_info } }) => {
        const isLoggedIn = !Array.isArray(customer_info);
        const justLoggedIn =
          state.customer.is_logged_in === false && isLoggedIn;
        commit("setSessionDB", {
          ...customer_info,
          isLoggedIn,
        });
        justLoggedIn &&
          afterLogin({
            state,
            dispatch,
          });
      });
    },
    async loginFB({ state, commit, dispatch }, data) {
      const mobileAppUrl = "/customer/account/mobileApp";
      const {
        accessToken,
        first_name: firstname,
        last_name: lastname,
        email: facebookEmail,
      } = data;
      const {
        data: { customer_id: id, customer_email: email = facebookEmail },
      } = await axios.post(
        mobileAppUrl,
        {},
        {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
          },
          params: {
            actionType: "retrieve_account",
            loginType: "facebook",
            fbaccesstoken: accessToken,
          },
        }
      );
      if (!id) {
        // First time facebook login
        const password = Math.random()
          .toString(36)
          .substr(2, 8);
        const {
          data: { customer_id, customer_email, success },
        } = await axios.post(
          mobileAppUrl,
          {},
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
            },
            params: {
              actionType: "new_account",
              loginType: "facebook",
              fbaccesstoken: accessToken,
              firstname,
              lastname,
              email,
              password,
              confirmation: password,
            },
          }
        );
        if (success) {
          commit("setSessionFB", {
            email: customer_email,
            id: customer_id,
            is_logged_in: true,
          });
          afterLogin({
            state,
            dispatch,
          });
        }
      } else {
        // Known facebook user
        commit("setSessionFB", {
          email,
          id,
          is_logged_in: true,
        });
        afterLogin({
          state,
          dispatch,
        });
      }
    },
    loadCart({ commit }) {
      commit("setLoading", true);
      return axios.get("/api/rest/cart").then(response => {
        let quote_items = response.data.quote_items;
        let quote = response.data.quote;
        let address = response.data.address;

        // process data
        let floatFields = ["price", "price_incl_tax", "row_total_incl_tax"];
        let intFields = [
          "item_id",
          "quote_id",
          "entity_id",
          "product_id",
          "parent_product_id",
        ];
        quote_items.forEach(function(item, index) {
          floatFields.forEach(function(field) {
            if (typeof item[field] === "string") {
              quote_items[index][field] = parseFloat(item[field]);
            }
          });
          intFields.forEach(function(field) {
            if (typeof item[field] === "string") {
              quote_items[index][field] = parseInt(item[field]);
            }
          });
        });
        quote.grand_total = parseFloat(quote.grand_total);
        commit("setcartItems", quote_items);
        commit("setQuote", quote);
        commit("setAddress", address);
        commit("setLoading", false);
      });
    },
    addToCart({ commit, state, dispatch }, data) {
      return new Promise((resolve, reject) => {
        commit("setLoading", true);
        /**
         * TODO: We need to ensure the attribute names we use are always consistent
         * TODO: ctd - the /api/rest/cart webservice requests attributes in one name and returns it differently
         */
        axios.put("/api/rest/cart", data).then(async response => {
          if (response.data.status == "error") {
            dispatch("notification", {
              type: "error",
              message: response.data.message,
            });
            await dispatch("loadCart");
            reject(response.data.message);
            return;
          }
          if (response.data.data && response.data.data.quote_item_id) {
            await dispatch("loadCart");
          } else if (response.data.data && response.data.data.options) {
            let options = response.data.data;
            data.entity_id = data.product; //TODO: Enforce a consistent api (attribute names are different in request / responses) product_id should
            data.price = data.product_price;
            data.parent_product_id = data.super_product;
            // data.qty = data.qty;
            options.request = data; // Set original product data in options as well
            dispatch("toggleModal", {
              modalKey: MODAL_KEY_PRODUCT_OPTION,
              isVisible: true,
              options,
            });
          } else {
            await dispatch("loadCart");
          }
          // TODO - add to cart on client using checksum instead of reloading whole cart
          resolve(response);
        });
      });
    },
    async addShipmentToCart(
      { dispatch },
      { shipment, shipmentPostcode, storeId }
    ) {
      if (!shipment) return;

      return await dispatch("addToCart", {
        product: shipment.entity_id,
        super_product: storeId,
        shipment_postcode: shipmentPostcode,
        // shipment_postcode: this.address && this.address.postcode,
      });
    },
    addPlasticBagToCart({ dispatch }, { plasticBag, storeId, qty }) {
      if (!plasticBag) return;

      dispatch("addToCart", {
        product: plasticBag.entity_id,
        super_product: storeId,
        // qty: 2,
      });
    },
    addPreorderTimeToCart(
      { dispatch },
      { storeId, selectedTime, selectedDay, deliveryHours }
    ) {
      const validSelectedTime = [
        selectedTime,
        selectedTime !== "asap",
        deliveryHours.map(({ value }) => value).includes(selectedTime),
      ].every(Boolean);

      if (!validSelectedTime) return;

      const preorder =
        selectedTime === "NULL"
          ? "NULL"
          : getTimestamp(selectedTime, selectedDay);

      preorder &&
        dispatch("addToCart", {
          product: storeId,
          preorder,
        });
    },
    startSearching({ commit }) {
      commit("setIsSearching", true);
    },
    stopSearching({ commit }) {
      commit("setIsSearching", false);
    },
    notification({ commit }, notification) {
      const { message } = notification;
      commit("setNotification", notification);
      writeClientLog({
        error: JSON.stringify({
          locale: i18n.locale,
          message,
        }),
        level: "translation",
      });
    },
    deleteNotification({ commit }) {
      commit("deleteNotification");
    },
    setQuoteItemQty({ commit, state, dispatch }, data) {
      commit("setClientQty", data);
      commit("startCartChange");
      // Update server quantity
      axios.put("/api/rest/cart", data).then(response => {
        if (response.data.status == "error") {
          let notification = {
            type: "error",
            message: response.data.message,
          };
          commit("setNotification", notification);
        }
        commit("endCartChange");
        if (state.cartActions === 0) {
          dispatch("loadCart");
        }
      });
    },
    removeShipmentInCart({ state, dispatch }, { shipmentId }) {
      const { cartItems } = state;
      const shipmentInCart = cartItems.find(
        ({
          product_id, // shipment id
        }) => product_id === shipmentId
      );
      if (!shipmentInCart) return;

      dispatch("setQuoteItemQty", {
        quote_item_id: shipmentInCart.item_id,
        setqty: 0,
      });
    },
    setShowCart({ commit }, show) {
      commit("setShowCart", show);
    },
    async getPickupLocations({ state, commit }) {
      let pickupLocations = get(state, "pickupLocations");
      if (pickupLocations && pickupLocations.length) return pickupLocations;

      pickupLocations = await getPickupLocations();
      commit("setPickupLocations", pickupLocations);
    },
    toggleCart({ commit }) {
      commit("toggleCart");
    },
    async login({ dispatch }, { username, password }) {
      const loginData = await loginAccount({
        username,
        password,
      });
      if (loginData) {
        const message = get(loginData, "message", "");
        const alreadyLoggedInMsg = "Customer is already logged in.";
        if (isAppWebView() && message === alreadyLoggedInMsg) {
          return;
        }

        dispatch("notification", {
          type: loginData.status,
          message,
          action: "login",
        });
        "success" === loginData.status && dispatch("getSession");
      }
    },
    async createCustomer({ dispatch }, body) {
      try {
        const response = await register(body);
        if (response.status === "error") {
          throw new Error(response.message);
        }
        dispatch("notification", {
          type: "success",
          message: response.message,
          action: "login",
        });
        dispatch("getSession");
        return response;
      } catch (error) {
        dispatch("notification", {
          type: "error",
          message: error.message,
        });
      }
    },
    async forgotPassword({ dispatch }, email) {
      try {
        const response = await forgotPassword(email);
        if (response.status === "error") {
          throw new Error(response.message);
        }
        dispatch("notification", {
          type: "success",
          message: response.message,
        });
        return response;
      } catch (error) {
        console.log(error);
        dispatch("notification", {
          type: "error",
          message: error.message,
        });
      }
    },
    async enterForgotPassword({ dispatch }, body) {
      try {
        const response = await enterForgotPassword(body);
        if (response.status === "error") {
          throw new Error(response.message);
        }
        dispatch("notification", {
          type: "success",
          message: response.message,
        });
        return response;
      } catch (error) {
        dispatch("notification", {
          type: "error",
          message: error.message,
        });
      }
    },
    async logout({ state, dispatch }) {
      await Promise.all([
        axios.put("/api/rest/customer", {
          logout: 1,
        }),
        state.customer.fb && FB.logout(),
      ]);
      postMessageToApp.logout();
      await dispatch("getSession");
      router.push("/");
    },
    toggleLogin({ commit }, redirectPath) {
      postMessageToApp.toggleLogin();
      commit("toggleLogin", redirectPath);
    },
    async getClientIP({ commit }) {
      const clientIP = await getClientIP();
      commit("getClientIP", clientIP);
      return clientIP;
    },
    async getStoreIdURLMap({ commit }) {
      if (!this.state.storeIdMap) {
        const storeIdMap = await getStoresIdURLMap();
        commit("setStoreIdMap", storeIdMap);
      }
    },
    setIsInGroceriesScreen({ commit }, setIsInGroceriesScreen) {
      commit("setIsInGroceriesScreen", setIsInGroceriesScreen);
    },
    setCurrentStoreId({ commit }, storeId) {
      commit("setCurrentStoreId", storeId);
    },
    async loadCategoriesList({ commit, getters }) {
      try {
        if (getters.urlPathToCategoryMapping) return;
        const data = await getCategories();
        commit("setCategoriesList", data);
      } catch (e) {
        console.log("ERROR -> loadCategoriesList", e);
      }
    },
    checkDataVersion({ getters, dispatch }) {
      const { dataVersion } = getters;
      if (dataVersion && dataVersion !== DATA_VERSION) {
        dispatch("resetData");
      }
    },
    resetData({ commit, dispatch }) {
      dispatch("logout");
      commit("resetData");
      window.location.reload();
    },
    setPrintData({ commit }, data) {
      commit("setPrintData", data);
    },
    async fetchStorePostcodeIdMap({ getters, commit }) {
      const { storePostcodeIdMap: oldFetched } = getters;
      if (oldFetched) return oldFetched;

      const storePostcodeIdMap = await getStorePostcodeMap();
      commit("setStorePostcodeIdMap", storePostcodeIdMap);
      return storePostcodeIdMap;
    },
    async fetchServerTime({ commit }) {
      const serverTime = await getServerTime();
      commit("setServerTime", serverTime);
    },
  },
  mutations: {
    updatePutCheckoutKey(state) {
      state.putCheckoutKey = +new Date()
    },
    setServerTime(state, serverTime) {
      state.serverTime = serverTime;
      state.serverTimeDiff = +serverTime - new Date();
    },
    resetData(state) {
      for (let key in state) {
        Vue.set(state, key, initialState[key]);
      }
    },
    setClientQtyByProductId(state, payload) {
      const { product_id, setqty } = payload.data;
      state.cartItems = state.cartItems.map(cartItem => {
        if (cartItem.product_id != product_id) {
          return cartItem;
        }
        return {
          ...cartItem,
          cart_qty: setqty,
        };
      });
    },
    setClientQty(state, data) {
      const { quote_item_id, setqty } = data;
      state.cartItems = state.cartItems.map(item => {
        if (item.item_id !== quote_item_id) {
          return item;
        }
        return {
          ...item,
          cart_qty: setqty,
        };
      });
    },
    addToCartClient(state, payload) {
      state.cartItems.push(payload.data);
    },
    // Manage search result visibility in layout
    setIsSearching(state, isSearching) {
      state.isSearching = isSearching;
    },
    setNotification(state, notification) {
      state.notification = notification;
    },
    deleteNotification(state) {
      state.notification = {};
    },
    setSessionDB(state, data) {
      const {
        email,
        group_id: group,
        entity_id: id,
        isLoggedIn,
        firstname,
        lastname,
        nationid,
        customer_credit,
      } = data;
      state.customer = {
        email,
        firstname,
        lastname,
        nationid,
        customer_credit,
        group,
        id,
        is_logged_in: isLoggedIn,
        fb: false,
      };
    },
    setSessionFB(state, data) {
      state.customer.fb = true;
      Object.assign(state.customer, data);
    },
    setcartItems(state, items) {
      state.cartItems = items.map(({ qty, ...item }) => ({
        cart_qty: qty,
        ...item,
      }));
    },
    setQuote(state, quote) {
      state.quote = quote;
    },
    setPickupLocations(state, locations) {
      state.pickupLocations = locations;
    },
    setAddress(state, address) {
      state.address = address;
    },
    setLoading(state, loading) {
      if (state !== null) {
        state.loading = loading;
      }
    },
    setShowCart(state, show) {
      state.showCart = show;
    },
    toggleCart(state) {
      state.showCart = !state.showCart;
    },
    startCartChange(state) {
      state.cartActions++;
    },
    endCartChange(state) {
      state.cartActions--;
    },
    toggleLogin(state, redirectPath) {
      state.showLogin = !state.showLogin;
      state.redirectPath = redirectPath;
    },
    getClientIP(state, clientIP) {
      state.clientIP = clientIP;
    },
    setStoreIdMap(state, storeIdMap = {}) {
      state.storeIdMap = storeIdMap;
      state.storeURLMap = Object.values(storeIdMap).reduce((prev, curr) => {
        prev[curr.url] = curr;
        return prev;
      }, {});
    },
    setIsInGroceriesScreen(state, isInGroceriesScreen) {
      state.isInGroceriesScreen = isInGroceriesScreen;
    },
    setCurrentStoreId(state, storeId) {
      state.currentStoreId = storeId;
    },
    setCategoriesList(state, { catWithChildren, urlPathToCategoryMapping }) {
      state.categoriesList = {
        catWithChildren,
        urlPathToCategoryMapping,
      };
    },
    setPrintData(state, data) {
      state.printData = data;
    },
    setStorePostcodeIdMap(state, storePostcodeIdMap) {
      Vue.set(state, "storePostcodeIdMap", storePostcodeIdMap);
    },
    setCounts(state, counting) {
      state.counts = counting;
    },
  },
  getters: {
    loading(state) {
      return state.loading;
    },
    pickupLocations: state => {
      return get(state, "pickupLocations");
    },
    customerInfo: state => {
      return get(state, "customer", {});
    },
    shippingAddress: state => {
      return get(state, "address.shipping", {});
    },
    billingAddress: state => {
      return get(state, "address.billing", {});
    },
    listAddress: state => {
      return get(state, "address.list", []);
    },
    currentStoreId: state => {
      return state.currentStoreId;
    },
    categoriesList: state => {
      return get(state, "categoriesList.catWithChildren");
    },
    urlPathToCategoryMapping: state => {
      return get(state, "categoriesList.urlPathToCategoryMapping");
    },
    getPathByCategoryNames: (state, getters) => categoryNameList => {
      const { categoriesList } = getters;
      let lastCategory = undefined;
      categoryNameList.forEach(catName => {
        if (!lastCategory) return (lastCategory = categoriesList);

        return (lastCategory = get(lastCategory, "children", []).find(
          category => category.name === catName
        ));
      });
      return get(lastCategory, "url_path");
    },
    printData: state => {
      return state.printData;
    },
    getNettoURLByID: state => storeId => {
      return get(state, `storeIdMap.${storeId}.url`);
    },
    getNettoIdByURL: state => storeUrl => {
      return get(state, `storeURLMap.${storeUrl}.id`);
    },
    getStoreNameByUrl: state => storeUrl => {
      return get(state, `storeURLMap.${storeUrl}.name`);
    },
    getNettoNameByID: state => storeId => {
      return get(state, `storeIdMap.${storeId}.name`);
    },
    getNettoIdByPostcode: state => postcode => {
      return get(state, `storePostcodeIdMap.${postcode}`);
    },
    storePostcodeIdMap: state => {
      return get(state, `storePostcodeIdMap`);
    },
    dataVersion: state => {
      return get(state, "__dataVersion");
    },
    productOptions: (state, getters) => {
      return get(
        getters.getModalState(MODAL_KEY_PRODUCT_OPTION),
        "options",
        {}
      );
    },
    counts: state => {
      return state.counts;
    },
    savedAddresses(state) {
      return get(state, "address.list", []);
    },
    isInGroceriesScreen(state) {
      return state.isInGroceriesScreen;
    },
    cartItems(state) {
      return state.cartItems;
    },
    quote(state) {
      return state.quote;
    },
    serverTimeDiff(state) {
      return +state.serverTimeDiff;
    },
    userBilling(state) {
      return get(state, "address.billing", {});
    },
    billingPhoneNumber(state, getters) {
      return get(getters.userBilling, "telephone", "").replace("+354", "");
    },
    putCheckoutKey(state) {
      return state.putCheckoutKey
    }
  },
  modules: {
    // modals: modalModule,
    // delivery: deliveryModule,
    loadingIndicators: loadingModule,
    errors: errorsModule,
    searchFilter: searchFilterModule,
  },
  plugins: [vuexLocal.plugin],
});

store.registerModule('modals', modalModule)
store.registerModule('delivery', deliveryModule)
export default store;
