<template>
  <!-- This component controls the category navigation in the navbar -->
  <div class="header__categories col-sm-3" v-click-outside="hideMenu">
    <a @click="toggleMenu" class="header__categories-menu noselect">
      <span class="header__categories__icon">
        <i class="icon--categories" />
      </span>
      <span class="header__categories__text">{{ $t("categories") }}</span>
      <span class="header__categories__arrow">
        <i class="icon--arrow-down" />
      </span>
    </a>
    <div class="header__categories__wrap" v-show="isMenuVisible">
      <ul class="header__categories__menu">
        <li
          class="header__categories__item"
          v-for="lvl1 in lv1Categories"
          :key="lvl1.category_id"
        >
          <router-link :to="'/' + lvl1.url_path" @click.native="hideMenu">
            <span>{{ lvl1.name }}</span>
          </router-link>
          <div class="header__categories__submenu">
            <div class="masonry">
              <div
                class="masonry-item"
                v-for="lvl2 in lvl1.children"
                :key="lvl2.category_id"
              >
                <ul>
                  <div>
                    <li class="header__categories__heading">
                      <router-link
                        :to="'/' + lvl2.url_path"
                        @click.native="hideMenu"
                      >
                        {{ lvl2.name }}
                      </router-link>
                    </li>
                    <li
                      v-for="lvl3 in lvl2.children"
                      :key="lvl3.category_id"
                      class="header__categories__subitem"
                    >
                      <router-link
                        :to="'/' + lvl3.url_path"
                        @click.native="hideMenu"
                        >{{ lvl3.name }}</router-link
                      >
                    </li>
                  </div>
                </ul>
              </div>
            </div>
          </div>
        </li>
        <li
          class="header__categories__item"
        >
          <router-link to="/all-categories">
            <span>View All</span>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<i18n>
{
  "is": {
    "categories": "Flokkar"
  },
  "en": {
    "categories": "Categories"
  }
}
</i18n>

<script>
import ClickOutside from "vue-click-outside";
import { mapGetters } from "vuex";
import get from "@/util/get";

export default {
  name: "HeaderCategories",
  data() {
    return {
      isMenuVisible: false,
    };
  },
  computed: {
    ...mapGetters(["categoriesList"]),
    lv1Categories() {
      const { categoriesList } = this;
      return get(categoriesList, "children");
    },
  },
  methods: {
    toggleMenu() {
      this.isMenuVisible = !this.isMenuVisible;
    },
    hideMenu() {
      this.isMenuVisible = false;
    },
  },
  watch: {
    $route() {
      this.hideMenu();
    },
  },
  directives: {
    ClickOutside,
  },
};
</script>

