<template>
  <!-- This component renders the grand total currently in the cart. If reusing this, consider refactoring to a mixin in order to simplify styling and layout -->
  <div class="checkout-bottom--wrapper">
    <div v-if="grandTotalAmount > 0" class="checkout-bottom row">
      <div class="checkout-bottomLeft col-xs-8">
        <h5>{{ $t("total") }}</h5>
      </div>
      <div class="checkout-bottomRight col-xs-4">
        <h5>{{ grandTotalAmount | formatISK }}</h5>
      </div>
    </div>
  </div>
</template>

<i18n>
{
  "is": {
    "total": "Alls"
  },
  "en": {
    "total": "Total"
  }
}
</i18n>

<script>
import cartItemsByStore from "@/mixins/cartItemsByStore";

export default {
  name: "GrandTotal",
  mixins: [cartItemsByStore],
  computed: {
    grandTotalAmount() {
      const totalPrice = this.cartItemsByStore.reduce((result, store) => {
        return result + store.priceTotal;
      }, 0);
      return totalPrice;
    },
  },
  watch: {
    grandTotalAmount(value) {
      this.$emit("grandTotalAmount", value);
    },
  },
  mounted() {
    this.$emit("grandTotalAmount", this.grandTotalAmount);
  },
};
</script>

